<template>
  <!-- Main Content-->
<div class="main-content side-content pt-0">
  <div class="main-container container-fluid pt-5">
    <div class="inner-body">
      <!-- Page Header -->
      <div class="page-header">
        <div>
          <h2 class="main-content-title tx-24 mg-b-5">Yêu cầu sản xuất</h2>
          <ol class="breadcrumb">
            <li class="breadcrumb-item">
              <a href="index.html">Trang chủ</a>
            </li>
            <li class="breadcrumb-item active" aria-current="page">Yêu cầu sản xuất</li>
          </ol>
        </div>
        <div class="d-flex">
          <div class="justify-content-center">
            <router-link to="/production_required" class="btn ripple btn-white btn-rounded  my-2 btn-icon-text">
              <i class="fe fe-corner-down-left"></i> Trở lại
            </router-link>
          </div>
        </div>
      </div>
      <!-- End Page Header -->
      <!-- Row -->
      <div class="row row-sm">
        <div class="col-lg-12 col-md-12">
          <div class="card custom-card">
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">Phiếu yêu cầu sản xuất Số: <span class="badge bg-success"> {{ updateID }}</span> </h6>
                  <p class="text-muted card-sub-title"></p>
                </div>
                <table class="table table-bordered">
                  <tbody>
                    <tr>
                      <td>
                        <div class="form-group">
                          <label class="fw-bold">Người tạo</label>
                          <input type="text" class="form-control text-body" v-model="storeProOder.user_create_by" readonly>
                        </div>
                      </td>
                      <td>
                        <div class="form-group">
                          <label class="fw-bold">Ngày tạo</label>
                          <input class="form-control text-body" type="text" v-model="storeProOder.production_order_create" readonly>
                        </div>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <div class="form-group ">
                          <label class="fw-bold">Số báo giá/Đơn hàng</label>
                          <textarea class="form-control text-black" rows="4" placeholder="Nhập số báo giá/đơn hàng" v-model="storeProOder.production_order_invoice" readonly></textarea>
                        </div>
                      </td>
                      <td>
                        <div class="form-group ">
                          <label class="fw-bold">Ghi chú</label>
                          <textarea 
                          class="form-control text-black" 
                          rows="7"
                          :value="storeProOder.production_order_note"
                           readonly
                          ></textarea>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">Yêu cầu chi tiết
                  </h6>
                  <p class="text-muted card-sub-title">Thêm danh sách sản phẩm yêu cầu</p>
                </div>
                <div class="table-responsive">
                  <table class="table table-bordered" id="productTable">
                    <thead class>
                      <tr>
                        <th style="width:2%">STT</th>
                        <th style="width: 14%;">Tên SP</th>
                        <th style="width: 7%;">Số lượng</th>
                        <th style="width: 7%;">Đơn vị</th>
                        <th style="width:8%">Dự kiến hoàn thành</th>
                        <th style="width: 11%;">Quy cách đóng gói</th>
                        <th >Yêu cầu chi tiết</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(product, index) in products" :key="index">
                        <td>{{ index +1 }}</td>
                        <td>{{ product.detail_product_order_name }}</td>
                        <td>{{ product.detail_product_order_quantity }}</td>
                        <td>{{ product.detail_product_order_unit }}</td>
                        <td>{{ product.detail_product_order_finish }}</td>
                        <td>{{ product.detail_product_order_packing }}</td>
                        <td > 
                          <textarea 
                          class="form-control text-body" 
                          v-if="product.detail_product_order_detail"
                          :value="product.detail_product_order_detail" 
                          :style="{ height: (product.detail_product_order_detail.split('\n').length * 25) + 'px' }"
                          readonly
                          ></textarea>
                        </td>
                      </tr>
					          </tbody>
                  </table>
                </div>
              </div>
            
          </div>
        </div>
      </div>
      <!-- End Row -->
    </div>
  </div>
</div>
<!-- End Main Content-->
</template>

<script>
import api from '@/components/uses/api.js'

export default {
data() {
    return {
	    editData:[],
      proOderID:null,
      storeProOder:[],
      products: [],
	    updateID:null
    };
  },
 mounted (){
	this.updateID = this.$route.params.id
	this.getProOderID();
 },
	methods: {
    getProOderID() {
        api.getData(`product-order/editProductOrder/${this.updateID}`)
        .then(Response => {
          this.editData = Response.data.data[0]
          this.products = this.editData.dataDetail
          this.storeProOder.production_order_create = this.editData.production_order_create;
          this.storeProOder.user_create_by = this.editData.detail_user[0].fullname;
          this.storeProOder.production_order_invoice = this.editData.production_order_invoice;
          this.storeProOder.production_order_note = this.editData.production_order_note;
        })
        .catch(error=> {
          console.log(error)
        })
      },

	}
  }
</script>
<style>
</style>