<template>
  <!-- Main Content-->
  <div class="main-content side-content pt-0">
    <div class="main-container container-fluid pt-5">
      <div class="inner-body">
        <!-- Page Header -->
        <div class="page-header">
          <div>
            <h2 class="main-content-title tx-24 mg-b-5">Quản lý nhóm sản phẩm</h2>
            <ol class="breadcrumb">
              <li class="breadcrumb-item">
                <a href="#">Trang chủ</a>
              </li>
              <li class="breadcrumb-item active" aria-current="page"> nhóm sản phẩm </li>
            </ol>
          </div>
          <div class="d-flex">
            <div class="justify-content-center">
              <a data-bs-target="#add_product_group" data-bs-toggle="modal" class="btn btn-primary btn-rounded my-2 text-white">
                <i class="fe fe-plus"></i> Thêm mới </a>
            </div>
          </div>
        </div>
        <!-- End Page Header -->
        <!-- Row -->
        <div class="row row-sm">
          <div class="col-lg-12">
            <div class="card custom-card">
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">Danh sách nhóm sản phẩm</h6>
                  <p class="text-muted card-sub-title"></p>
                </div>
                <div class="table-responsive">
                  <table :data="product_groups" class="table table-bordered" id="productGroupTable">
                    <thead>
                      <tr>
                        <th class="wd-20p">STT</th>
                        <th class="wd-25p">Mã nhóm sản phẩm</th>
                        <th class="wd-25p">Tên nhóm sản phẩm</th>
                        <th class="wd-25p">Ghi chú</th>
                        <th class="wd-15p no-print no-excel">Thao tác</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in product_groups" :key="item._id">
                        <td>{{ index + 1 }}</td>
                        <td>{{ item.product_group_code }}</td>
                        <td>{{ item.product_group_name }}</td>
                        <td>{{ item.product_group_note }}</td>
                        <td>
                          <a @click="editProductGroup(item)" data-bs-target="#update_product_group" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-info ms-1"><i class="fe fe-edit-2"></i></a>
                          <a  @click="deleteProductGroup(item._id)" class="btn btn-sm btn-outline-danger ms-1"> <i class="fe fe-trash"></i></a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- End Row -->
      </div>
    </div>
  </div>
  <!-- End Main Content-->
  <!-- Large Modal -->
  <div class="modal" id="add_product_group" ref="addProGroupModal">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Thêm mới nhóm sản phẩm</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="addProGroup">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Mã nhóm sản phẩm</label>
                      <input v-model="storeProGroup.product_group_code" class="form-control text-body" name="product_group_code" :class="{'is-invalid':addErrors.product_group_code}" type="text" placeholder="Nhập mã nhóm sản phẩm" />
                      <div class="invalid-feedback mt-2" v-if="addErrors.product_group_code">{{addErrors.product_group_code}}</div>
                    </div>
                  </td>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Tên nhóm sản phẩm</label>
                      <input v-model="storeProGroup.product_group_name" class="form-control text-body" name="product_group_name" :class="{'is-invalid':addErrors.product_group_name}" type="text" placeholder="Nhập tên nhóm sản phẩm" />
                      <div class="invalid-feedback" v-if="addErrors.product_group_name">{{addErrors.product_group_name}}</div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td colspan="2">
                    <div class="form-group">
                      <label class="fw-bold ">Ghi chú</label>
                      <textarea v-model="storeProGroup.product_group_note" class="form-control text-body" :class="{'is-invalid':addErrors.product_group_note}" rows="5" placeholder="Nhập ghi chú"></textarea>
                      <div class="invalid-feedback">{{addErrors.product_group_note}}</div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div v-if="showSuccessMessage" class="alert alert-success text-center">
            {{ successMessage }}
          </div>
          <div class="modal-footer d-flex justify-content-between">
            <div class="form-check">
              <input class="form-check-input" type="checkbox" id="check1" name="option1" value="something" v-model="dontCloseModal">
              <label class="form-check-label fw-bold">Không đóng màn hình</label>
            </div>
            <div>
              <button class="btn ripple btn-primary" type="submit">Lưu</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('addProGroupModal','storeProGroup','addErrors')" type="button"> Đóng </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->
  <!-- Large Modal -->
  <div class="modal" id="update_product_group" ref="updateProGroup">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Cập nhật nhóm sản phẩm</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="updateProGroup">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Mã nhóm sản phẩm</label>
                      <input v-model="selectedProGroup.product_group_code" :class="{'is-invalid': updateErrors.product_group_code}" class="form-control text-body" type="text" placeholder="Nhập mã nhóm sản phẩm" />
                      <div class="invalid-feedback" v-if="updateErrors.product_group_code">{{updateErrors.product_group_code}} </div>
                    </div>
                  </td>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Tên nhóm sản phẩm</label>
                      <input v-model="selectedProGroup.product_group_name" class="form-control text-body" :class="{'is-invalid': updateErrors.product_group_name}" type="text" placeholder="Nhập tên nhóm sản phẩm" />
                      <div class="invalid-feedback" v-if="updateErrors.product_group_name">{{updateErrors.product_group_name}} </div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td colspan="2">
                    <div class="form-group">
                      <label class="fw-bold ">Ghi chú</label>
                      <textarea v-model="selectedProGroup.product_group_note" :class="{'is-invalid':updateErrors.product_group_note}" class="form-control text-body" name="" rows="5" placeholder="Nhập ghi chú"></textarea>
                      <div class="invalid-feedback" v-if="updateErrors.product_group_note">{{updateErrors.product_group_note}} </div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="modal-footer">
            <button class="btn ripple btn-primary" type="submit">Lưu</button>
            <button class="btn ripple btn-danger" @click="closeModal('updateProGroup', 'selectedProGroup','updateErrors' )" type="button"> Đóng </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->
</template>
<script>
  import util from '@/components/uses/utils.js'
  import api from '@/components/uses/api.js'
  export default {
    data() {
      return {
        product_groups: {
          product_group_code:'',
          product_group_name:'',
          product_group_note:''
        },
        storeProGroup:{
          product_group_code:'',
          product_group_name:'',
          product_group_note:''
        },
        selectedProGroup: [],
        dontCloseModal: false,
        addErrors: [],
        updateErrors: [],
        showSuccessMessage: false, 
        successMessage: '',
      };
    },
    mounted() {
      this.getproduct_groups();
    },
    methods: {
      loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#productGroupTable')
        });
      },
      getproduct_groups() {
        api.getData('product-group/listProductGroup')
        .then(response=> {
          this.product_groups = response.data.data;
          this.loadtable();
        })
        .catch(error=> {
          util.errorRedirect()
        })
      },

      addProGroup() {
        const productGroupData = {
          product_group_code: this.storeProGroup.product_group_code,
          product_group_name: this.storeProGroup.product_group_name,
          product_group_note: this.storeProGroup.product_group_note ? this.storeProGroup.product_group_note : ''
        };
        const proTypeField = [
          { name: 'product_group_code', label: 'Mã nhóm sản phẩm', maxLength: 50, nullable: false }, 
          { name: 'product_group_name', label: 'Tên nhóm sản phẩm', maxLength: 100, nullable: false }, 
          { name: 'product_group_note', label: 'Ghi chú', maxLength: 50, nullable: true }
        ]
        this.addErrors = util.validateData(productGroupData, proTypeField);
        if (Object.keys(this.addErrors).length === 0) {
          api.postData('product-group/storeProductGroup',productGroupData)
          .then(response => {
             if (response.data.error) {
              response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.errors[errorField] = [];
                this.errors[errorField] = errorMessage;
              });
              } else {
                if (!this.dontCloseModal) {
                  this.getproduct_groups();
                  this.closeModal('addProGroupModal', 'storeProGroup', 'errors');
                  util.showSuccessAlert('Thêm thành công');
                } else {
                  this.getproduct_groups();
                  this.storeProGroup={};
                  this.showSuccessMessage = true;
                  this.successMessage = 'Thêm thành công'
                  setTimeout(() => {
                  this.showSuccessMessage = false; 
                }, 1000); 
                }
              }
          })
            .catch(error => {
            console.error('Lỗi khi thêm nhóm sản phẩm:', error);
        });
        }
      },

      deleteProductGroup(id) {
        util.confirmDelete(() => {
          api.deleteData(`product-group/deleteProductGroup/?id=${id}`)
          .then(response => {
            util.showSuccessAlert('Xóa thành công');
            this.getproduct_groups();
          })
          .catch(error => {
          console.error('Lỗi khi xóa nhóm sản phẩm:', error);
          });
          })
      },

      closeModal(modalRef, datakey, errorskey) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
        this[datakey] = {};
        this[errorskey] = {};
      },

      editProductGroup(item) {
        this.selectedProGroup.id = item._id;
        this.selectedProGroup.product_group_code = item.product_group_code;
        this.selectedProGroup.product_group_name = item.product_group_name;
        this.selectedProGroup.product_group_note = item.product_group_note;
      },
      updateProGroup() {
        const dataUpdate = {
          product_group_code: this.selectedProGroup.product_group_code,
          product_group_name: this.selectedProGroup.product_group_name,
          product_group_note: this.selectedProGroup.product_group_note
        }
        const updateProGroupField = [
          { name: 'product_group_code', label: 'Mã nhóm sản phẩm', maxLength: 50, nullable: false}, 
          { name: 'product_group_name', label: 'Tên nhóm sản phẩm', maxLength: 50, nullable: false}, 
          { name: 'product_group_note', label: 'Ghi chú', maxLength: 50, nullable: true}
        ]
        this.updateErrors = util.validateData(dataUpdate, updateProGroupField); 
        if (Object.keys(this.updateErrors).length === 0) {
            api.putData(`product-group/updateProductGroup/${this.selectedProGroup.id}` , dataUpdate)
            .then(response => {
              if (response.data.error) {
              response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.updateErrors[errorField] = [];
                this.updateErrors[errorField] = errorMessage;
              });
              } else {
                util.showSuccessAlert('Cập nhật thành công');
                this.closeModal('updateProGroup', 'selectedProGroup', 'updateErrors');
                this.getproduct_groups();
              }
            })
            .catch(error => {
              console.error('Lỗi khi cập nhật nhóm sản phẩm:', error);
            });
        }
      },
    }
  }
</script>
