<template>
  <!-- Main Content-->
  <div class="main-content side-content pt-0">
    <div class="main-container container-fluid pt-5">
      <div class="inner-body">
        <!-- Page Header -->
        <div class="page-header">
          <div>
            <h2 class="main-content-title tx-24 mg-b-5">
              Quản lý tài khoản
            </h2>
            <ol class="breadcrumb">
              <li class="breadcrumb-item"><a href="#">Trang chủ</a></li>
              <li class="breadcrumb-item active" aria-current="page">
                Tài khoản
              </li>
            </ol>
          </div>
          <div class="d-flex">
            <div class="justify-content-center">
              <a data-bs-target="#add_user" data-bs-toggle="modal" class="btn btn-primary btn-rounded text-white my-2 ">
                <i class="fe fe-plus"></i> Thêm mới
              </a>
            </div>
          </div>
        </div>
        <!-- End Page Header -->

        <!-- Row -->
        <div class="row row-sm">
          <div class="col-lg-12">
            <div class="card custom-card">
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">Danh sách khu vực</h6>
                  <p class="text-muted card-sub-title"></p>
                </div>
                <div class="table-responsive">
                  <table :data="users" class="table table-bordered" id="userTable">
                    <thead>
                      <tr>
                        <th>STT</th>
                        <th>Họ tên</th>
                        <th>Email</th>
                        <th>Giới tính</th>
                        <th>Ngày sinh</th>
                        <th>Chức vụ</th>
                        <th>Bộ phận</th>
                        <th>Khu vực</th>
                        <th>Quyền</th>
                        <th>Thao tác</th>
                      </tr>
                    </thead>
                    <tbody>
                       <tr v-for="(item, index) in users" :key="item._id">
                       <td>{{ index +1 }}</td>
                       <td class="d-flex align-items-center">
                          <div class="main-img-user avatar-md">
                            <img alt="avatar" class="radius" :src="'http://192.168.48.31:8080/' + item.avatar">
                          </div>
                          <span class="ms-2">{{ item.fullname }}</span>
                        </td>
                        <td>
                          <a :href="'mailto:' + item.email">{{ item.email }}</a>
                        </td>
                         <td>
                          <span :class="item.gender === true ? 'tag tag-success tag-pill' : 'tag tag-warning tag-pill'">
                            {{ item.gender == true ? 'Nam' : item.gender == false ? 'Nữ' : '' }}
                          </span>
                        </td>
                        <td>
                           <span>{{ formatDate(item.birthday) }}</span>
                        </td>
                        <td>
                          <span>{{ item.getPostion[0].position_name }}</span>
                        </td>
                         <td>
                          <span>{{ item.getDepartment[0].department_name }}</span>
                        </td>
                        <td>
                          <span>{{ item.getRegion[0].region_name }}</span>
                        </td>
                        <td>
                          <span :class="item.role_id === '1' ? 'tag tag-success tag-pill' : 'tag tag-warning tag-pill'">
                           {{ item.role_id == 1 ? 'admin' : item.role_id == 2 ? 'user' : '' }}
                          </span>
                         </td>
                         <td>
                          <!-- <a data-bs-target="#update_user" @click="editUser(item)" data-bs-toggle="modal" class="btn btn-sm ripple text-white btn-main-primary">Sửa</a>
                          <button class="btn btn-sm ripple btn-danger ms-1"  @click="deleteUser(item._id)">Xóa</button> -->

                           <a @click="editUser(item)" data-bs-target="#update_user" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-info ms-1"><i class="fe fe-edit-2"></i></a>
                          <a @click="deleteUser(item._id)" class="btn btn-sm btn-outline-danger ms-1"> <i class="fe fe-trash"></i></a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- End Row -->
      </div>
    </div>
  </div>
  <!-- End Main Content-->
    <!-- Extra Large Modal -->
   <div class="modal fade" id="add_user" ref="addUserModal">
     <div class="modal-dialog modal-xl" role="document">
       <div class="modal-content modal-content-demo">
         <div class="modal-header">
           <h6 class="modal-title">Thêm mới người dùng</h6>
           <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
         </div>
         <form @submit.prevent="addUser">
           <div class="modal-body">
             <table class="table">
               <tbody>
                 <tr>
                   <td>
                     <div class="form-group">
                       <label class="fw-bold">Email</label>
                       <input v-model="storeUser.email" class="form-control text-body" :class="{'is-invalid': addErrors.email}" placeholder="Nhập email" />
                       <div class="invalid-feedback" v-if="addErrors.email">{{addErrors.email}} </div>
                     </div>
                   </td>
                   <td>
                     <div class="form-group">
                       <label class="fw-bold">Tên đăng nhập</label>
                       <input class="form-control text-body" type="text" :class="{'is-invalid': addErrors.username}" placeholder="Tên đăng nhập" v-model="storeUser.username" />
                       <div class="invalid-feedback" v-if="addErrors.username">{{addErrors.username}} </div>
                     </div>
                   </td>
                   <td>
                     <div class="form-group">
                       <label class="fw-bold">Mật khẩu</label>
                       <input v-model="storeUser.password" class="form-control text-body" :class="{'is-invalid': addErrors.password}" type="password" placeholder="Nhập mật khẩu" />
                       <div class="invalid-feedback" v-if="addErrors.password">{{addErrors.password}} </div>
                     </div>
                   </td>
                   <td>
                     <div class="form-group">
                       <label class="fw-bold">Nhập lại mật khẩu</label>
                       <input class="form-control text-body" type="password" :class="{'is-invalid': addErrors.repeat_password}" placeholder="Nhập lại mật khẩu" v-model="storeUser.repeat_password" />
                       <div class="invalid-feedback" v-if="addErrors.repeat_password">{{addErrors.repeat_password}} </div>
                     </div>
                   </td>
                 </tr>
                 <tr>
                  
                   <td>
                     <div class="form-group">
                       <label class="fw-bold">Họ tên</label>
                       <input v-model="storeUser.fullname" class="form-control text-body" :class="{'is-invalid': addErrors.fullname}" type="text" placeholder="Nhập họ tên" />
                       <div class="invalid-feedback" v-if="addErrors.fullname">{{addErrors.fullname}} </div>
                     </div>
                   </td>
                   <td>
                     <div class="form-group">
                       <label class="fw-bold">Giới tính</label>
                       <select class="form-control form-select text-body" :class="{'is-invalid': addErrors.gender}" v-model="storeUser.gender">
                         <option value="true">Nam</option>
                         <option value="false">Nữ</option>
                       </select>
                       <div class="invalid-feedback" v-if="addErrors.gender">{{addErrors.gender}} </div>
                     </div>
                   </td>
                   <td>
                     <div class="form-group">
                       <label class="fw-bold">Ngày sinh</label>
                       <input v-model="storeUser.birthday" class="form-control text-body" :class="{'is-invalid': addErrors.birthday}" type="date" />
                       <div class="invalid-feedback" v-if="addErrors.birthday">{{addErrors.birthday}} </div>
                     </div>
                   </td>
                   <td>
                     <div class="form-group">
                       <label class="fw-bold">Bộ phận</label>
                       <select class="form-control form-select text-body" :class="{'is-invalid': addErrors.department_id}" v-model="storeUser.department_id">
                         <option value="" disabled selected>Chọn bộ phận</option>
                         <option v-for="department in departments" :key="department._id" :value="department._id">{{ department.department_name}}</option>
                       </select>
                       <div class="invalid-feedback" v-if="addErrors.department_id">{{addErrors.department_id}} </div>
                     </div>
                   </td>
                 </tr>
                 <tr>
                   <td>
                     <div class="form-group">
                       <label class="fw-bold">Chức vụ</label>
                       <select class="form-control form-select text-body" v-model="storeUser.position_id" :class="{'is-invalid': addErrors.position_id}">
                         <option value="" disabled selected>Chọn chức vụ</option>
                         <option v-for="position in positions" :key="position._id" :value="position._id">{{ position.position_name}}</option>
                       </select>
                       <div class="invalid-feedback" v-if="addErrors.position_id">{{addErrors.position_id}} </div>
                     </div>
                   </td>
                   <td>
                     <div class="form-group">
                       <label class="fw-bold">Khu vực</label>
                       <select class="form-control form-select text-body" :class="{'is-invalid': addErrors.region_id}" v-model="storeUser.region_id">
                         <option value="" disabled selected>Chọn khu vực</option>
                         <option v-for="region in regions" :key="region._id" :value="region._id">{{ region.region_name}}</option>
                       </select>
                       <div class="invalid-feedback" v-if="addErrors.region_id">{{addErrors.region_id}} </div>
                     </div>
                   </td>
                    <td>
                     <div class="form-group">
                       <label class="fw-bold">Quyền</label>
                       <select class="form-control form-select text-body" v-model="storeUser.role_id" :class="{'is-invalid': addErrors.role_id}">
                         <option value="" disabled selected>Chọn quyền</option>
                         <option value="1">Admin</option>
                         <option value="2">User</option>
                       </select>
                       <div class="invalid-feedback" v-if="addErrors.role_id">{{addErrors.role_id}} </div>
                     </div>
                   </td>
                 </tr>
                 <tr>
                   <td colspan="2">
                     <input :class="{'is-invalid': addErrors.avatar}" type="file" class="dropify" id="imageUpload1" name="avatar" data-height="200" @change="handleImageChange" />
                     <div class="invalid-feedback" style="display: block;" v-if="addErrors.avatar">{{addErrors.avatar}}</div>
                   </td>
                 </tr>
               </tbody>
             </table>
           </div>
           <div v-if="showSuccessMessage" class="alert alert-success text-center">
             {{ successMessage }}
           </div>
           <div class="modal-footer d-flex justify-content-between">
             <div class="form-check">
               <input class="form-check-input" type="checkbox" id="check1" name="option1" value="something" v-model="dontCloseModal">
               <label class="form-check-label fw-bold">Không đóng màn hình</label>
             </div>
             <div>
               <button class="btn ripple btn-primary" type="submit">Lưu</button>
               <button class="btn ripple btn-danger ms-2" @click="closeModal('addUserModal','storeUser','addErrors')" type="button"> Đóng </button>
             </div>
           </div>
         </form>
       </div>
     </div>
   </div>
   <!--End Extra Large Modal -->

        <!-- Extra Large Modal -->
			<div class="modal fade" id="update_user" ref="updateUserModal">
				<div class="modal-dialog modal-xl" role="document">
					<div class="modal-content modal-content-demo">
						<div class="modal-header">
							<h6 class="modal-title">Cập nhật người dùng</h6>
							<button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
						</div>
            <form @submit.prevent="updateUser" enctype="multipart/form-data">
						<div class="modal-body">
              <table class="table">
              <tbody>
                <tr>
                  <td>
                     <div class="form-group">
                      <label class="fw-bold">Email</label>
                      <input v-model="selectedUser.email" class="form-control text-body" :class="{'is-invalid': updateErrors.email}"  placeholder="Nhập email" />
                       <div class="invalid-feedback" v-if="updateErrors.email">{{updateErrors.email}} </div>
                    </div>
                  </td>
                    <td>
                         <div class="form-group">
                      <label class="fw-bold">Họ tên</label>
                      <input v-model="selectedUser.fullname" class="form-control text-body" :class="{'is-invalid': updateErrors.fullname}" type="text" placeholder="Nhập họ tên" />
                       <div class="invalid-feedback" v-if="updateErrors.fullname">{{updateErrors.fullname}} </div>
                    </div>
                    </td>
                      <td>
                         <div class="form-group">
                      <label class="fw-bold">Ngày sinh</label>
                      <input v-model="selectedUser.birthday" class="form-control text-body" :class="{'is-invalid': updateErrors.birthday}" type="date" />
                       <div class="invalid-feedback" v-if="updateErrors.birthday">{{updateErrors.birthday}} </div>
                    </div>
                      </td>
                        <td>
                            <div class="form-group">
                      <label class="fw-bold">Giới tính</label>
                      <select class="form-control form-select text-body" :class="{'is-invalid': updateErrors.gender}" v-model="selectedUser.gender">
                        <option value="true">Nam</option>
                        <option value="false">Nữ</option>
                      </select>
                       <div class="invalid-feedback" v-if="updateErrors.gender">{{updateErrors.gender}} </div>
                    </div>
                        </td>

                </tr>
                 <tr>
                    <td>
  <div class="form-group">
                      <label class="fw-bold">Chức vụ</label>
                      <select  class="form-control form-select text-body" v-model="selectedUser.position_id" :class="{'is-invalid': updateErrors.position_id}">
                        <option value="" disabled selected>Chọn chức vụ</option>
                       <option v-for="position in positions" :key="position._id" :value="position._id" >{{ position.position_name}}</option>
                      </select>
                       <div class="invalid-feedback" v-if="updateErrors.position_id">{{updateErrors.position_id}} </div>
                    </div>
                    </td>
                      <td>
                          <div class="form-group">
                      <label class="fw-bold">Bộ phận</label>
                      <select  class="form-control form-select text-body" :class="{'is-invalid': updateErrors.department_id}" v-model="selectedUser.department_id">
                        <option value="" disabled selected>Chọn bộ phận</option>
                        <option v-for="department in departments" :key="department._id" :value="department._id" >{{ department.department_name}}</option>
                      </select>
                       <div class="invalid-feedback" v-if="updateErrors.department_id">{{updateErrors.department_id}} </div>
                    </div>

                      </td>
                        <td>
 
                    <div class="form-group">
                      <label class="fw-bold">Khu vực</label>
                      <select class="form-control form-select text-body" :class="{'is-invalid': updateErrors.region_id}" v-model="selectedUser.region_id">
                        <option value="" disabled selected>Chọn khu vực</option>
                         <option v-for="region in regions" :key="region._id" :value="region._id" >{{ region.region_name}}</option>
                      </select>
                       <div class="invalid-feedback" v-if="updateErrors.region_id">{{updateErrors.region_id}} </div>
                    </div>
                        </td>
                          <td>
  <div class="form-group">
                      <label class="fw-bold">Quyền</label>
                      <select  class="form-control form-select text-body" v-model="selectedUser.role_id" :class="{'is-invalid': updateErrors.role_id}">
                        <option value="" disabled selected>Chọn quyền</option>
                       <option value="1" >Admin</option>
                       <option value="2" >User</option>
                      </select>
                       <div class="invalid-feedback" v-if="updateErrors.role_id">{{updateErrors.role_id}} </div>
                    </div>
                          </td>

                 </tr>
                  <tr>
                      <td>
                         <label class="fw-bold">Ảnh hiện tại</label>
                           <div >
                            <img alt="avatar" class="float-sm-left wd-100p wd-sm-200 mg-t-10 mg-sm-t-0"  :src="'http://192.168.48.31:8080/' + selectedUser.oldAvatar">
                          </div>
                      </td>
                        <td colspan="2">
                           <label class="fw-bold">Đổi ảnh mới</label>
                           <input :class="{'is-invalid': updateErrors.avatar}" type="file" class="dropify" id="imageUpload" name="avatar" data-height="200" @change="handleImageChange" />
                            <div class="invalid-feedback" style="display: block;"  v-if="updateErrors.avatar">{{updateErrors.avatar}}</div>
                        </td>
                  </tr>
                 
              </tbody>
            </table>
            </div>
						 <div v-if="showSuccessMessage" class="alert alert-success text-center">
            {{ successMessage }}
          </div>
        <div class="modal-footer">
          
              <button class="btn ripple btn-primary" type="submit">Lưu</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('updateUserModal','selectedUser','updateErrors')" type="button"> Đóng </button>
          </div>
            </form>
					</div>
				</div>
			</div>
			<!--End Extra Large Modal -->

</template>

<script>
import { format} from "date-fns";
import util from '@/components/uses/utils.js'
import api from '@/components/uses/api.js'

export default {
   mounted() {
    const script1 = document.createElement('script');
    script1.src = '/frontend/plugins/fileuploads/js/fileupload.js';
    script1.async = true;
    document.head.appendChild(script1);

    const script2 = document.createElement('script');
    script2.src = '/frontend/plugins/fileuploads/js/file-upload.js';
    script2.async = true;
    document.head.appendChild(script2);
     this.getUsers()
  },
  data() {
    return {
      users: [],
      storeUser:{
         email:'',
         username:'',
         password:'',
         repeat_password:'',
         fullname:'',
         avatar:'',
         gender:'',
         birthday:'',
         department_id:'',
         position_id:'',
         region_id:'',
         role_id:''
        },
      addErrors: [],
      selectedUser: [],
      dontCloseModal: false,
      updateErrors: [],
      showSuccessMessage: false, 
      successMessage: '',
      positions:[],
      regions:[],
      departments:[]

    }
  }, 

 methods: {
  formatDate(date) {
      if (!date) {
        return ''; 
      }
      const parsedDate = new Date(date);

      if (isNaN(parsedDate.getTime())) {
        return date; 
      }

    return format(parsedDate, 'MM/dd/yyyy');
    },
   loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#userTable')
        });
      },
  getUsers() {
  api.getData('account/listUser')
        .then(response => {
          this.users = response.data.data;
          this.positions = response.data.dataPosition;
          this.regions = response.data.dataRegion;
          this.departments = response.data.dataDepartment;
          this.loadtable();
          console.log(response)
        })
        .catch(error=> {
          console.log(error)
        })
  },

   handleImageChange(event) {
      this.storeUser.avatar = event.target.files[0]
      this.selectedUser.avatar = event.target.files[0]
  },

  addUser() {
    const userData = {
          email: this.storeUser.email,
          username: this.storeUser.username,
          password: this.storeUser.password,
          repeat_password: this.storeUser.repeat_password,
          fullname: this.storeUser.fullname,
          avatar: this.storeUser.avatar,
          gender: this.storeUser.gender,
          birthday: this.storeUser.birthday,
          department_id: this.storeUser.department_id,
          position_id: this.storeUser.position_id,
          region_id: this.storeUser.region_id,
          role_id: this.storeUser.role_id,
        }

  const userField = [
          { name: 'email', label: 'Email', maxLength: 50, type:'email', nullable: false }, 
          { name: 'username', label: 'Tên đăng nhập', maxLength: 50, nullable: false }, 
          { name: 'password', label: 'Mật khẩu', maxLength: 50, nullable: false },
          { name: 'repeat_password', label: 'Mật khẩu nhập lại', maxLength: 50, nullable: false }, 
          { name: 'fullname', label: 'Họ tên', maxLength: 50, nullable: false }, 
          { name: 'avatar', label: 'Ảnh đại diện', maxLength: 50, type:'image', nullable: true }, 
          { name: 'gender', label: 'Giới tính', maxLength: 50, nullable: false }, 
          { name: 'birthday', label: 'Ngày sinh', maxLength: 50, nullable: false }, 
          { name: 'department_id', label: 'Bộ phận', maxLength: 50, nullable: false },
          { name: 'position_id', label: 'Chức vụ', maxLength: 50, nullable: false }, 
          { name: 'region_id', label: 'Khu vực', maxLength: 50, nullable: false }, 
          { name: 'role_id', label: 'Quyền', maxLength: 50, nullable: false }, 
          ]
   this.addErrors = util.validateData(userData, userField);
    if(Object.keys(this.addErrors).length ===0) {
      const formData = new FormData();
      formData.append('email', this.storeUser.email)
      formData.append('username', this.storeUser.username)
      formData.append('password', this.storeUser.password)
      formData.append('fullname', this.storeUser.fullname)
      formData.append('avatar', this.storeUser.avatar)
      formData.append('gender', this.storeUser.gender)
      formData.append('birthday', this.storeUser.birthday)
      formData.append('department_id', this.storeUser.department_id)
      formData.append('position_id', this.storeUser.position_id)
      formData.append('region_id', this.storeUser.region_id)
      formData.append('role_id', this.storeUser.role_id)
      api.postData('account/registerUser', formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
    .then(response => {
       if (response.data.error) {
              response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.errors[errorField] = [];
                this.errors[errorField] = errorMessage;
              });
              } else {
                if (!this.dontCloseModal) {
                  this.getUsers();
                  this.closeModal('addUserModal', 'storeUser', 'errors');
                  util.showSuccessAlert('Thêm thành công');
                } else {
                  this.getUsers();
                  this.storeUser={};
                  this.showSuccessMessage = true;
                  this.successMessage = 'Thêm thành công'
                  setTimeout(() => {
                  this.showSuccessMessage = false; 
                }, 1000); 
                }
              }
              console.log(response)
    }) 
    .catch(error => {
      console.error('Lỗi khi thêm người dùng:', error);
    })
      }
},

      deleteUser(id) {
        util.confirmDelete(() => {
          api.deleteData(`account/deleteUser/?id=${id}`)
          .then(response => {
            util.showSuccessAlert('Xóa thành công');
            this.getUsers()
          })
          .catch(error => {
                console.log(error)
            });
          })
      },
      closeModal(modalRef, datakey, errorskey) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
        this[datakey] = {};
        this[errorskey] = {};
      },

       editUser(item) {
        this.selectedUser.oldAvatar=item.avatar
        this.selectedUser.id = item._id;
        this.selectedUser.email = item.email;
        this.selectedUser.fullname = item.fullname;
        this.selectedUser.birthday = format(new Date(item.birthday), 'yyyy-MM-dd');
        this.selectedUser.gender = item.gender;
        this.selectedUser.position_id = item.position_id;
        this.selectedUser.department_id = item.department_id;
        this.selectedUser.region_id = item.region_id;
        this.selectedUser.role_id = item.role_id;
        
      },

        updateUser() {
          const updateData = {
            email: this.selectedUser.email,
            fullname: this.selectedUser.fullname,
            avatar: this.selectedUser.avatar,
            gender: this.selectedUser.gender !== false ? this.selectedUser.gender : '0',
            birthday: this.selectedUser.birthday,
            department_id: this.selectedUser.department_id,
            position_id: this.selectedUser.position_id,
            region_id: this.selectedUser.region_id,
            role_id: this.selectedUser.role_id
          };

         const userField = [
            { name: 'email', label: 'Email', maxLength: 50, nullable: false }, 
            { name: 'fullname', label: 'Họ tên', maxLength: 50, nullable: false }, 
            { name: 'avatar', label: 'Ảnh đại diện', maxLength: 50, nullable: true, type:'image'}, 
            { name: 'gender', label: 'Giới tính', maxLength: 50, nullable: false }, 
            { name: 'birthday', label: 'Ngày sinh', maxLength: 50, nullable: false }, 
            { name: 'department_id', label: 'Bộ phận', maxLength: 50, nullable: false },
            { name: 'position_id', label: 'Chức vụ', maxLength: 50, nullable: false }, 
            { name: 'region_id', label: 'Khu vực', maxLength: 50, nullable: false }, 
            { name: 'role_id', label: 'Quyền', maxLength: 50, nullable: false }, 
          ]

            this.updateErrors = util.validateData(updateData, userField);
          if(Object.keys(this.updateErrors).length ===0) {

              const formData = new FormData();
              formData.append('email', this.selectedUser.email)
              formData.append('fullname', this.selectedUser.fullname)
              formData.append('avatar', this.selectedUser.avatar )
              formData.append('old_avatar', this.selectedUser.oldAvatar)
              formData.append('gender', this.selectedUser.gender)
              formData.append('birthday', this.selectedUser.birthday)
              formData.append('department_id', this.selectedUser.department_id)
              formData.append('position_id', this.selectedUser.position_id)
              formData.append('region_id', this.selectedUser.region_id)
              formData.append('role_id', this.selectedUser.role_id)
            api.putData(`account/updateUser/?id=${this.selectedUser.id}` , formData, {
                      headers: {
                          'Content-Type': 'multipart/form-data'
                        }
            } )
            .then(response => {
              if (response.data.error) {
              response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.updateErrors[errorField] = [];
                this.updateErrors[errorField] = errorMessage;
              });
              } else {
                util.showSuccessAlert('Cập nhật thành công');
                this.closeModal('updateUserModal', 'selectedUser', 'updateErrors');
                this.getUsers();
                
              }
            })
            .catch(error => {
              console.error('Lỗi khi cập nhật bộ phận:', error);
            });
          }
      },

}
}
</script>

