					</div>
					<div class="modal-footer">
						<div>
						<button class="btn ripple btn-primary" @click="qualityCheck">Lưu</button>
						<button class="btn ripple btn-danger ms-2" @click="closeModal('QualityControlModal','QC','updateErrors')" type="button"> Đóng </button>
						</div>
