			<!--End Extra Large Modal -->

			   <!-- Extra Large Modal -->
				<div class="modal fade" id="product_info" ref="semiProductInfo">
					<div class="modal-dialog modal-xl" role="document">
					<div class="modal-content modal-content-demo"  style="box-shadow: rgba(0, 0, 0, 0.5) 0px 4px 100px">
						<div class="modal-header">
						<h6 class="modal-title">Thông tin chi tiết</h6>
