										<span>{{ detailInfo.product_assembly_date }}</span>
								</div>
								</td>
								<td >
								<div class="form-group">
									<p class="mg-b-10 fw-bold">Kết quả kiểm tra</p>
									<span v-if="detailInfo.product_result" class="badge bg-pill" :class="getResult(detailInfo.product_result)">{{ ResultShow(detailInfo.product_result) }}</span>
									<span v-else class="bage bg-pill bg-danger">N/A</span>
								</div>
								</td>
								<td>
								<div class="form-group">
									<p class="mg-b-10 fw-bold">Ngày kiểm tra</p>
									<span  v-if="detailInfo.product_test_date">{{ detailInfo.product_test_date }}</span>
									<span v-else class="bage bg-pill bg-danger">N/A</span>
								</div>
