</template>

<script>
import api from '@/components/uses/api.js'
import util from '@/components/uses/utils.js'
import { format} from "date-fns";

export default {
props:[
    'jobsheetCode'
],
data() {
	return {
		selectedProductCode:[],
		selectedRows: [],
		products:[],
		QC:[],
		productGroups:[],
		departments:[],
		isQC:null,
		jobsheets:[],
		isTableExpanded: true,
		detailInfo:[],
	}
},
 watch: {
    jobsheetCode() {
      this.getProducts()
    }
  },

mounted() {
    this.getProducts()
},
methods: {
	toggleTable() {
      this.isTableExpanded = !this.isTableExpanded; 
    },
	loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#productTB')
        });
      },
	getProducts() {
		if(this.jobsheetCode !=null) {
			api.getData (`jobsheet/showDetailJobSheet/${this.jobsheetCode}`)
			.then(Response => {
				this.jobsheets=Response.data.data[0]
				this.products=Response.data.data[0].dataProduct
				this.productGroups = Response.data.getProductGroup
				this.departments = Response.data.getDepartment
				this.isQC=Response.data._isCheckQuantityControl
				this.loadtable()
			})
			.catch(Error => {
				console.log(Error)
			})
		}
	},
	   getStatus(status) {
         switch(status) {
          case '0': return 'bg-primary-light';
          case '1': return 'bg-success-light';
          case '2': return 'bg-success-light';
          case '3': return 'bg-success-light';
		  case '4': return 'bg-success-light';
		  case '5': return 'bg-success-light';
		  case '6': return 'bg-success-light';
		  case '7': return 'bg-success-light';
		  case '8': return 'bg-success-light';
		  case '9': return 'bg-success-light';
		  case '10': return 'bg-danger-light';
          default: return 'bg-danger-light';
        }
      },
    
      productStatus (statusCode) {
         switch(parseInt(statusCode)) {
          case 0: return "Mới tạo";
          case 1: return "Đã yêu cầu xuất kho";
          case 2: return "Đã xuất kho";
          case 3: return "Đã yêu cầu sản xuất";
		  case 4: return "Đang sản xuất";
		  case 5: return "Hoàn thành";
		  case 6: return "Đã gửi yêu cầu kiểm tra";
		  case 7: return "Đã kiểm tra";
		  case 8: return "Đã gửi yêu cầu nhập kho";
		  case 9: return "Đã nhập kho";
		  case 10: return "Đã hủy";
          default: return "Không rõ"
        }
      },

	  exportMaterial(id) {
		 const updateData ={
			jobsheetCode :this.jobsheetCode,
			arrayProductID : this.selectedProductCode
		 }
		 util.confirmDelete(() => {
            api.putData(`jobsheet/OrderExportMaterialsJobSheet`, updateData) 
			.then(Response => {
				if(Response.data.status == 200) {
					util.showSuccessAlert('Đã yêu cầu xuất kho')
					this.getProducts()
					this.selectedRows.splice(0, this.selectedRows.length);
				} 
			})
			.catch(Error => {
				console.log(Error)
			})
          }, 'Bạn chắc chắn muốn yêu cầu xuất kho?')
	  }, 

	  confirmExportMaterial(id) {
		 const updateData ={
			jobsheetCode :this.jobsheetCode,
			arrayProductID : this.selectedProductCode
		 }
		 util.confirmDelete(() => {
            api.putData(`jobsheet/ExportMaterialsJobSheet`, updateData) 
			.then(Response => {
				if(Response.data.status == 200) {
					util.showSuccessAlert('Đã xuất kho')
					this.getProducts()
					this.selectedRows.splice(0, this.selectedRows.length);
				} 
			})
			.catch(Error => {
				console.log(Error)
			})
          }, 'Bạn chắc chắn muốn yêu cầu xuất kho?')
	  }, 
	  assemblyRequest(id) {
		 const updateData ={
			jobsheetCode :this.jobsheetCode,
			arrayProductID : this.selectedProductCode
		 }
		 util.confirmDelete(() => {
            api.putData(`jobsheet/OrderProductJobSheet`, updateData) 
			.then(Response => {
				if(Response.data.status == 200) {
					util.showSuccessAlert('Đã xuất kho')
					this.getProducts()
					this.selectedRows.splice(0, this.selectedRows.length);
					 
				} 
				console.log(Response)
			})
			.catch(Error => {
				console.log(Error)
			})
          }, 'Bạn chắc chắn muốn yêu cầu xuất kho?')
	  }, 
	  warehoustRequest() {
			const updateData ={
				jobsheetCode :this.jobsheetCode,
				arrayProductID : this.selectedProductCode
			}
			util.confirmDelete(() => {
				api.putData(`jobsheet/OrderStoreJobSheet`, updateData) 
				.then(Response => {
					if(Response.data.status == 200) {
						util.showSuccessAlert('Đã gửi yêu cầu nhập kho')
						this.getProducts()
						this.selectedRows.splice(0, this.selectedRows.length);
					} 
				})
				.catch(Error => {
					console.log(Error)
				})
			}, 'Bạn chắc chắn muốn gửi yêu cầu nhập kho?')
		},
		confirmWarehouse() {
			const updateData ={
				jobsheetCode :this.jobsheetCode,
				arrayProductID : this.selectedProductCode
			}
			util.confirmDelete(() => {
				api.putData(`jobsheet/StoreWarehouseJobSheet`, updateData) 
				.then(Response => {
					if(Response.data.status == 200) {
						util.showSuccessAlert('Đã nhập kho')
						this.getProducts()
						this.selectedRows.splice(0, this.selectedRows.length);
					} 
				})
				.catch(Error => {
					console.log(Error)
				})
			}, 'Nhập kho sản phẩm?')
		},
		checkExist() {
			if(this.isQC == 0) {
				const modal = this.$refs.QualityControlModal;
				$(modal).modal('show'); 
				const user = JSON.parse(sessionStorage.getItem('userLogin'));
				this.QC.user_id = user.fullname,
				this.QC.quality_control_create_date = format(new Date(), "dd/MM/yyyy")
			} else {
				this.qualityCheck()
			}
		},
		 qualityCheck() {
				const updateData ={
				jobsheetCode :this.jobsheetCode,
				arrayProductID : this.selectedProductCode, 
				fullname:this.QC.user_id,
				type_quality_control:this.QC.type_quality_control,
				type_product_to_check:this.QC.type_product_to_check,
				department:this.QC.deparment,
				type_product_group:this.QC.type_product_group,
				quality_control_specification:this.QC.quality_control_specification,
				quality_control_note:this.QC.quality_control_note
				}
				api.putData(`jobsheet/OrderQCJobSheet`, updateData) 
					.then(Response => {
						if(Response.data.status == 200) {
							util.showSuccessAlert('Đã gửi yêu cầu kiểm tra')
							this.getProducts()
							this.closeModal('QualityControlModal','QC','updateErrors')
							this.selectedRows.splice(0, this.selectedRows.length);
						} 
					})
					.catch(Error => {
						console.log(Error)
					})
	  },
	   	closeModal(modalRef, datakey, errorskey) {
			const modal = this.$refs[modalRef];
			$(modal).modal('hide');
			this[datakey] = {};
			this[errorskey] = {};
			},

		getResult(result) {
         switch(result) {
          case '0': return 'bg-danger-light';
          case '1': return 'bg-success-light';
          default: return 'bg-danger-light';
        }
      },
    
      ResultShow (result) {
         switch(parseInt(result)) {
          case 0: return "FAIL";
          case 1: return "PASS";
          default: return "Không rõ"
        }
      },

	checkStatus() {
		this.selectedProductCode = []
		for (let i = 0; i < this.selectedRows.length; i++) {
			this.selectedProductCode.push(this.selectedRows[i].product_code);
		}
	},

	checkAction(Status) {
		 if (this.selectedRows.length < 1) {
			return false;
		}
		for(let j =0; j<this.selectedRows.length; j++) {
			if(this.selectedRows[0].product_status != this.selectedRows[j].product_status){
				return false 
			}
			else {
				if(this.selectedRows[j].product_status != Status) {
					return false
				}

				if(this.selectedRows[j].product_status == 7 && this.selectedRows[j].product_result != 1) {
					return false
				}
			}
		}
		return true
	},

		viewDetail(modalId,item){
     	 $(`#${modalId}`).modal('show');
		this.detailInfo=item
	}


}
}
</script>
