<template>
  <!-- Main Content-->
  <div class="main-content side-content pt-0">
    <div class="main-container container-fluid pt-5">
      <div class="inner-body">
        <!-- Page Header -->
        <div class="page-header">
          <div>
            <h2 class="main-content-title tx-24 mg-b-5">
              Quản lý thiết bị
            </h2>
            <ol class="breadcrumb">
              <li class="breadcrumb-item"><a href="#">Trang chủ</a></li>
              <li class="breadcrumb-item active" aria-current="page">
                Quản lý thiết bị
              </li>
            </ol>
          </div>
          <div class="d-flex">
            <div class="justify-content-center">
              <a data-bs-target="#add_monitoring_network" data-bs-toggle="modal" class="btn btn-primary btn-rounded my-2 text-white">
                <i class="fe fe-plus"></i> Thêm mới </a>
            </div>
          </div>
        </div>
        <!-- End Page Header -->
                        
        <!-- Row -->
        <div class="row row-sm">
          <div class="col-lg-12">
            <div class="card custom-card">
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">
                    Quản lý thiết bị
                  </h6>
                  <p class="text-muted card-sub-title"></p>
                </div>
                <div class="table-responsive">
                  <table :data="monitors" class="table table-bordered" id="monitorTable">
                    <thead>
                      <tr>
                        <th>STT</th>
                        <th>Khu vực</th>
                        <th>Đã lắp đặt</th>
                        <th>Dự kiến lắp đặt</th>
                        <th>Ghi chú</th>
                        <th>Thao tác</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in monitors" :key="item._id">
                        <td>{{ index+1 }}</td>
                        <td>{{ item.getProvinceData ? item.getProvinceData[0].province_name :''}}</td>
                        <td>{{item.installed ||''}}</td>
                        <td>{{item.next_phase||''}}</td>
                        <td>{{item.note||''}}</td>
                        <td>
                            <router-link :to="'/monitoring_network/detail/'+ item._id" class="btn btn-sm btn-outline-primary ms-1"> <i class="fe fe-eye"></i></router-link>
                             <a @click="editMonitor(item)" data-bs-target="#update_monitoring_network" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-info ms-1"><i class="fe fe-edit-2"></i></a>
                          <a @click="deleteMonitor(item._id)" class="btn btn-sm btn-outline-danger ms-1"> <i class="fe fe-trash"></i></a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- End Row -->
          
          <!-- Row -->
                <div class="row">
                    <div class="col-lg-6 col-md-12">
                        <div class="card custom-card overflow-hidden">
                            <div class="card-body">
                                <ChartByArea/>
                            </div>
                        </div>
                    </div>
                    <div class="col-lg-6 col-md-12">
                        <div class="card custom-card overflow-hidden">
                            <div class="card-body">
                                 <ChartByYear/>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- End Row -->


             

             
      </div>
    </div>
  </div>
  <!-- End Main Content-->

    <!-- Large Modal -->
  <div class="modal" id="add_monitoring_network" ref="addMonitoringModal">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Thêm mới</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="addMonitor">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td >
                    <div class="form-group">
                      <label class="fw-bold ">Khu vực</label>

                      <select class="form-control form-select text-body" v-model="storeMonitoring.area_id" :class="{'is-invalid':addErrors.area_id}">
                        <option v-for="(item, index) in provinces" :key="item._id" :value="item.province_id">{{ item.province_name }}</option>
                      </select>
                       <div class="invalid-feedback mt-2" v-if="addErrors.area_id">{{addErrors.area_id}}</div>
                    
                    
                    
                    </div>
                  </td>
                 <td class="align-top">
                    <div class="form-group">
                      <label class="fw-bold ">Dự kiến lắp đặt</label>
                      <input v-model="storeMonitoring.next_phase" class="form-control text-body"  :class="{'is-invalid':addErrors.next_phase}" type="number" placeholder="Số lượng dự kiến lắp đặt" />
                      <div class="invalid-feedback" v-if="addErrors.next_phase">{{addErrors.next_phase}}</div>
                    </div>
                  </td>
                </tr>
           
                <tr>
                  
                  <td colspan="2">
                    <div class="form-group">
                      <label class="fw-bold ">Ghi chú</label>
                      <textarea v-model="storeMonitoring.note" class="form-control text-body" :class="{'is-invalid':addErrors.note}" rows="4" placeholder="Ghi chú"></textarea>
                      <div class="invalid-feedback">{{addErrors.note}}</div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
           <div v-if="showSuccessMessage" class="alert alert-success text-center">
            {{ successMessage }}
          </div>
          <div class="modal-footer d-flex justify-content-between">
            <div class="form-check">
              <input class="form-check-input" type="checkbox" id="check1" name="option1" value="something" v-model="dontCloseModal">
              <label class="form-check-label fw-bold">Không đóng màn hình</label>
            </div>
            <div>
              <button class="btn ripple btn-primary" type="submit">Lưu</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('addMonitoringModal','storeMonitoring','addErrors')" type="button"> Đóng </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->

   <!-- Large Modal -->
  <div class="modal" id="update_monitoring_network" ref="updateMonitoringModal">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Cập nhật</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="updateMonitor">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td >
                    <div class="form-group">
                      <label class="fw-bold ">Khu vực</label>
                       <select class="form-control form-select text-body" v-model="selectedMonitoring.area_id" :class="{'is-invalid':updateErrors.area_id}">
                        <option v-for="(item, index) in provinces" :key="item._id" :value="item.province_id">{{ item.province_name }}</option>
                      </select>
                       <div class="invalid-feedback mt-2" v-if="updateErrors.area_id">{{updateErrors.area_id}}</div>
                    </div>
                  </td>
                    <td class="align-top">
                    <div class="form-group">
                      <label class="fw-bold ">Dự kiến lắp đặt</label>
                      <input v-model="selectedMonitoring.next_phase" class="form-control text-body" name="product_type_name" :class="{'is-invalid':updateErrors.next_phase}" type="number" placeholder="Số lượng dự kiến lắp đặt" />
                      <div class="invalid-feedback" v-if="updateErrors.next_phase">{{updateErrors.next_phase}}</div>
                    </div>
                  </td>
                
                </tr>
                <tr>
                  
                  <td colspan="2">
                    <div class="form-group">
                      <label class="fw-bold ">Ghi chú</label>
                      <textarea v-model="selectedMonitoring.note" class="form-control text-body" :class="{'is-invalid':updateErrors.note}" rows="5" placeholder="Ghi chú"></textarea>
                      <div class="invalid-feedback">{{updateErrors.note}}</div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
         
          <div class="modal-footer">
            <div>
              <button class="btn ripple btn-primary" type="submit">Lưu</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('updateMonitoringModal','storeMonitoring','addErrors')" type="button"> Đóng </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->

     
</template>

<script>
import util from '@/components/uses/utils.js'
import api from '@/components/uses/api.js'

import ChartByArea from '@/views/MonitoringNetwork/ChartByArea.vue'
import ChartByYear from '@/views/MonitoringNetwork/ChartByYear.vue'

export default {
  components: {
    ChartByArea,
    ChartByYear,
},
  data() {
    return {
      storeMonitoring: [],
      selectedMonitoring: [],
      addErrors: [],
      updateErrors: [],
      dontCloseModal: false,
      showSuccessMessage: false,
      successMessage: '',
      monitors:[],
      provinces:[],
    }
  },
  mounted() {
    this.getMonitors();
  },
  methods: {
 
     loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#monitorTable')
        });
      },

      getMonitors() {
        api.getData('manager-ims/listManagerIMS')
        .then(Response => {
         
          this.monitors = Response.data.data.getData
          this.provinces = Response.data.data.provinces
          this.loadtable()
        })
        .catch(Error => {
          console.log(Error)
        })

      },

    addMonitor() {
      const monitorData = {
        area_id: this.storeMonitoring.area_id,
        next_phase: this.storeMonitoring.next_phase,
        note: this.storeMonitoring.note
      }
      const monitorField = [
        {name:'area_id', label:'Tên khu vực', nullable:false},
        {name:'next_phase', label:'Số lượng dự kiến',type:'number', nullable:true},
        {name:'note', label:'Ghi chú', nullable:true},
      ]
      this.addErrors = util.validateData(monitorData, monitorField);
      if(Object.keys(this.addErrors).length >0) {return}

      api.postData('manager-ims/storeManagerIMS', monitorData) 
      .then(Response => {
        if(Response.data.status==200) {
          if(!this.dontCloseModal) 
          {
            this.getMonitors()
            util.showSuccessAlert('Thêm thành công')
          this.closeModal('addMonitoringModal','storeMonitoring', 'addErrors')
          }
          else {
             this.getMonitors()
            this.storeMonitoring={};
            this.showSuccessMessage=true;
            this.successMessage='Thêm thành công'
            setTimeout(() => {
              this.showSuccessMessage=false
            }, 1000)
        }
          
        } 
       
      })
      .catch( Error => {
        console.log(Error)
      })
    },

    editMonitor(item) {
      this.selectedMonitoring = {
        id:item._id,
        area_id: item.area_id,
        next_phase: item.next_phase,
        note: item.note,
      }
    },

    updateMonitor() {
      const dataUpdate = {
        area_id: this.selectedMonitoring.area_id,
        next_phase: this.selectedMonitoring.next_phase,
        note: this.selectedMonitoring.note,
      }
       const updateField = [
        {name:'area_id', label:'Tên khu vực', nullable:false},
        {name:'next_phase', label:'Số lượng dự kiến',type:'number', nullable:false},
        {name:'note', label:'Ghi chú', nullable:true},
      ]
      this.updateErrors = util.validateData(dataUpdate, updateField);

      if(Object.keys(this.updateErrors).length >0) {return}
      
      api.putData(`manager-ims/updateManagerIMS/${this.selectedMonitoring.id}`, dataUpdate) 
      .then(Response => {
        console.log(Response)
        if(Response.data.status ==200) {
          util.showSuccessAlert('Cập nhật thành công')
          this.getMonitors()
          this.closeModal('updateMonitoringModal', 'selectedMonitoring', 'updateErrors')
        }
      })
      .catch(Error => {
        console.log(Error)
      })
    },

     deleteMonitor(id) {
        util.confirmDelete(() => {
          api.deleteData(`manager-ims/deleteManagerIMS/${id}`)
          .then(Response => {
            util.showSuccessAlert('Xóa thành công');
            this.getMonitors();
          })
          .catch(error => {
            console.error('Lỗi khi xóa khu vực:', error);
          });
          })
      },

    closeModal(modalRef, datakey, errorskey) {
      const modal = this.$refs[modalRef];
      $(modal).modal('hide');
      this[datakey] = {};
      this[errorskey] = {};
    },
  }
}
</script>
