<template>
  <!-- Main Content-->
  <div class="main-content side-content pt-0">
    <div class="main-container container-fluid pt-5">
      <div class="inner-body">
        <!-- Page Header -->
        <div class="page-header">
          <div>
            <h2 class="main-content-title tx-24 mg-b-5">Quản lý dòng sản phẩm</h2>
            <ol class="breadcrumb">
              <li class="breadcrumb-item">
                <a href="#">Trang chủ</a>
              </li>
              <li class="breadcrumb-item active" aria-current="page"> Dòng sản phẩm </li>
            </ol>
          </div>
          <div class="d-flex">
            <div class="justify-content-center">
              <a data-bs-target="#add_product_series" data-bs-toggle="modal" class="btn btn-primary btn-rounded my-2 text-white">
                <i class="fe fe-plus"></i> Thêm mới </a>
            </div>
          </div>
        </div>
        <!-- End Page Header -->
        <!-- Row -->
        <div class="row row-sm">
          <div class="col-lg-12">
            <div class="card custom-card">
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">Danh sách dòng sản phẩm</h6>
                  <p class="text-muted card-sub-title"></p>
                </div>
                <div class="table-responsive">
                  <table :data="product_series" class="table table-bordered" id="productSeriesTable">
                    <thead>
                      <tr>
                        <th class="wd-20p">STT</th>
                        <th class="wd-25p">Mã dòng sản phẩm</th>
                        <th class="wd-25p">Tên dòng sản phẩm</th>
                        <th class="wd-25p">Ghi chú</th>
                        <th class="wd-15p no-print no-excel">Thao tác</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in product_series" :key="item.id">
                        <td>{{ index + 1 }}</td>
                        <td>{{ item.product_series_code }}</td>
                        <td>{{ item.product_series_name }}</td>
                        <td>{{ item.product_series_note }}</td>
                        <td>
                          <!-- <button @click="editProductSeries(item)" data-bs-target="#update_product_series" data-bs-toggle="modal" class="btn btn-sm ripple btn-main-primary text-white">Sửa</button>
                          <button @click="deleteProductSeries(item._id)" class="btn btn-sm ripple btn-danger ms-1"> Xóa</button> -->

                           <a @click="editProductSeries(item)" data-bs-target="#update_product_series" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-info ms-1"><i class="fe fe-edit-2"></i></a>
                          <a @click="deleteProductSeries(item._id)" class="btn btn-sm btn-outline-danger ms-1"> <i class="fe fe-trash"></i></a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- End Row -->
      </div>
    </div>
  </div>
  <!-- End Main Content-->
  <!-- Large Modal -->
  <div class="modal" id="add_product_series" ref="addProSeriesModal">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Thêm mới dòng sản phẩm</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="addProSeries">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Mã dòng sản phẩm</label>
                      <input v-model="storeProSeries.product_series_code" class="form-control text-body" name="product_series_code" :class="{'is-invalid':addErrors.product_series_code}" type="text" placeholder="Nhập mã dòng sản phẩm" />
                      <div class="invalid-feedback mt-2" v-if="addErrors.product_series_code">{{addErrors.product_series_code}}</div>
                    </div>
                  </td>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Tên dòng sản phẩm</label>
                      <input v-model="storeProSeries.product_series_name" class="form-control text-body" name="product_series_name" :class="{'is-invalid':addErrors.product_series_name}" type="text" placeholder="Nhập tên dòng sản phẩm" />
                      <div class="invalid-feedback" v-if="addErrors.product_series_name">{{addErrors.product_series_name}}</div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td colspan="2">
                    <div class="form-group">
                      <label class="fw-bold ">Ghi chú</label>
                      <textarea v-model="storeProSeries.product_series_note" class="form-control text-body" :class="{'is-invalid':addErrors.product_series_note}" rows="5" placeholder="Nhập ghi chú"></textarea>
                      <div class="invalid-feedback">{{addErrors.product_series_note}}</div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div v-if="showSuccessMessage" class="alert alert-success text-center">
            {{ successMessage }}
          </div>
          <div class="modal-footer d-flex justify-content-between">
            <div class="form-check">
              <input class="form-check-input" type="checkbox" id="check1" name="option1" value="something" v-model="dontCloseModal">
              <label class="form-check-label fw-bold">Không đóng màn hình</label>
            </div>
            <div>
              <button class="btn ripple btn-primary" type="submit">Lưu</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('addProSeriesModal','storeProSeries','addErrors')" type="button"> Đóng </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->
  <!-- Large Modal -->
  <div class="modal" id="update_product_series" ref="updateProSeries">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Cập nhật dòng sản phẩm</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="updateProSeries">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Mã dòng sản phẩm</label>
                      <input v-model="selectedProSeries.product_series_code" :class="{'is-invalid': updateErrors.product_series_code}" class="form-control text-body" type="text" placeholder="Nhập mã dòng sản phẩm" />
                      <div class="invalid-feedback" v-if="updateErrors.product_series_code">{{updateErrors.product_series_code}} </div>
                    </div>
                  </td>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Tên dòng sản phẩm</label>
                      <input v-model="selectedProSeries.product_series_name" class="form-control text-body" :class="{'is-invalid': updateErrors.product_series_name}" type="text" placeholder="Nhập tên dòng sản phẩm" />
                      <div class="invalid-feedback" v-if="updateErrors.product_series_name">{{updateErrors.product_series_name}} </div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td colspan="2">
                    <div class="form-group">
                      <label class="fw-bold ">Ghi chú</label>
                      <textarea v-model="selectedProSeries.product_series_note" :class="{'is-invalid':updateErrors.product_series_note}" class="form-control text-body" name="" rows="5" placeholder="Nhập ghi chú"></textarea>
                      <div class="invalid-feedback" v-if="updateErrors.product_series_note">{{updateErrors.product_series_note}} </div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="modal-footer">
            <button class="btn ripple btn-primary" type="submit">Lưu</button>
            <button class="btn ripple btn-danger" @click="closeModal('updateProSeries', 'selectedProSeries','updateErrors' )" type="button"> Đóng </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->
</template>
<script>
  import util from '@/components/uses/utils.js'
  import api from '@/components/uses/api.js'
  export default {
    data() {
      return {
        product_series: {
          product_series_code:'',
          product_series_name:'',
          product_series_note:''
        },
        storeProSeries:{
          product_series_code:'',
          product_series_name:'',
          product_series_note:''
        },
        selectedProSeries: [],
        dontCloseModal: false,
        addErrors: [],
        updateErrors: [],
        showSuccessMessage: false, 
        successMessage: '',
      };
    },
    mounted() {
      this.getproduct_series();
    },
    methods: {
      loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#productSeriesTable')
        });
      },
      getproduct_series() {
        api.getData('product-series/listProductSeries')
        .then(response=> {
          this.product_series = response.data.data;
          this.loadtable();
          console.log(response)
        })
        .catch(error=> {
          console.log(error)
        })
      },

      addProSeries() {
        const productSeriesData = {
          product_series_code: this.storeProSeries.product_series_code,
          product_series_name: this.storeProSeries.product_series_name,
          product_series_note: this.storeProSeries.product_series_note ? this.storeProSeries.product_series_note : ''
        };
        const proTypeField = [
          { name: 'product_series_code', label: 'Mã dòng sản phẩm', maxLength: 50, nullable: false }, 
          { name: 'product_series_name', label: 'Tên dòng sản phẩm', maxLength: 100, nullable: false }, 
          { name: 'product_series_note', label: 'Ghi chú', maxLength: 50, nullable: true }
        ]
        this.addErrors = util.validateData(productSeriesData, proTypeField);
        if (Object.keys(this.addErrors).length === 0) {
          api.postData('product-series/storeProductSeries',productSeriesData)
          .then(response => {
             if (response.data.error) {
              response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.errors[errorField] = [];
                this.errors[errorField] = errorMessage;
              });
              } else {
                if (!this.dontCloseModal) {
                  this.getproduct_series();
                  this.closeModal('addProSeriesModal', 'storeProSeries', 'errors');
                  util.showSuccessAlert('Thêm thành công');
                } else {
                  this.getproduct_series();
                  this.storeProSeries={};
                  this.showSuccessMessage = true;
                  this.successMessage = 'Thêm thành công'
                  setTimeout(() => {
                  this.showSuccessMessage = false; 
                }, 1000); 
                }
              }
          })
            .catch(error => {
            console.error('Lỗi khi thêm dòng sản phẩm:', error);
        });
        }
      },

      deleteProductSeries(id) {
        util.confirmDelete(() => {
          api.deleteData(`product-series/deleteProductSeries/?id=${id}`)
          .then(response => {
            util.showSuccessAlert('Xóa thành công');
            this.getproduct_series();
          })
          .catch(error => {
          console.error('Lỗi khi xóa dòng sản phẩm:', error);
          });
          })
      },

      closeModal(modalRef, datakey, errorskey) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
        this[datakey] = {};
        this[errorskey] = {};
      },

      editProductSeries(item) {
        this.selectedProSeries.id = item._id;
        this.selectedProSeries.product_series_code = item.product_series_code;
        this.selectedProSeries.product_series_name = item.product_series_name;
        this.selectedProSeries.product_series_note = item.product_series_note;
      },
      updateProSeries() {
        const dataUpdate = {
          product_series_code: this.selectedProSeries.product_series_code,
          product_series_name: this.selectedProSeries.product_series_name,
          product_series_note: this.selectedProSeries.product_series_note
        }
        const updateProSeriesField = [
          { name: 'product_series_code', label: 'Mã dòng sản phẩm', maxLength: 50, nullable: false}, 
          { name: 'product_series_name', label: 'Tên dòng sản phẩm', maxLength: 50, nullable: false}, 
          { name: 'product_series_note', label: 'Ghi chú', maxLength: 50, nullable: true}
        ]
        this.updateErrors = util.validateData(dataUpdate, updateProSeriesField); 
        if (Object.keys(this.updateErrors).length === 0) {
            api.putData(`product-series/updateProductSeries/${this.selectedProSeries.id}` , dataUpdate)
            .then(response => {
              if (response.data.error) {
              response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.updateErrors[errorField] = [];
                this.updateErrors[errorField] = errorMessage;
              });
              } else {
                util.showSuccessAlert('Cập nhật thành công');
                this.closeModal('updateProSeries', 'selectedProSeries', 'updateErrors');
                this.getproduct_series();
              }
            })
            .catch(error => {
              console.error('Lỗi khi cập nhật dòng sản phẩm:', error);
            });
        }
      },
    }
  }
</script>
