<template>
  <!-- Main Content-->
  <div class="main-content side-content pt-0">
    <div class="main-container container-fluid pt-5">
      <div class="inner-body">
        <!-- Page Header -->
        <div class="page-header">
          <div>
            <h2 class="main-content-title tx-24 mg-b-5">Quản lý chức vụ</h2>
            <ol class="breadcrumb">
              <li class="breadcrumb-item">
                <a href="#">Trang chủ</a>
              </li>
              <li class="breadcrumb-item active" aria-current="page"> Chức vụ </li>
            </ol>
          </div>
          <div class="d-flex">
            <div class="justify-content-center">
              <a data-bs-target="#add_position" data-bs-toggle="modal" class="btn btn-primary btn-rounded my-2 text-white">
                <i class="fe fe-plus"></i> Thêm mới </a>
            </div>
          </div>
        </div>
        <!-- End Page Header -->
        <!-- Row -->
        <div class="row row-sm">
          <div class="col-lg-12">
            <div class="card custom-card">
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">Danh sách chức vụ</h6>
                  <p class="text-muted card-sub-title"></p>
                </div>
                <div class="table-responsive">
                  <table :data="positions" class="table table-bordered" id="positionTable">
                    <thead>
                      <tr>
                        <th class="wd-20p">STT</th>
                        <th class="wd-25p">Mã chức vụ</th>
                        <th class="wd-25p">Tên chức vụ</th>
                        <th class="wd-25p">Ghi chú</th>
                        <th class="wd-15p">Thao tác</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in positions" :key="item._id">
                        <td>{{ index + 1 }}</td>
                        <td>{{ item.position_code }}</td>
                        <td>{{ item.position_name }}</td>
                        <td>{{ item.position_note }}</td>
                        <td>
                            <a @click="editPosition(item)"  data-bs-target="#update_position" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-info ms-1"><i class="fe fe-edit-2"></i></a>
                          <a @click="deletePosition(item._id)" class="btn btn-sm btn-outline-danger ms-1"> <i class="fe fe-trash"></i></a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- End Row -->
      </div>
    </div>
  </div>
  <!-- End Main Content-->
 <!-- Large Modal -->
  <div class="modal" id="add_position" ref="addPositionModal">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Thêm mới chức vụ</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="addPosition">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Mã chức vụ</label>
                      <input v-model="storePosition.position_code" class="form-control text-body" name="position_code" :class="{'is-invalid':addErrors.position_code}" type="text" placeholder="Nhập mã chức vụ" />
                      <div class="invalid-feedback mt-2" v-if="addErrors.position_code">{{addErrors.position_code}}</div>
                    </div>
                  </td>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Tên chức vụ</label>
                      <input v-model="storePosition.position_name" class="form-control text-body" name="position_name" :class="{'is-invalid':addErrors.position_name}" type="text" placeholder="Nhập tên chức vụ" />
                      <div class="invalid-feedback" v-if="addErrors.position_name">{{addErrors.position_name}}</div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td colspan="2">
                    <div class="form-group">
                      <label class="fw-bold ">Ghi chú</label>
                      <textarea v-model="storePosition.position_note" class="form-control text-body" :class="{'is-invalid':addErrors.position_note}" rows="5" placeholder="Nhập ghi chú"></textarea>
                      <div class="invalid-feedback">{{addErrors.position_note}}</div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div v-if="showSuccessMessage" class="alert alert-success text-center">
            {{ successMessage }}
          </div>
          <div class="modal-footer d-flex justify-content-between">
            <div class="form-check">
              <input class="form-check-input" type="checkbox" id="check1" name="option1" value="something" v-model="dontCloseModal">
              <label class="form-check-label fw-bold">Không đóng màn hình</label>
            </div>
            <div>
              <button class="btn ripple btn-primary" type="submit">Lưu</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('addPositionModal','storePosition','addErrors')" type="button"> Đóng </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->
  <!-- Large Modal -->
  <div class="modal" id="update_position" ref="updatePositionModal">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Cập nhật chức vụ</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="updatePosition">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Mã chức vụ</label>
                      <input v-model="selectedPosition.position_code" :class="{'is-invalid': updateErrors.position_code}" class="form-control text-body" type="text" placeholder="Nhập mã chức vụ" />
                      <div class="invalid-feedback" v-if="updateErrors.position_code">{{updateErrors.position_code}} </div>
                    </div>
                  </td>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Tên chức vụ</label>
                      <input v-model="selectedPosition.position_name" class="form-control text-body" :class="{'is-invalid': updateErrors.position_name}" type="text" placeholder="Nhập tên chức vụ" />
                      <div class="invalid-feedback" v-if="updateErrors.position_name">{{updateErrors.position_name}} </div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td colspan="2">
                    <div class="form-group">
                      <label class="fw-bold ">Ghi chú</label>
                      <textarea v-model="selectedPosition.position_note" :class="{'is-invalid':updateErrors.position_note}" class="form-control text-body" name="" rows="5" placeholder="Nhập ghi chú"></textarea>
                      <div class="invalid-feedback" v-if="updateErrors.position_note">{{updateErrors.position_note}} </div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="modal-footer">
            <button class="btn ripple btn-primary" type="submit">Lưu</button>
            <button class="btn ripple btn-danger" @click="closeModal('updatePositionModal', 'selectedPosition','updateErrors' )" type="button"> Đóng </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->
</template>

<script>
  import util from '@/components/uses/utils.js'
  import api from '@/components/uses/api.js'
  export default {
    data() {
      return {
        positions:[],
		    storePosition: {
          position_code:'',
          position_name:'',
          position_note:''
        },
        selectedPosition: [],
        dontCloseModal: false,
        addErrors: [],
        updateErrors: [],
        showSuccessMessage:false,
        notifications: [],
      };
    },
    mounted() {
      this.getPositions();
    },
    methods: {
      loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#positionTable')
        });
      },
      async getPositions() {
        try {
          const Response = await api.getData('position/listPosition');
          this.positions = Response.data.data
          this.loadtable();
        } catch (error) {
          util.errorRedirect()
        }
      },

      addPosition() {
        const positionData = {
          position_code: this.storePosition.position_code,
          position_name: this.storePosition.position_name,
          position_note: this.storePosition.position_note || '',
        };

        const positionField = [
          { name: 'position_code', label: 'Mã bộ phận', maxLength: 50, nullable: false },
          { name: 'position_name', label: 'Tên bộ phận', maxLength: 50, nullable: false },
          { name: 'position_note', label: 'Ghi chú', maxLength: 50, nullable: true }
        ];

        this.addErrors = util.validateData(positionData, positionField);

        if (Object.keys(this.addErrors).length === 0) {
          api.postData('position/storePosition', positionData)
            .then(response => {
              if (response.data.error) {
                response.data.error.details.forEach((error) => {
                  const errorMessage = error.message;
                  const errorField = error.path[0];
                  this.errors[errorField] = [];
                  this.errors[errorField] = errorMessage;
                });
              } else {
                if (!this.dontCloseModal) {
                  this.getPositions();
                  this.closeModal('addPositionModal', 'storePosition', 'errors');
                  util.showSuccessAlert('Thêm thành công');
                } else {
                  this.getPositions();
                  this.storePosition = {};
                  this.showSuccessMessage = true;
                  this.successMessage = 'Thêm thành công';
                  setTimeout(() => {
                    this.showSuccessMessage = false;
                  }, 1000);
                }
              }
            })
            .catch(error => {
              console.error('Lỗi khi thêm:', error);
            });
        }
      },

	  closeModal(modalRef, datakey, errorskey) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
        this[datakey] = {};
        this[errorskey] = {};
      },

	  editPosition(item) {
        this.selectedPosition.id = item._id;
        this.selectedPosition.position_code = item.position_code;
        this.selectedPosition.position_name = item.position_name;
        this.selectedPosition.position_note = item.position_note;
      },

	   updatePosition() {
        const dataUpdate = {
          position_code: this.selectedPosition.position_code,
          position_name: this.selectedPosition.position_name,
          position_note: this.selectedPosition.position_note
        }
        const updatePositionField = [
          { name: 'position_code', label: 'Mã bộ phận', maxLength: 50, nullable: false}, 
          { name: 'position_name', label: 'Tên bộ phận', maxLength: 50, nullable: false}, 
          { name: 'position_note', label: 'Ghi chú', maxLength: 50, nullable: true}
        ]
        this.updateErrors = util.validateData(dataUpdate, updatePositionField); 
        if (Object.keys(this.updateErrors).length === 0) {
            api.putData(`position/updatePosition/${this.selectedPosition.id}`, dataUpdate)
            .then(response => {
              if (response.data.error) {
              response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.updateErrors[errorField] = [];
                this.updateErrors[errorField] = errorMessage;
              });
              } else {
                util.showSuccessAlert('Cập nhật thành công');
                this.closeModal('updatePositionModal', 'selectedPosition', 'updateErrors');
                this.getPositions();
              }
            })
            .catch(error => {
              console.error('Lỗi khi cập nhật bộ phận:', error);
			  console.log(error)
            });
        }
      },
	    deletePosition(id) {
        util.confirmDelete(() => {
          api.deleteData(`position/deletePosition/?id=${id}`)
          .then(response => {
            util.showSuccessAlert('Xóa thành công');
            this.getPositions();
          })
          .catch(error => {
          console.error('Lỗi khi xóa chức vụ:', error);
          });
          })
      },

    }
  }
</script>

