<div class="main-content side-content pt-0">
  <div class="main-container container-fluid pt-5">
    <div class="inner-body">
      <!-- Page Header -->
      <div class="page-header">
        <div>
          <h2 class="main-content-title tx-24 mg-b-5">Lịch sản xuất</h2>
          <ol class="breadcrumb">
            <li class="breadcrumb-item">
              <a href="#">Trang chủ</a>
            </li>
            <li class="breadcrumb-item active" aria-current="page"> jobsheet </li>
          </ol>
        </div>
        <div class="d-flex">
          <div class="justify-content-center">
            <a class="btn ripple btn-primary btn-rounded text-light" data-bs-target="#add_jobsheet_modal" data-bs-toggle="modal">
              <i class="fe fe-plus"></i> Thêm mới </a>
          </div>
        </div>
      </div>
      <!-- End Page Header -->
      <!-- Row -->
      <div class="row row-sm">
        <div class="col-lg-12">
          <div class="card custom-card">
            <div class="card-body">
              <div>
                <h6 class="main-content-label mb-1"> Danh sách lịch sản xuất </h6>
                <p class="text-muted card-sub-title"></p>
              </div>
              <div class="table-responsive">
                <table :data="jobsheets" class="table table-bordered custom-data-table" id="jobsheetTable">
                  <thead>
                    <tr>
                      <th>STT</th>
                      <th>Mã lịch SX</th>
                      <th>Ngày tạo</th>
                      <th>Người tạo</th>
                      <th>Mã sản phẩm</th>
                      <th style="max-width: 70px;">Tên sản phẩm</th>
                      <th>Loại sản xuất</th>
                      <th>Trạng thái</th>
                      <th>Ghi chú</th>
                      <th>Thao tác</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(item, index) in jobsheets" :key="item._id">
                      <td>{{index +1 }}</td>
                      <td>{{ item.jobsheet_code }}</td>
                      <td>{{ formatDate(item.jobsheet_create_date) }}</td>
                      <td>{{ item.user_id }}</td>
                      <td>{{ item.product_id }}</td>
                      <td>{{ item.product_name }}</td>
                      <td>  {{ item.product_type_code === 'N' || item.product_type_code === 'S' ? 'BTP' : 'TP' }}</td>
                 
                      <td>
                         <span class="badge bg-pill" :class="getStatus(item.jobsheet_status)">{{ jobsheetStatus(item.jobsheet_status) }}</span>
                      </td>
                      <td>
                       
                      </td>
                      <td>
                           <a v-if="item.product_type_code =='P' ||item.product_type_code =='R' "   @click="chooseJobsheet(item)"  data-bs-target="#viewProduct" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-primary  ms-1"><i class="fe fe-eye"></i></a>
                        <a v-if="item.product_type_code =='N' ||item.product_type_code =='S' " @click="chooseJobsheet(item)"  data-bs-target="#viewSemiProduct" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-primary  ms-1"><i class="fe fe-eye"></i></a>

                    <button class="btn btn-sm ripple btn-outline-primary ms-2" data-bs-toggle="dropdown" v-if="item.jobsheet_status ==0">
                        <i class="fe fe-more-horizontal"></i>
                    </button>
                    <div class="dropdown-menu">
                      <button v-if="item.jobsheet_status ==0" @click="editJobsheet(item)" class="dropdown-item text-body" data-bs-target="#update_jobsheet_modal" data-bs-toggle="modal">Cập nhật</button>
                      <button v-if="item.jobsheet_status ==0" class="dropdown-item" @click="cancelJobsheet(item._id)">Hủy jobsheet</button>
                      </div>
                    </td>
