              <label class="form-check-label fw-bold">Đóng màn hình</label>
            </div>
            <div>
             <button class="btn ripple btn-primary" type="submit">Lưu</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('addJobsheetModal','storeJobsheet','addErrors')" type="button"> Đóng </button>
            </div>
          </div>
      </form>
    </div>
