<script>
import { format, parse } from "date-fns";
import util from '@/components/uses/utils.js'
import api from '@/components/uses/api.js'
import ListProduct from '@/views/production-management/jobsheet/ViewProduct.vue'
import ListSemiProduct from '@/views/production-management/jobsheet/ViewSemiProduct.vue'

export default {
  components: {
    ListProduct,
    ListSemiProduct
  },
  data() {
    return {
		dontCloseModal: false,
		showSuccessMessage: false, 
		successMessage: '',
		jobsheets: [],
		proOder:null,
		proOders:[],
		proSeries:null,
		proTypes:null,
		ProTypes:[],
		storeJobsheet:[],
		addErrors:[],
		selectedJobsheet:[],
		updateErrors:[],
    ProSeries:[],

    jobsheetCode:null
    }
  }, 
  mounted () {
	  this.init()
	  this.getJobsheet();
  },
  methods: {
    chooseJobsheet(item) {
      this.jobsheetCode=item.jobsheet_code
    },
    formatDate(date) {
      if (!date) {
        return ''; 
      }
      const parsedDate = new Date(date);

      if (isNaN(parsedDate.getTime())) {
        return date; 
      }

    return format(parsedDate, 'MM/dd/yyyy');
    },
    init() {
      const currentDate = new Date();
      const storedUserLogin = JSON.parse(sessionStorage.getItem('userLogin'));
      this.storeJobsheet.jobsheet_create_date = format(currentDate, "dd/MM/yyyy");
      this.storeJobsheet.user_id = storedUserLogin.fullname;
    },
	loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#jobsheetTable')
        });
      },
      closeModal(modalRef, datakey, errorskey) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
        this[datakey] = {};
        this[errorskey] = {};
         this.init()
      },
	getJobsheet() {
		api.getData('jobsheet/listJobsheet')
		.then(Response => {
      this.ProSeries = Response.data.getproductSeries
			this.jobsheets =Response.data.data
			this.proOders = Response.data.getproductOrderNo
			this.ProTypes = Response.data.getproductType
			this.loadtable() 
		})
		.catch(Error => {
			console.log(Error)
		})
	}, 
	addJobsheet () {
		const jobsheetData = {
			jobsheet_create_date:this.storeJobsheet.jobsheet_create_date,
			jobsheet_finish_date:this.storeJobsheet.jobsheet_finish_date,
			production_order_code:this.storeJobsheet.production_order_code,
			user_id:this.storeJobsheet.user_id,
			product_id:this.storeJobsheet.product_id,
			product_name:this.storeJobsheet.product_name,
			BOM_code:this.storeJobsheet.BOM_code,
			product_unit:this.storeJobsheet.product_unit,
			product_quantity:this.storeJobsheet.product_quantity,
			product_type_code:this.storeJobsheet.product_type_code,
			product_series_code:this.storeJobsheet.product_series_code,
			specification:this.storeJobsheet.specification,
			jobsheet_note:this.storeJobsheet.jobsheet_note,
			production_style:this.storeJobsheet.production_style,
		};

		const jobsheetField = [
			{ name: 'jobsheet_create_date', label: 'Ngày tạo', maxLength: 50, nullable: false }, 
			{ name: 'jobsheet_finish_date', label: 'Dự kiến hoàn thành', maxLength: 50, nullable: false }, 
			{ name: 'production_order_code', label: 'Lý do', maxLength: 50, nullable: false }, 
			{ name: 'user_id', label: 'Người tạo', maxLength: 50, nullable: false }, 
			{ name: 'product_id', label: 'Mã sản phẩm', maxLength: 50, nullable: false }, 
			{ name: 'product_name', label: 'Tên sản phẩm', maxLength: 200, nullable: false }, 
			{ name: 'BOM_code', label: 'Mã BOM', maxLength: 50, nullable: false }, 
			{ name: 'product_unit', label: 'Đơn vị', maxLength: 50, nullable: false }, 
			{ name: 'product_quantity', label: 'Số lượng', maxLength: 50, nullable: false, type:'number' }, 
			{ name: 'product_type_code', label: 'Loại sản phẩm', maxLength: 50, nullable: false }, 
			{ name: 'product_series_code', label: 'Dòng sản phẩm', maxLength: 50, nullable: false }, 
			{ name: 'specification', label: 'Quy cách', maxLength: 50, nullable: false }, 
			{ name: 'jobsheet_note', label: 'Ghi chú', maxLength: 900, nullable: true }, 
			{ name: 'production_style', label: 'Loại sản xuất', maxLength: 50, nullable: false }, 
		]
		 this.addErrors = util.validateData(jobsheetData, jobsheetField);
		 if (Object.keys(this.addErrors).length === 0) { 
			api.postData('jobsheet/storeJobSheet',jobsheetData)
			.then(Response => {
				if (Response.data.error) {
				Response.data.error.details.forEach((error) => {
					const errorMessage = error.message;
					const errorField = error.path[0];
					this.addErrors[errorField] = [];
					this.addErrors[errorField] = errorMessage;
				});
              } else {
                if (!this.dontCloseModal) {
                  this.getJobsheet();
                  this.closeModal('addJobsheetModal', 'storeJobsheet', 'addErrors');
                  util.showSuccessAlert('Thêm thành công');
                } else {
                  this.getJobsheet();
                  this.storeJobsheet={};
                  this.showSuccessMessage = true;
                  this.successMessage = 'Thêm thành công'
                  setTimeout(() => {
                  this.showSuccessMessage = false; 
                }, 1000); 
                }
              }
			})
			.catch(Error => {
				console.log(Error)
			})
		 }
	},

	editJobsheet(item) {
    this.selectedJobsheet.id = item._id,
		this.selectedJobsheet.jobsheet_create_date = item.jobsheet_create_date,
    this.selectedJobsheet.jobsheet_finish_date = format(parse(item.jobsheet_finish_date, "yyyy-MM-dd'T'HH:mm:ss.SSSX", new Date()),'yyyy-MM-dd');
		this.selectedJobsheet.proOder = item.production_order_code,
		this.selectedJobsheet.user_id = item.user_id,
		this.selectedJobsheet.product_id = item.product_id,
		this.selectedJobsheet.product_name = item.product_name,
		this.selectedJobsheet.BOM_code = item.BOM_code,
		this.selectedJobsheet.product_unit = item.product_unit,
		this.selectedJobsheet.product_quantity = item.product_quantity,
		this.selectedJobsheet.product_type_code = item.product_type_code,
		this.selectedJobsheet.product_series_code = item.product_series_code,
		this.selectedJobsheet.specification = item.specification,
		this.selectedJobsheet.jobsheet_note = item.jobsheet_note,
		this.selectedJobsheet.production_style = item.production_style
	},

  updateJobsheet() {
    const updateData = {
      jobsheet_create_date:this.selectedJobsheet.jobsheet_create_date,
			jobsheet_finish_date:this.selectedJobsheet.jobsheet_finish_date,
			production_order_code:this.selectedJobsheet.proOder,
			user_id:this.selectedJobsheet.user_id,
			product_id:this.selectedJobsheet.product_id,
			product_name:this.selectedJobsheet.product_name,
			BOM_code:this.selectedJobsheet.BOM_code,
			product_unit:this.selectedJobsheet.product_unit,
			product_quantity:this.selectedJobsheet.product_quantity,
			product_type_code:this.selectedJobsheet.product_type_code,
			product_series_code:this.selectedJobsheet.product_series_code,
			specification:this.selectedJobsheet.specification,
			jobsheet_note:this.selectedJobsheet.jobsheet_note,
			production_style:this.selectedJobsheet.production_style,
    }

    const updateJobsheetField = [
      { name: 'jobsheet_create_date', label: 'Ngày tạo', maxLength: 50, nullable: false }, 
			{ name: 'jobsheet_finish_date', label: 'Dự kiến hoàn thành', maxLength: 50, nullable: false }, 
			{ name: 'production_order_code', label: 'Lý do', maxLength: 50, nullable: false }, 
			{ name: 'user_id', label: 'Người tạo', maxLength: 50, nullable: false }, 
			{ name: 'product_id', label: 'Mã sản phẩm', maxLength: 50, nullable: false }, 
			{ name: 'product_name', label: 'Tên sản phẩm', maxLength: 200, nullable: false }, 
			// { name: 'BOM_code', label: 'Mã BOM', maxLength: 50, nullable: false }, 
			{ name: 'product_unit', label: 'Đơn vị', maxLength: 50, nullable: false }, 
			{ name: 'product_quantity', label: 'Số lượng', maxLength: 50, nullable: false, type:'number' }, 
			{ name: 'product_type_code', label: 'Loại sản phẩm', maxLength: 50, nullable: false }, 
			{ name: 'product_series_code', label: 'Dòng sản phẩm', maxLength: 50, nullable: false }, 
			{ name: 'specification', label: 'Quy cách', maxLength: 50, nullable: false }, 
			{ name: 'jobsheet_note', label: 'Ghi chú', maxLength: 900, nullable: true }, 
			{ name: 'production_style', label: 'Loại sản xuất', maxLength: 50, nullable: false }, 
    ]
     this.updateErrors = util.validateData(updateData, updateJobsheetField); 
     console.log(Object.keys(this.updateErrors))
     if(Object.keys(this.updateErrors).length ===0) {
        api.putData(`jobsheet/updateJobSheet/${this.selectedJobsheet.id}`,updateData )
        .then(Response => {
           if (Response.data.error) {
              Response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.updateErrors[errorField] = [];
                this.updateErrors[errorField] = errorMessage;
              });
              } else {
                util.showSuccessAlert('Cập nhật thành công');
                this.closeModal('updateJobsheetModal', 'selectedJobsheet', 'updateErrors');
                this.getJobsheet();
              }
        })
        .catch(Error => {
          console.log(Error)
        })
     }
  }, 
  cancelJobsheet(id) {
        util.confirmDelete(() => {
          api.putData(`jobsheet/cancelJobSheet/${id}`)
          .then(response => {
            util.showSuccessAlert('Hủy thành công');
            this.getJobsheet();
          })
          .catch(error => {
                console.log(error)
            });
          })
      },

    getStatus(status) {
         switch(status) {
          case '0': return 'bg-primary-light';
          case '1': return 'bg-success-light';
          case '2': return 'bg-success-light';
          case '3': return 'bg-danger-light';
          default: return 'bg-danger-light';
        }
      },
    
      jobsheetStatus (statusCode) {
         switch(parseInt(statusCode)) {
          case 0: return "Mới tạo";
          case 1: return "Đang sản xuất";
          case 2: return "Hoàn thành";
          case 3: return "Đã hủy";
          default: return "Không rõ"
        }
      }
  }

};
</script>