<template>
<!-- Main Content-->
			<div class="main-content side-content pt-0">

				<div class="main-container container-fluid pt-5">
					<div class="inner-body">

						<!-- Page Header -->
						<div class="page-header">
							<div>
								<h2 class="main-content-title tx-24 mg-b-5">Tài Khoản</h2>
								<ol class="breadcrumb">
									<li class="breadcrumb-item"><a href="#">Trang chủ</a></li>
									<li class="breadcrumb-item active" aria-current="page">Tài khoản</li>
								</ol>
							</div>
						</div>
						<!-- End Page Header -->

						<div class="row square">
							<div class="col-lg-12 col-md-12">
								<div class="card custom-card">
									<div class="card-body">
										<div class="panel profile-cover ">
											<div class="profile-cover__img ">
												<div class="main-img-user avatar-xxl d-none d-sm-block mx-auto">
													<img alt="avatar" class="rounded-circle" :src="'http://192.168.48.31:8080/' + user.avatar">
												</div>
												<h3 class="h3 text-white">{{ user.fullname }}</h3>
											</div>
											<div class="profile-cover__action custom-profile-cover"></div>
                                        </div>
										<div class="profile-tab tab-menu-heading">
											<nav class="nav main-nav-line p-3 tabs-menu profile-nav-line bg-gray-100">
												<a class="nav-link  active" data-bs-toggle="tab" href="#about">Thông tin tài khoản</a>
												<a class="nav-link" data-bs-toggle="tab" href="#edit">Đổi mật khẩu</a>
											</nav>
										</div>
									</div>
								</div>
							</div>
						</div>
						<!-- Row -->
						<div class="row row-sm">
							<div class="col-lg-12 col-md-12">
								<div class="card custom-card main-content-body-profile">
									<div class="tab-content">
										<div class="main-content-body tab-pane p-4 border-top-0 active" id="about">
											<div class="card-body border ">
												<div class="mb-4 main-content-label">Tài khoản</div>
													<table class="table table-bordered">
													<tbody>
														<tr>
														<td >
															<div class="form-group">
															<label class="fw-bold">Email</label>
															<input class="form-control text-body" type="email" placeholder="Nhập email" :value="user.email" readonly/>
															</div>
															<div class="form-group">
															<label class="fw-bold">Ngày sinh</label>
															<input class="form-control text-body" type="text" :value="user.birthday" readonly />
															</div>
															<div class="form-group">
															<label class="fw-bold">Giới tính</label>
															<input class="form-control text-body" type="text" :value="user.gender ? 'Nam' : 'Nữ'" readonly />
															</div>
														</td>
														<td>
															
															<div class="form-group">
															<label class="fw-bold">Bộ phận</label>
															<input class="form-control text-body" type="text" :value="user.department" readonly />
															</div>
															<div class="form-group">
															<label class="fw-bold">Khu vực</label>
															<input class="form-control text-body" type="text" :value="user.region" readonly />
															</div>
															<div class="form-group">
															<label class="fw-bold">Chức vụ</label>
															<input class="form-control text-body" type="text" :value="user.position" readonly />
															</div>
														</td>
														</tr>
													</tbody>
													</table>
											</div>
										</div>
										<div class="main-content-body tab-pane p-4 border-top-0" id="edit">
											<div class="card-body border">
												<div class="mb-4 main-content-label">Đổi mật khẩu</div>
													<div class="form-group ">
														<div class="row row-sm">
															<div class="col-md-3">
																<label class="form-label">Mật khẩu hiện tại</label>
															</div>
															<div class="col-md-9">
																<input type="password" v-model="password.old_password" :class="{'is-invalid': changeErrors.old_password}" class="form-control text-body" placeholder="Mật khẩu hiện tại">
																  <div class="invalid-feedback" v-if="changeErrors.old_password">{{changeErrors.old_password}} </div>
															</div>
														</div>
													</div>
													<div class="form-group ">
														<div class="row row-sm">
															<div class="col-md-3">
																<label class="form-label">Mật khẩu mới</label>
															</div>
															<div class="col-md-9">
																<input type="password" v-model="password.new_password" :class="{'is-invalid': changeErrors.new_password}" class="form-control text-body" placeholder="Mật khẩu mới">
																  <div class="invalid-feedback" v-if="changeErrors.new_password">{{changeErrors.new_password}} </div>
															</div>
														</div>
													</div>
													<div class="form-group ">
														<div class="row row-sm">
															<div class="col-md-3">
																<label class="form-label">Nhập lại mật khẩu</label>
															</div>
															<div class="col-md-9">
																<input type="password" v-model="password.repeat_password" :class="{'is-invalid': changeErrors.repeat_password}"  class="form-control text-body" placeholder="Nhập lại mật khẩu">
																<div class="invalid-feedback" v-if="changeErrors.repeat_password">{{changeErrors.repeat_password}} </div>
															</div>
														</div>
													</div>
													<button class="btn btn-primary" @click="changePassword">Lưu</button>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
						<!-- End Row -->
					</div>
				</div>
			</div>
			<!-- End Main Content-->

</template>
<script>
import util from '@/components/uses/utils.js'
  import api from '@/components/uses/api.js'
export default {
	data() {
		return {
			user:[],
			password:[],
			changeErrors:[]
		}
	},
	mounted () {
	this.user = JSON.parse(sessionStorage.getItem('userLogin'));
  },

  methods: {
	changePassword () {
		const changeData = {
			old_password: this.password.old_password,
			new_password: this.password.new_password,
			repeat_password: this.password.repeat_password
		}
		const changeField = [
        { name: 'old_password', label: 'Mật khẩu hiện tại', maxLength: 50, nullable: false },
        { name: 'new_password', label: 'Mật khẩu mới', maxLength: 50,   nullable: false, type:'password'},
        { name: 'repeat_password',label:'Mật khẩu nhập lại', maxLength: 50, nullable: false }
      ];
	   this.changeErrors = util.validateData(changeData, changeField);

	   if(Object.keys(this.changeErrors).length >0) { return;}

	   if(changeData.new_password != changeData.repeat_password) 
	   {
			this.changeErrors.repeat_password ='Mật khẩu nhập lại không chính xác'; 
			return;
	   }

	   api.putData(`account/changePasswordUser/${this.user.userID}`,changeData)
	   .then(Response => {
		console.log(Response)
			console.log(Response.data.status)
			if(Response.data.status ==421) {
				this.changeErrors.old_password ='Mật khẩu không chính xác'; 
				return;
			}
			if(Response.data.status ==422) {
				this.changeErrors.repeat_password ='Mật khẩu nhập lại không chính xác'; 
				return;
			}
			util.showSuccessAlert('Đổi mật khẩu thành công')
			this.password ={}
	   })
	   .catch(Error => {
			console.log('Lỗi rồi ', Error)
	   })
	}
  }

}

</script>