<template>
  <!-- Main Content-->
  <div class="main-content side-content pt-0">
    <div class="main-container container-fluid pt-5">
      <div class="inner-body">
        <!-- Page Header -->
        <div class="page-header">
          <div>
            <h2 class="main-content-title tx-24 mg-b-5">Quản lý loại sản phẩm</h2>
            <ol class="breadcrumb">
              <li class="breadcrumb-item">
                <a href="#">Trang chủ</a>
              </li>
              <li class="breadcrumb-item active" aria-current="page"> loại sản phẩm </li>
            </ol>
          </div>
          <div class="d-flex">
            <div class="justify-content-center">
              <a data-bs-target="#add_product_type" data-bs-toggle="modal" class="btn btn-primary btn-rounded my-2 text-white">
                <i class="fe fe-plus"></i> Thêm mới </a>
            </div>
          </div>
        </div>
        <!-- End Page Header -->
        <!-- Row -->
        <div class="row row-sm">
          <div class="col-lg-12">
            <div class="card custom-card">
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">Danh sách loại sản phẩm</h6>
                  <p class="text-muted card-sub-title"></p>
                </div>
                <div class="table-responsive">
                  <table :data="product_types" class="table table-bordered" id="productTypeTable">
                    <thead>
                      <tr>
                        <th class="wd-20p">STT</th> 
                        <th class="wd-25p">Mã loại sản phẩm</th>
                        <th class="wd-25p">Mô tả</th>
                        <th class="wd-25p">Ghi chú</th>
                        <th class="wd-15p">Thao tác</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in product_types" :key="item.id">
                        <td>{{ index + 1 }}</td>
                        <td>{{ item.product_type_code }}</td>
                        <td>{{ item.product_type_name }}</td>
                        <td>{{ item.product_type_note }}</td>
                        <td>
                          <!-- <button @click="editProductType(item)" data-bs-target="#update_product_type" data-bs-toggle="modal" class="btn btn-sm ripple btn-main-primary text-white">Sửa</button>
                          <button @click="deleteProductType(item._id)" class="btn btn-sm ripple btn-danger ms-1"> Xóa</button> -->

                          <a @click="editProductType(item)" data-bs-target="#update_product_type" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-info ms-1"><i class="fe fe-edit-2"></i></a>
                          <a @click="deleteProductType(item._id)" class="btn btn-sm btn-outline-danger ms-1"> <i class="fe fe-trash"></i></a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- End Row -->
      </div>
    </div>
  </div>
  <!-- End Main Content-->
  <!-- Large Modal -->
  <div class="modal" id="add_product_type" ref="addProTypeModal">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Thêm mới loại sản phẩm</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="addProType">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Mã loại sản phẩm</label>
                      <input v-model="storeProType.product_type_code" class="form-control text-body" name="product_type_code" :class="{'is-invalid':addErrors.product_type_code}" type="text" placeholder="Nhập mã loại sản phẩm" />
                      <div class="invalid-feedback mt-2" v-if="addErrors.product_type_code">{{addErrors.product_type_code}}</div>
                    </div>
                  </td>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Tên loại sản phẩm</label>
                      <input v-model="storeProType.product_type_name" class="form-control text-body" name="product_type_name" :class="{'is-invalid':addErrors.product_type_name}" type="text" placeholder="Nhập tên loại sản phẩm" />
                      <div class="invalid-feedback" v-if="addErrors.product_type_name">{{addErrors.product_type_name}}</div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td colspan="2">
                    <div class="form-group">
                      <label class="fw-bold ">Ghi chú</label>
                      <textarea v-model="storeProType.product_type_note" class="form-control text-body" :class="{'is-invalid':addErrors.product_type_note}" rows="5" placeholder="Nhập ghi chú"></textarea>
                      <div class="invalid-feedback">{{addErrors.product_type_note}}</div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div v-if="showSuccessMessage" class="alert alert-success text-center">
            {{ successMessage }}
          </div>
          <div class="modal-footer d-flex justify-content-between">
            <div class="form-check">
              <input class="form-check-input" type="checkbox" id="check1" name="option1" value="something" v-model="dontCloseModal">
              <label class="form-check-label fw-bold">Không đóng màn hình</label>
            </div>
            <div>
              <button class="btn ripple btn-primary" type="submit">Lưu</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('addProTypeModal','storeProType','addErrors')" type="button"> Đóng </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->
  <!-- Large Modal -->
  <div class="modal" id="update_product_type" ref="updateProType">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Cập nhật loại sản phẩm</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="updateProType">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Mã loại sản phẩm</label>
                      <input v-model="selectedProType.product_type_code" :class="{'is-invalid': updateErrors.product_type_code}" class="form-control text-body" type="text" placeholder="Nhập mã loại sản phẩm" />
                      <div class="invalid-feedback" v-if="updateErrors.product_type_code">{{updateErrors.product_type_code}} </div>
                    </div>
                  </td>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Tên loại sản phẩm</label>
                      <input v-model="selectedProType.product_type_name" class="form-control text-body" :class="{'is-invalid': updateErrors.product_type_name}" type="text" placeholder="Nhập tên loại sản phẩm" />
                      <div class="invalid-feedback" v-if="updateErrors.product_type_name">{{updateErrors.product_type_name}} </div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td colspan="2">
                    <div class="form-group">
                      <label class="fw-bold ">Ghi chú</label>
                      <textarea v-model="selectedProType.product_type_note" :class="{'is-invalid':updateErrors.product_type_note}" class="form-control text-body" name="" rows="5" placeholder="Nhập ghi chú"></textarea>
                      <div class="invalid-feedback" v-if="updateErrors.product_type_note">{{updateErrors.product_type_note}} </div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="modal-footer">
            <button class="btn ripple btn-primary" type="submit">Lưu</button>
            <button class="btn ripple btn-danger" @click="closeModal('updateProType', 'selectedProType','updateErrors' )" type="button"> Đóng </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->
</template>
<script>
  import util from '@/components/uses/utils.js'
  import api from '@/components/uses/api.js'
  export default {
    data() {
      return {
        product_types: {
          product_type_code:'',
          product_type_name:'',
          product_type_note:''

        },
        storeProType:{
          product_type_code:'',
          product_type_name:'',
          product_type_note:''
        },
        selectedProType: [],
        dontCloseModal: false,
        addErrors: [],
        updateErrors: [],
        showSuccessMessage: false, 
        successMessage: '',
      };
    },
    mounted() {
      this.getproduct_types();
    },
    methods: {
      loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#productTypeTable')
        });
      },
      
      getproduct_types() {
        api.getData('product-type/listProductType')
        .then(response=> {
          this.product_types = response.data.data;
          this.loadtable();
        })
        .catch(error=> {
          util.errorRedirect()
        })
      },

      addProType() {
        const productTypeData = {
          product_type_code: this.storeProType.product_type_code,
          product_type_name: this.storeProType.product_type_name,
          product_type_note: this.storeProType.product_type_note ? this.storeProType.product_type_note : ''
        };
        const proTypeField = [
          { name: 'product_type_code', label: 'Mã loại sản phẩm', maxLength: 50, nullable: false }, 
          { name: 'product_type_name', label: 'Tên loại sản phẩm', maxLength: 50, nullable: false }, 
          { name: 'product_type_note', label: 'Ghi chú', maxLength: 50, nullable: true }
        ]
        this.addErrors = util.validateData(productTypeData, proTypeField);
        if (Object.keys(this.addErrors).length === 0) {
          api.postData('product-type/storeProductType',productTypeData)
          .then(response => {
             if (response.data.error) {
              response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.errors[errorField] = [];
                this.errors[errorField] = errorMessage;
              });
              } else {
                if (!this.dontCloseModal) {
                  this.getproduct_types();
                  this.closeModal('addProTypeModal', 'storeProType', 'errors');
                  util.showSuccessAlert('Thêm thành công');
                } else {
                  this.getproduct_types();
                  this.storeProType={};
                  this.showSuccessMessage = true;
                  this.successMessage = 'Thêm thành công'
                  setTimeout(() => {
                  this.showSuccessMessage = false; 
                }, 1000); 
                }
              }
          })
            .catch(error => {
            console.error('Lỗi khi thêm loại sản phẩm:', error);
        });
        }
      },

      deleteProductType(id) {
        util.confirmDelete(() => {
          api.deleteData(`product-type/deleteProductType/?id=${id}`)
          .then(response => {
            util.showSuccessAlert('Xóa thành công');
            this.getproduct_types();
          })
          .catch(error => {
          console.error('Lỗi khi xóa loại sản phẩm:', error);
          });
          })
      },

      closeModal(modalRef, datakey, errorskey) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
        this[datakey] = {};
        this[errorskey] = {};
      },

      editProductType(item) {
        this.selectedProType.id = item._id;
        this.selectedProType.product_type_code = item.product_type_code;
        this.selectedProType.product_type_name = item.product_type_name;
        this.selectedProType.product_type_note = item.product_type_note;
      },
      updateProType() {
        const dataUpdate = {
          product_type_code: this.selectedProType.product_type_code,
          product_type_name: this.selectedProType.product_type_name,
          product_type_note: this.selectedProType.product_type_note
        }
        const updateProTypeField = [
          { name: 'product_type_code', label: 'Mã loại sản phẩm', maxLength: 50, nullable: false}, 
          { name: 'product_type_name', label: 'Tên loại sản phẩm', maxLength: 50, nullable: false}, 
          { name: 'product_type_note', label: 'Ghi chú', maxLength: 50, nullable: true}
        ]
        this.updateErrors = util.validateData(dataUpdate, updateProTypeField); 
        if (Object.keys(this.updateErrors).length === 0) {
            api.putData(`product-type/updateProductType/${this.selectedProType.id}` , dataUpdate)
            .then(response => {
              if (response.data.error) {
              response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.updateErrors[errorField] = [];
                this.updateErrors[errorField] = errorMessage;
              });
              } else {
                util.showSuccessAlert('Cập nhật thành công');
                this.closeModal('updateProType', 'selectedProType', 'updateErrors');
                this.getproduct_types();
              }
            })
            .catch(error => {
              console.error('Lỗi khi cập nhật loại sản phẩm:', error);
            });
        }
      },
    }
  }
</script>
