<template>
  <!-- Main Content-->
  <div class="main-content side-content pt-0">
    <div class="main-container container-fluid pt-5">
      <div class="inner-body">
        <!-- Page Header -->
        <div class="page-header">
          <div>
            <h2 class="main-content-title tx-24 mg-b-5">Quản lý khu vực</h2>
            <ol class="breadcrumb">
              <li class="breadcrumb-item">
                <a href="#">Trang chủ</a>
              </li>
              <li class="breadcrumb-item active" aria-current="page"> Khu vực </li>
            </ol>
          </div>
          <div class="d-flex">
            <div class="justify-content-center">
              <a data-bs-target="#add_region" data-bs-toggle="modal" class="btn btn-primary btn-rounded my-2 text-white">
                <i class="fe fe-plus"></i> Thêm mới </a>
            </div>
          </div>
        </div>
        <!-- End Page Header -->
        <!-- Row -->
        <div class="row row-sm">
          <div class="col-lg-12">
            <div class="card custom-card">
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">Danh sách khu vực</h6>
                  <p class="text-muted card-sub-title"></p>
                </div>
                <div class="table-responsive">
                  <table :data="regions" class="table table-bordered" id="regionTable">
                    <thead>
                      <tr>
                        <th>STT</th>
                        <th>Mã khu vực</th>
                        <th>Tên khu vực</th>
                        <th>Ghi chú</th>
                        <th>Thao tác</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in regions" :key="item.id">
                        <td>{{ index + 1 }}</td>
                        <td>{{ item.region_code }}</td>
                        <td>{{ item.region_name }}</td>
                        <td>{{ item.region_note }}</td>
                        <td>
                          <a @click="editRegion(item)"  data-bs-target="#update_region" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-info ms-1"><i class="fe fe-edit-2"></i></a>
                          <a @click="deleteRegion(item._id)" class="btn btn-sm btn-outline-danger ms-1"> <i class="fe fe-trash"></i></a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- End Row -->
      </div>
    </div>
  </div>
  <!-- End Main Content-->
  <!-- Large Modal -->
  <div class="modal" id="add_region" ref="addRegionModal">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Thêm mới khu vực</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="addRegion">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Mã khu vực</label>
                      <input v-model="storeRegion.region_code" class="form-control text-body" name="region_code" :class="{'is-invalid':addErrors.region_code}" type="text" placeholder="Nhập mã khu vực" />
                      <div class="invalid-feedback mt-2" v-if="addErrors.region_code">{{addErrors.region_code}}</div>
                    </div>
                  </td>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Tên khu vực</label>
                      <input v-model="storeRegion.region_name" class="form-control text-body" name="region_name" :class="{'is-invalid':addErrors.region_name}" type="text" placeholder="Nhập tên khu vực" />
                      <div class="invalid-feedback" v-if="addErrors.region_name">{{addErrors.region_name}}</div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td colspan="2">
                    <div class="form-group">
                      <label class="fw-bold ">Ghi chú</label>
                      <textarea v-model="storeRegion.region_note" class="form-control text-body" :class="{'is-invalid':addErrors.region_note}" rows="5" placeholder="Nhập ghi chú"></textarea>
                      <div class="invalid-feedback">{{addErrors.region_note}}</div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div v-if="showSuccessMessage" class="alert alert-success text-center">
            {{ successMessage }}
          </div>
          <div class="modal-footer d-flex justify-content-between">
            <div class="form-check">
              <input class="form-check-input" type="checkbox" id="check1" name="option1" value="something" v-model="dontCloseModal">
              <label class="form-check-label fw-bold">Không đóng màn hình</label>
            </div>
            <div>
              <button class="btn ripple btn-primary" type="submit">Lưu</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('addRegionModal','storeRegion','addErrors')" type="button"> Đóng </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->
  <!-- Large Modal -->
  <div class="modal" id="update_region" ref="updateRegionModal">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Cập nhật khu vực</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="updateRegion">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Mã khu vực</label>
                      <input v-model="selectedRegion.region_code" :class="{'is-invalid': updateErrors.region_code}" class="form-control text-body" type="text" placeholder="Nhập mã khu vực" />
                      <div class="invalid-feedback" v-if="updateErrors.region_code">{{updateErrors.region_code}} </div>
                    </div>
                  </td>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Tên khu vực</label>
                      <input v-model="selectedRegion.region_name" class="form-control text-body" :class="{'is-invalid': updateErrors.region_name}" type="text" placeholder="Nhập tên khu vực" />
                      <div class="invalid-feedback" v-if="updateErrors.region_name">{{updateErrors.region_name}} </div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td colspan="2">
                    <div class="form-group">
                      <label class="fw-bold ">Ghi chú</label>
                      <textarea v-model="selectedRegion.region_note" :class="{'is-invalid':updateErrors.region_note}" class="form-control text-body" name="" rows="5" placeholder="Nhập ghi chú"></textarea>
                      <div class="invalid-feedback" v-if="updateErrors.region_note">{{updateErrors.region_note}} </div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="modal-footer">
            <button class="btn ripple btn-primary" type="submit">Lưu</button>
            <button class="btn ripple btn-danger" @click="closeModal('updateRegionModal', 'selectedRegion','updateErrors' )" type="button"> Đóng </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->
</template>

<script>
  import util from '@/components/uses/utils.js'
  import api from '@/components/uses/api.js'
  export default {
    name: 'ListRegion',
   
    data() {
      return {
        regions: [],
        storeRegion:{
          region_code:'',
          region_name:'',
          region_note:''
        },
        selectedRegion: [],
        dontCloseModal: false,
        addErrors: [],
        updateErrors: [],
        showSuccessMessage: false, 
        successMessage: '',
      };
    },
    mounted() {
      this.getRegions();
    },
    methods: {
      loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#regionTable')
        });
      },
      getRegions() {
        api.getData('region/listRegion')
        .then(response=> {
          this.regions = response.data.data;
          this.loadtable();
        })
        .catch(error=> {
          util.errorRedirect()
        })
      },

      addRegion() {
        const regionData = {
          region_code: this.storeRegion.region_code,
          region_name: this.storeRegion.region_name,
          region_note: this.storeRegion.region_note ? this.storeRegion.region_note : ''
        };
        const regionField = [
          { name: 'region_code', label: 'Mã khu vực', maxLength: 50, nullable: false }, 
          { name: 'region_name', label: 'Tên khu vực', maxLength: 50, nullable: false }, 
          { name: 'region_note', label: 'Ghi chú', maxLength: 50, nullable: true }
        ]
        this.addErrors = util.validateData(regionData, regionField);
        if (Object.keys(this.addErrors).length === 0) {
          api.postData('region/storeRegion',regionData)
          .then(response => {
             if (response.data.error) {
              response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.errors[errorField] = [];
                this.errors[errorField] = errorMessage;
              });
              } else {
                if (!this.dontCloseModal) {
                   this.getRegions();
                  this.closeModal('addRegionModal', 'storeRegion', 'errors');
                  util.showSuccessAlert('Thêm thành công');
                } else {
                  this.getRegions();
                  this.storeRegion={};
                  this.showSuccessMessage = true;
                  this.successMessage = 'Thêm thành công'
                  setTimeout(() => {
                  this.showSuccessMessage = false; 
                }, 1000); 
                }
              }
          })
            .catch(error => {
            console.error('Lỗi khi thêm khu vực:', error);
        });
        }
      },
      deleteRegion(id) {
        util.confirmDelete(() => {
          api.deleteData(`region/deleteRegion/?id=${id}`)
          .then(response => {
            util.showSuccessAlert('Xóa thành công');
            this.getRegions();
          })
          .catch(error => {
          console.error('Lỗi khi xóa khu vực:', error);
          });
          })
      },

      closeModal(modalRef, datakey, errorskey) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
        this[datakey] = {};
        this[errorskey] = {};
      },

      editRegion(item) {
        this.selectedRegion.id = item._id;
        this.selectedRegion.region_code = item.region_code;
        this.selectedRegion.region_name = item.region_name;
        this.selectedRegion.region_note = item.region_note;
      },
      updateRegion() {
        const dataUpdate = {
          region_code: this.selectedRegion.region_code,
          region_name: this.selectedRegion.region_name,
          region_note: this.selectedRegion.region_note
        }
        const updateRegionField = [
          { name: 'region_code', label: 'Mã khu vực', maxLength: 50, nullable: false}, 
          { name: 'region_name', label: 'Tên khu vực', maxLength: 50, nullable: false}, 
          { name: 'region_note', label: 'Ghi chú', maxLength: 50, nullable: true}
        ]
        this.updateErrors = util.validateData(dataUpdate, updateRegionField); 
        if (Object.keys(this.updateErrors).length === 0) {
            api.putData(`region/updateRegion/${this.selectedRegion.id}` , dataUpdate)
            .then(response => {
              if (response.data.error) {
              response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.updateErrors[errorField] = [];
                this.updateErrors[errorField] = errorMessage;
              });
              } else {
                util.showSuccessAlert('Cập nhật thành công');
                this.closeModal('updateRegionModal', 'selectedRegion', 'updateErrors');
                this.getRegions();
              }
            })
            .catch(error => {
              console.error('Lỗi khi cập nhật khu vực:', error);
            });
        }
      },
    }
  }
</script>
