									<li class="breadcrumb-item"><a href="jobsheet.html">Lịch sản xuất</a></li>
									<li class="breadcrumb-item active" aria-current="page">Chi tiết lịch sản xuất</li>
								</ol>
							</div>
							<div class="d-flex">
								<div class="justify-content-center">
									<router-link to="/jobsheets" class="btn btn-white btn-rounded my-2 btn-icon-text" :disabled="selectedRows.length === 0">
									  <i class="fe fe-corner-down-left"></i> Trở lại
									</router-link>
