														</div>
													</td>
													<td colspan="2">
														<div class="form-group">
														 <p class="mg-b-10 fw-bold">Tên sản phẩm</p>
														<span>{{jobsheets.product_name }}</span>
														</div>
													</td>
												</tr>
												<tr>
														
													<td>
														<div class="form-group">
														 <p class="mg-b-10 fw-bold">Số lượng</p>
														<span>{{jobsheets.product_quantity }}</span>
														</div>
													</td>
													<td>
														<div class="form-group">
														 <p class="mg-b-10 fw-bold">Đơn vị</p>
														<span>{{jobsheets.product_unit }}</span>
														</div>
													</td>
													<td>
														<div class="form-group">
														 <p class="mg-b-10 fw-bold">Loại sản xuất</p>
														<span>{{jobsheets.product_type_code }}</span>
														</div>
													</td>
													<td class="align-top"> 
														<div class="form-group">
														 <p class="mg-b-10 fw-bold">Mã BOM</p>
														<span>{{jobsheets.BOM_code }}</span>
														</div>
													</td>
													
													<td>
														<div class="form-group">
														 <p class="mg-b-10 fw-bold">Quy cách</p>
														<span>{{jobsheets.specification }}</span>
														</div>
													</td>
													<td>
														<div class="form-group">
														 <p class="mg-b-10 fw-bold">Ghi chú</p>
														<span>{{jobsheets.jobsheet_note }}</span>
														</div>
													</td>
												</tr>
											</tbody>
											</table>
										</div>
									</div>
								</div>
							</div>
						</div>
						<!-- End Row -->

						<!-- Page Header -->
						<div class="page-header">
							<div class="d-flex">
								<div class="justify-content-center">
									<button @click="exportMaterial" class="btn btn-primary btn-rounded btn-icon-text my-2 me-2" :disabled="!checkAction(0)" >
