									</button>
									<button @click="confirmExportMaterial" class="btn btn-primary btn-rounded btn-icon-text my-2 me-2" :disabled="!checkAction(1)">
										<i class="fe fe-download me-2"></i> Xuất kho
									</button>
										<button @click="weldingRequest" class="btn btn-primary btn-rounded btn-icon-text my-2 me-2" :disabled="!checkAction(2)">
										<i class="fe fe-download me-2"></i>Yêu cầu sản xuất
									</button>
										<button @click="checkExist" class="btn btn-primary btn-rounded btn-icon-text my-2 me-2" :disabled="!checkAction(5)" >
										<i class="fe fe-download me-2"></i> Yêu cầu kiểm tra
									</button>
									<button @click="warehoustRequest" class="btn btn-primary btn-rounded btn-icon-text my-2 me-2" :disabled="!checkAction(7)" >
