											</p>
										</div>
										<div class="table-responsive">
											<table class="table table-bordered" id="semiProductTB">
												<thead>
													<tr>
														<th>Chọn</th>
														<th>STT</th>
														<th>Số lot</th>
														 <th>Số SIM/E-SIM</th>
                                                        <th>Trạng thái</th>
														<th>Kết quả</th>
                                                        <th>Ghi chú</th>
													</tr>
												</thead>
												<tbody>
													<tr v-for="(item, index) in semiProducts" :key="item._id">
														 <td>
															<input type="checkbox" v-model="selectedRows" @change="checkStatus()" :value="item" :disabled="item.semi_product_status == 9 ||item.semi_product_status == 10" />
														</td>
														<td>{{ index +1 }}</td>
														<td>{{ item.semi_product_lot }}	</td>
														<td>
															<div class="tooltip1" @click="viewDetail(item)" data-bs-target="#semi_product_info" data-bs-toggle="modal">
																{{ item.CategoriesSimInfo[0] ? item.CategoriesSimInfo[0].serial_sim : ''}}
															</div>
															</td>
                                                        <td>
															 <span class="badge bg-pill" :class="getStatus(item.semi_product_status)">{{ semiProductStatus(item.semi_product_status) }}</span>
														</td>
														<td>
															 <span v-if="item.semi_product_result" class="badge bg-pill" :class="getResult(item.semi_product_result)">{{ ResultShow(item.semi_product_result) }}</span>
														</td>
                                                        <td>{{ item.semi_product_note }}</td>
													</tr>
												</tbody>
											</table>
										</div>
									</div>
								</div>
							</div>
						</div>
						<!-- End Row -->
					</div>
				</div>
			</div>
			<!-- End Main Content-->

			<!-- Extra Large Modal -->
			<div class="modal fade" id="quality_control_modal" ref="QualityControlModal">
			<div class="modal-dialog modal-xl" role="document">
				<div class="modal-content modal-content-demo">
				<div class="modal-header">
					<h6 class="modal-title">Phiếu yêu cầu kiểm tra</h6>
					<button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
				</div>
					<div class="modal-body">
					<table class="table">
						<tbody>
						<tr>
							<td class="align-top">
							<div class="form-group">
								<p class="mg-b-10 fw-bold">Người tạo</p>
								<input type="text" class="form-control text-black" v-model="QC.user_id" readonly>
								<div class="invalid-feedback mt-2" ></div>
							</div>
							</td>
							<td class="align-top">
							<div class="form-group">
								<p class="mg-b-10 fw-bold">Ngày tạo</p>
								<input type="text" class="form-control text-black" v-model="QC.quality_control_create_date" readonly>
							</div>
							</td>
							<td class="align-top">
							<div class="form-group">
								<p class="mg-b-10 fw-bold">Loại kiểm tra</p>
								<select class="form-control form-select text-body" v-model="QC.type_quality_control">
									<option value="T">Test</option>
									<option value="R">Retest</option>
								</select>
							</div>
							</td>
						</tr>
						<tr>
							<td class="align-top">
							<div class="form-group">
								<p class="mg-b-10 fw-bold">Bộ phận yêu cầu</p>
								<select class="form-control form-select text-body" v-model="QC.deparment">
									<option v-for="(item, index) in departments" :key="item.department_code" :value="item.department_code">
										{{item.department_name }}
									</option>
								</select>
							</div>
							</td>
							<td class="align-top" >
							<div class="form-group">
								<p class="mg-b-10 fw-bold">Loại sản phẩm</p>
								<select class="form-control form-select text-body" v-model="QC.type_product_to_check">
									<option value="P">Thành phẩm</option>
									<option value="S">Bán thành phẩm</option>
									<option value="M">Nguyên vật liệu</option>
								</select>
							</div>
							</td>
							<td class="align-top">
							<div class="form-group">
								<p class="mg-b-10 fw-bold">Nhóm sản phẩm</p>
								<select class="form-control form-select text-body" v-model="QC.type_product_group">
									<option v-for="(item, index) in productGroups" :key="item.product_group_code" :value="item.product_group_code">
										({{item.product_group_code }}) {{item.product_group_name }}
									</option>
								</select>
							</div>
							</td>
						</tr>
						<tr>
							<td class="align-top" colspan="2">
							<div class="form-group">
								<p class="mg-b-10 fw-bold">Quy cách Test</p>
								<textarea  class="form-control text-black" v-model="QC.quality_control_specification" rows="5" placeholder="Nhập quy cách test"></textarea>
								<div class="invalid-feedback mt-2"></div>
							</div>
							</td>
							<td class="align-top" colspan="2">
							<div class="form-group">
								<p class="mg-b-10 fw-bold">Ghi chú</p>
								<textarea  class="form-control text-black" v-model="QC.quality_control_note" rows="5" placeholder="Nhập ghi chú"></textarea>
								<div class="invalid-feedback mt-2"></div>
							</div>
							</td>
						</tr>
						</tbody>
					</table>
					</div>
					<div class="modal-footer">
						<div>
						<button class="btn ripple btn-primary" @click="qualityCheck">Lưu</button>
						<button class="btn ripple btn-danger ms-2" @click="closeModal('QualityControlModal','QC','updateErrors')" type="button"> Đóng </button>
						</div>
					</div>
				</div>
			</div>
			</div>
			<!--End Extra Large Modal -->

     <!-- Extra Large Modal -->
      <div class="modal fade" id="semi_product_info" ref="semiProductInfo">
        <div class="modal-dialog modal-xl" role="document">
          <div class="modal-content modal-content-demo">
            <div class="modal-header">
              <h6 class="modal-title">Thông tin chi tiết</h6>
              <button
                aria-label="Close"
                class="btn-close"
                data-bs-dismiss="modal"
                type="button"
              ></button>
            </div>
            <div class="modal-body">
              <table class="table"> 
                <tbody>
                  <tr>
					  <td>
                      <div class="form-group">
                        <p class="mg-b-10 fw-bold">Mã LSX</p>
                        <span>{{ detailInfo.jobsheet_code }}</span>
                      </div>
                    </td>
                    <td>
                      <div class="form-group">
                       <p class="mg-b-10 fw-bold">Mã sản phẩm</p>
                        <span>{{ detailInfo.semi_product_code }}</span>
                      </div>
                    </td>
                    <td>
                      <div class="form-group">
                        <p class="mg-b-10 fw-bold">Tên sản phẩm</p>
                         <span>{{ detailInfo.semi_product_name }}</span>
                      </div>
                    </td>
                    <td >
                      <div class="form-group">
                        <p class="mg-b-10 fw-bold">Số Lot Board</p>
                         <span>{{ detailInfo.semi_product_lot }}</span>
                      </div>
                    </td>
					 <td >
                      <div class="form-group">
                        <p class="mg-b-10 fw-bold">Trạng Thái</p>
						  <span class="badge bg-pill" :class="getStatus(detailInfo.semi_product_status)">{{ semiProductStatus(detailInfo.semi_product_status) }}</span>
                      </div>
                    </td>
                  </tr>

                    <tr>
                    <td>
                      <div class="form-group">
                       <p class="mg-b-10 fw-bold">Nhân viên sản xuất</p>
                        <span>{{ detailInfo.semi_product_assembler }}</span>
                      </div>
                    </td>
                     <td>
                      <div class="form-group">
                        <p class="mg-b-10 fw-bold">Ngày hoàn thành</p>
                            <span>{{ detailInfo.semi_product_assembly_date }}</span>
                      </div>
                    </td>
                    <td >
                      <div class="form-group">
                        <p class="mg-b-10 fw-bold">Kết quả kiểm tra</p>
						  <span class="badge bg-pill" :class="getResult(detailInfo.semi_product_result)">{{ ResultShow(detailInfo.semi_product_result) }}</span>
                      </div>
                    </td>
                    <td>
                      <div class="form-group">
                        <p class="mg-b-10 fw-bold">Ngày kiểm tra</p>
                          <span>{{ detailInfo.semi_product_test_date }}</span>
                      </div>
                    </td>
                    <td>
                      <div class="form-group">
                        <p class="mg-b-10 fw-bold">Số phiếu/Người kiểm tra</p>
                        <span>{{ detailInfo.semi_product_tester }}</span>
                      </div>
                    </td>
                  </tr>

				   <tr>
                    <td>
                      <div class="form-group">
                       <p class="mg-b-10 fw-bold">Số SIM/E-SIM</p>
                        <span>{{ detailInfo.CategoriesSimInfo ? detailInfo.CategoriesSimInfo[0].serial_sim : '' }}</span>
                      </div>
                    </td>
                     <td>
                      <div class="form-group">
                        <p class="mg-b-10 fw-bold">Gói Cước</p>
                            <span>{{detailInfo.CategoriesSimInfo ? detailInfo.CategoriesSimInfo[0].SimpackageInfo[0].sim_package_description :'' }}</span>
                      </div>
                    </td>
                    <td>
                      <div class="form-group">
                        <p class="mg-b-10 fw-bold">Ngày kích hoạt</p>
                         <span>{{ detailInfo.CategoriesSimInfo ? detailInfo.CategoriesSimInfo[0].activation_date :'' }}</span>
                      </div>
                    </td>
                    <td>
                      <div class="form-group">
                        <p class="mg-b-10 fw-bold">Ngày hết hạn</p>
                          <span>{{detailInfo.CategoriesSimInfo ? detailInfo.CategoriesSimInfo[0].expiration_date :'' }}</span>
                      </div>
                    </td>
                    <td>
                      <div class="form-group">
                        <p class="mg-b-10 fw-bold">Loại</p>
                        <span :class="detailInfo.CategoriesSimInfo && detailInfo.CategoriesSimInfo[0].sim_type === 'E-SIM' ? 'tag tag-success tag-pill' : 'tag tag-warning tag-pill'">
							{{detailInfo.CategoriesSimInfo ? detailInfo.CategoriesSimInfo[0].sim_type :'' }}</span>
					
                      </div>
                    </td>
                  </tr>
                </tbody>

              </table>
               
            </div>
            
            <div class="modal-footer">
              <button class="btn ripple btn-danger ms-2" @click="closeModal('semiProductInfo')" type="button"> Đóng </button>
          </div>
          </div>
        </div>
      </div>
      <!--End Extra Large Modal -->

</template>

<script>
import { format} from "date-fns";
import api from '@/components/uses/api.js'
import util from '@/components/uses/utils.js'
export default {
data() {
	return {
		selectedProductCode:[],
		selectedRows: [],
		semiProducts:[],
		jobsheetID:null,
		QC:[],
		updateErrors:[],
		productGroups:[],
		departments:[],
		isQC:null,
		jobsheets:[],
		isTableExpanded: true,
		detailInfo:[]
	}
},
mounted() {
	this.jobsheetID = this.$route.params.id;
	this.getSemiProducts();
},
methods: {
	toggleTable() {
      this.isTableExpanded = !this.isTableExpanded; 
    },
	loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#semiProductTB')
        });
      },
	getSemiProducts() {
		api.getData (`jobsheet/showDetailJobSheet/${this.jobsheetID}`)
		.then(Response => {
			this.jobsheets=Response.data.data[0]
			this.semiProducts=Response.data.data[0].dataSemiProduct
			this.productGroups = Response.data.getProductGroup
			this.departments = Response.data.getDepartment
			this.isQC=Response.data._isCheckQuantityControl
			this.loadtable()
		})
		.catch(Error => {
			console.log(Error)
		})
	},
	 getStatus(status) {
         switch(status) {
          case '0': return 'bg-primary-light';
          case '1': return 'bg-success-light';
          case '2': return 'bg-success-light';
          case '3': return 'bg-success-light';
		  case '4': return 'bg-success-light';
		  case '5': return 'bg-success-light';
		  case '6': return 'bg-success-light';
		  case '7': return 'bg-success-light';
		  case '8': return 'bg-success-light';
		  case '9': return 'bg-success-light';
		  case '10': return 'bg-danger-light';
          default: return 'bg-danger-light';
        }
      },
    
      semiProductStatus (statusCode) {
         switch(parseInt(statusCode)) {
          case 0: return "Mới tạo";
          case 1: return "Đã yêu cầu xuất kho";
          case 2: return "Đã xuất kho";
          case 3: return "Đã yêu cầu sản xuất";
		  case 4: return "Đang sản xuất";
		  case 5: return "Hoàn thành";
		  case 6: return "Đã gửi yêu cầu kiểm tra";
		  case 7: return "Đã kiểm tra";
		  case 8: return "Đã gửi yêu cầu nhập kho";
		  case 9: return "Đã nhập kho";
		  case 10: return "Đã hủy";
          default: return "Không rõ"
        }
      },

	  	closeModal(modalRef, datakey, errorskey) {
			const modal = this.$refs[modalRef];
			$(modal).modal('hide');
			this[datakey] = {};
			this[errorskey] = {};
			},

	   exportMaterial() {
		 const updateData ={
			jobsheetCode :this.jobsheetID,
			arrayProductID : this.selectedProductCode
		 }
		 util.confirmDelete(() => {
            api.putData(`jobsheet/OrderExportMaterialsJobSheet`, updateData) 
			.then(Response => {
				if(Response.data.status == 200) {
					util.showSuccessAlert('Đã yêu cầu xuất kho')
					this.getSemiProducts()
					this.selectedRows.splice(0, this.selectedRows.length);
				} 
			})
			.catch(Error => {
				console.log(Error)
			})
          }, 'Bạn chắc chắn muốn yêu cầu xuất kho?')
	  }, 

	  confirmExportMaterial() {
		 const updateData ={
			jobsheetCode :this.jobsheetID,
			arrayProductID : this.selectedProductCode
		 }
		 util.confirmDelete(() => {
            api.putData(`jobsheet/ExportMaterialsJobSheet`, updateData) 
			.then(Response => {
				if(Response.data.status == 200) {
					util.showSuccessAlert('Đã xuất kho')
					this.getSemiProducts()
					this.selectedRows.splice(0, this.selectedRows.length);
				} 
			})
			.catch(Error => {
				console.log(Error)
			})
          }, 'Bạn chắc chắn muốn xuất kho?')
	  }, 

	  weldingRequest() {
		 const updateData ={
			jobsheetCode :this.jobsheetID,
			arrayProductID : this.selectedProductCode
		 }
		 util.confirmDelete(() => {
            api.putData(`jobsheet/OrderProductJobSheet`, updateData) 
			.then(Response => {
				if(Response.data.status == 200) {
					util.showSuccessAlert('Đã gửi yêu cầu sản xuất')
					this.getSemiProducts()
					this.selectedRows.splice(0, this.selectedRows.length);
				} 
			})
			.catch(Error => {
				console.log(Error)
			})
          }, 'Bạn chắc chắn muốn yêu cầu sản xuất?')
	  },

		warehoustRequest() {
			const updateData ={
				jobsheetCode :this.jobsheetID,
				arrayProductID : this.selectedProductCode
			}
			util.confirmDelete(() => {
				api.putData(`jobsheet/OrderStoreJobSheet`, updateData) 
				.then(Response => {
					if(Response.data.status == 200) {
						util.showSuccessAlert('Đã gửi yêu cầu nhập kho')
						this.getSemiProducts()
						this.selectedRows.splice(0, this.selectedRows.length);
					} 
				})
				.catch(Error => {
					console.log(Error)
				})
			}, 'Bạn chắc chắn muốn gửi yêu cầu nhập kho?')
		},

		confirmWarehouse() {
			const updateData ={
				jobsheetCode :this.jobsheetID,
				arrayProductID : this.selectedProductCode
			}
			util.confirmDelete(() => {
				api.putData(`jobsheet/StoreWarehouseJobSheet`, updateData) 
				.then(Response => {
					if(Response.data.status == 200) {
						util.showSuccessAlert('Đã nhập kho')
						this.getSemiProducts()
						this.selectedRows.splice(0, this.selectedRows.length);
					} 
				})
				.catch(Error => {
					console.log(Error)
				})
			}, 'Nhập kho sản phẩm?')
		},

		checkExist() {
			if(this.isQC == 0) {
				const modal = this.$refs.QualityControlModal;
				$(modal).modal('show'); 
				const user = JSON.parse(sessionStorage.getItem('userLogin'));
				this.QC.user_id = user.fullname,
				this.QC.quality_control_create_date = format(new Date(), "dd/MM/yyyy")
			} else {
				this.qualityCheck()
			}
		},
	  
	qualityCheck() {
				const updateData ={
				jobsheetCode :this.jobsheetID,
				arrayProductID : this.selectedProductCode, 
				fullname:this.QC.user_id,
				type_quality_control:this.QC.type_quality_control,
				type_product_to_check:this.QC.type_product_to_check,
				department:this.QC.deparment,
				type_product_group:this.QC.type_product_group,
				quality_control_specification:this.QC.quality_control_specification,
				quality_control_note:this.QC.quality_control_note
				}
				api.putData(`jobsheet/OrderQCJobSheet`, updateData) 
					.then(Response => {
						if(Response.data.status == 200) {
							util.showSuccessAlert('Đã gửi yêu cầu kiểm tra')
							this.getSemiProducts()
							this.closeModal('QualityControlModal','QC','updateErrors')
							this.selectedRows.splice(0, this.selectedRows.length);
						} 
					})
					.catch(Error => {
						console.log(Error)
					})
	  },

	  	getResult(result) {
         switch(result) {
          case '0': return 'bg-danger-light';
          case '1': return 'bg-success-light';
          default: return 'bg-danger-light';
        }
      },
    
      ResultShow (result) {
         switch(parseInt(result)) {
          case 0: return "FAIL";
          case 1: return "PASS";
          default: return "Không rõ"
        }
      },
	
	  checkStatus() {
		this.selectedProductCode = []
		for (let i = 0; i < this.selectedRows.length; i++) {
			this.selectedProductCode.push(this.selectedRows[i].semi_product_lot);
		}
	},

	checkAction(Status) {
		if(this.selectedRows.length <1) {
			return false
		} 
		for(let j =0; j<this.selectedRows.length; j++) {
			if(this.selectedRows[0].semi_product_status != this.selectedRows[j].semi_product_status){
				return false 
			}
			else {
				if(this.selectedRows[j].semi_product_status != Status) {
					return false
				}

				if(this.selectedRows[j].semi_product_status == 7 && this.selectedRows[j].semi_product_result != 1) {
					return false
				}
			}
		}
		return true
	},

	viewDetail(item){
		this.detailInfo=item
	}
}
}
</script>


