<template>
  <!-- Main Content-->
<div class="main-content side-content pt-0">
  <div class="main-container container-fluid pt-5">
    <div class="inner-body">
      <!-- Page Header -->
      <div class="page-header">
        <div>
          <h2 class="main-content-title tx-24 mg-b-5">Yêu cầu sản xuất</h2>
          <ol class="breadcrumb">
            <li class="breadcrumb-item">
              <a href="index.html">Trang chủ</a>
            </li>
            <li class="breadcrumb-item active" aria-current="page">Yêu cầu sản xuất</li>
          </ol>
        </div>
        <div class="d-flex">
          <div class="justify-content-center">
            <router-link to="/production_required" class="btn ripple btn-rounded btn-white my-2 btn-icon-text">
               <i class="fe fe-corner-down-left"></i> Trở lại
            </router-link>
          </div>
        </div>
      </div>
      <!-- End Page Header -->
      <!-- Row -->
      <div class="row row-sm">
        <div class="col-lg-12 col-md-12">
          <div class="card custom-card">
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">Phiếu yêu cầu sản xuất Số: <span class="badge bg-success"> {{ updateID }}</span> </h6>
                  <p class="text-muted card-sub-title"></p>
                </div>
                <table class="table table-bordered">
                  <tbody>
                    <tr>
                      <td>
                        <div class="form-group">
                          <label class="fw-bold">Người tạo</label>
                          <input type="text" class="form-control text-body" v-model="storeProOder.user_create_by" readonly>
                        </div>
                      </td>
                      <td>
                        <div class="form-group">
                          <label class="fw-bold">Ngày tạo</label>
                          <input class="form-control text-body" type="text" v-model="storeProOder.production_order_create" readonly>
                        </div>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <div class="form-group ">
                          <label class="fw-bold">Số báo giá/Đơn hàng</label>
                          <textarea class="form-control text-black" rows="4" placeholder="Nhập số báo giá/đơn hàng" v-model="storeProOder.production_order_invoice" :class="{'is-invalid': updateOderErrors.production_order_invoice}"></textarea>
                          <div class="invalid-feedback" v-if="updateOderErrors.production_order_invoice">{{updateOderErrors.production_order_invoice}} </div>
                        </div>
                      </td>
                      <td>
                        <div class="form-group ">
                          <label class="fw-bold">Ghi chú</label>
                          <textarea 
                          class="form-control text-black" 
                          rows="6" placeholder="Nhập ghi chú" 
                          v-model="storeProOder.production_order_note" 
                          :class="{'is-invalid': updateOderErrors.production_order_note}"></textarea>
                          <div class="invalid-feedback" v-if="updateOderErrors.production_order_note">{{updateOderErrors.production_order_note}} </div>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">Yêu cầu chi tiết
                    <button data-bs-target="#productModal" data-bs-toggle="modal" class="btn btn-primary btn-sm">
													<i class="fa fa-plus"></i>
												</button> 
                  </h6>
                  <p class="text-muted card-sub-title">Thêm danh sách sản phẩm yêu cầu</p>
                </div>
                <div class="table-responsive">
                  <table class="table table-bordered" id="productTable">
                    <thead class>
                      <tr>
                        <th style="width:2%">STT</th>
                        <th style="width: 14%;">Tên SP</th>
                        <th style="width: 7%;">Số lượng</th>
                        <th style="width: 7%;">Đơn vị</th>
                        <th style="width: 11%;">Dự kiến hoàn thành</th>
                        <th style="width: 11%;">Quy cách đóng gói</th>
                        <th>Yêu cầu chi tiết</th>
                        <th >Thao tác</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(product, index) in products" :key="index">
                        <td>{{ index +1 }}</td>
                        <td>{{ product.detail_product_order_name }}</td>
                        <td>{{ product.detail_product_order_quantity }}</td>
                        <td>{{ product.detail_product_order_unit }}</td>
                        <td>{{ product.detail_product_order_finish }}</td>
                        <td>{{ product.detail_product_order_packing }}</td>
                        <td > 
                          <textarea class="form-control text-body" rows="10" v-model="product.detail_product_order_detail" readonly></textarea>
                        </td>
                      <td class="d-flex">
                        <a @click="editProduct(product)" data-bs-target="#updateProductModal" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-info ms-1"><i class="fe fe-edit-2"></i></a>
                          <a @click="removeTableRow(product._id)" class="btn btn-sm btn-outline-danger ms-1"> <i class="fe fe-trash"></i></a>
                      </td>
                      </tr>
					          </tbody>
                  </table>
                </div>
                 <div class="invalid-feedback" v-if="updateOderErrors.data" style="display: block; font-size: 13px;">{{updateOderErrors.data}}</div>
                <div class="form-group row justify-content-end mb-0 mt-3">
                  <div class="col-md-12 ps-md-2">
                    <button @click="updateProOder" class="btn ripple btn-primary btn-rounded pd-x-30 mg-r-5">Lưu</button>
                  </div>
                </div>
              </div>
            
          </div>
        </div>
      </div>
      <!-- End Row -->
    </div>
  </div>
</div>
<!-- End Main Content-->

<!-- Grid modal -->
			<div class="modal" id="productModal"  ref="productModal">
				<div class="modal-dialog modal-lg" >
					<div class="modal-content modal-content-demo">
						<div class="modal-header">
							<h6 class="modal-title">Yêu cầu chi tiết</h6><button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
						</div>
						<!-- <form @submit.prevent="addTableRow">  -->
						<div class="modal-body">
							<table class="table ">
							<tbody>
								<tr>
								<td class="align-top" colspan="2">
									<div class="form-group">
									<label class="fw-bold">Tên sản phẩm</label> 
										<input class="form-control text-black" v-model="newProduct.detail_product_order_name" :class="{'is-invalid':addErrors.detail_product_order_name}"  placeholder="Nhập tên sản phẩm" >
                    <div class="invalid-feedback" v-if="addErrors.detail_product_order_name">{{addErrors.detail_product_order_name}} </div>
                  </div>
								</td>
								</tr>
								<tr>
                  	<td class="align-top">
									<div class="form-group">
									<label class="fw-bold"> Số lượng</label>
									<input type="number" class="form-control text-black" placeholder="Nhập số lượng"  v-model="newProduct.detail_product_order_quantity" :class="{'is-invalid':addErrors.detail_product_order_quantity}">
                  <div class="invalid-feedback" v-if="addErrors.detail_product_order_quantity">{{addErrors.detail_product_order_quantity}} </div>
                </div>
								</td>
								<td class="align-top">
									<div class="form-group">
									<label class="fw-bold">Đơn vị tính</label> 
									<select class="form-control text-black" name="product_unit" v-model="newProduct.detail_product_order_unit" :class="{'is-invalid':addErrors.detail_product_order_unit}">
										<option value="SET">SET</option>
										<option value="PCE">PCE</option>
									</select>
                  <div class="invalid-feedback" v-if="addErrors.detail_product_order_unit">{{addErrors.detail_product_order_unit}} </div>
									</div>
								</td>
							
								</tr>
								<tr>
                  	<td class="align-top">
									<div class="form-group">
									<label class="fw-bold"> Ngày dự kiến hoàn thành</label>
									<input class="form-control text-black" type="date" v-model="newProduct.detail_product_order_finish" :class="{'is-invalid':addErrors.detail_product_order_finish}">
                    <div class="invalid-feedback" v-if="addErrors.detail_product_order_finish">{{addErrors.detail_product_order_finish}} </div>
                </div>
								</td>
								<td >
									<div class="form-group">
									<label class="fw-bold">Quy cách đóng gói</label> 
									<textarea class="form-control text-black" rows="5" placeholder="Quy cách đóng gói" v-model="newProduct.detail_product_order_packing" :class="{'is-invalid':addErrors.detail_product_order_packing}"></textarea>
                    <div class="invalid-feedback" v-if="addErrors.detail_product_order_packing">{{addErrors.detail_product_order_packing}} </div>
                </div>
								</td>
								</tr>
								<tr>
								<td colspan="2">
									<div class="form-group">
									<label class="fw-bold"> Yêu cầu chi tiết</label>
									<textarea class="form-control text-black" rows="5" placeholder="Yêu cầu chi tiết" v-model="newProduct.detail_product_order_detail" :class="{'is-invalid':addErrors.detail_product_order_detail}"></textarea>
								<div class="invalid-feedback" v-if="addErrors.detail_product_order_detail">{{addErrors.detail_product_order_detail}} </div>	
                </div>
								</td>
								</tr>
							</tbody>
							</table>
						</div>
              <div v-if="showSuccessMessage" class="alert alert-success text-center">
            {{ successMessage }}
          </div>
          <div class="modal-footer d-flex justify-content-between">
            <div class="form-check">
              <input class="form-check-input" type="checkbox" id="check1" name="option1" value="something" v-model="dontCloseModal">
              <label class="form-check-label fw-bold">Không đóng màn hình</label>
            </div>
            <div>
              <button class="btn ripple btn-primary" @click="addTableRow">Thêm</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('productModal','newProduct','addErrors')" > Đóng </button>
            </div>
          </div>
						<!-- </form> -->
					</div>
				</div>
			</div>
			<!--End Grid modal -->

			<!-- Grid modal -->
			<div class="modal" id="updateProductModal"  ref="updateProductModal">
				<div class="modal-dialog modal-lg" >
					<div class="modal-content modal-content-demo">
						<div class="modal-header">
							<h6 class="modal-title">Yêu cầu chi tiết</h6><button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
						</div>
						<div class="modal-body">
							<table class="table ">
							<tbody>
								<tr>
								<td class="align-top" colspan="2">
									<div class="form-group">
									<label class="fw-bold">Tên sản phẩm</label> 
										<input class="form-control text-black" v-model="selectedProduct.detail_product_order_name" :class="{'is-invalid':updateErrors.detail_product_order_name}" placeholder="Nhập tên sản phẩm" >
                    <div class="invalid-feedback" v-if="updateErrors.detail_product_order_name">{{updateErrors.detail_product_order_name}} </div>
                  </div>
								</td>
							
								</tr>
								<tr>
                  	<td class="align-top">
									<div class="form-group">
									<label class="fw-bold"> Số lượng</label>
									<input type="number" class="form-control text-black" placeholder="Nhập số lượng"  v-model="selectedProduct.detail_product_order_quantity" :class="{'is-invalid':updateErrors.detail_product_order_quantity}">
                  <div class="invalid-feedback" v-if="updateErrors.detail_product_order_quantity">{{updateErrors.detail_product_order_quantity}} </div>
                </div>
								</td>
								<td class="align-top">
									<div class="form-group">
									<label class="fw-bold">Đơn vị tính</label> 
									<select class="form-control text-black" name="product_unit" v-model="selectedProduct.detail_product_order_unit" :class="{'is-invalid':updateErrors.detail_product_order_unit}">
										<option value="SET">SET</option>
										<option value="PCE">PCE</option>
									</select>
                  <div class="invalid-feedback" v-if="updateErrors.detail_product_order_unit">{{updateErrors.detail_product_order_unit}} </div>
									</div>
								</td>
							
								</tr>
								<tr>
                  	<td class="align-top">
									<div class="form-group">
									<label class="fw-bold"> Ngày dự kiến hoàn thành</label>
									<input class="form-control text-black" type="date" v-model="selectedProduct.detail_product_order_finish" :class="{'is-invalid':updateErrors.detail_product_order_finish}">
                    <div class="invalid-feedback" v-if="updateErrors.detail_product_order_finish">{{updateErrors.detail_product_order_finish}} </div>
                </div>
								</td>
								<td>
									<div class="form-group">
									<label class="fw-bold">Quy cách đóng gói</label> 
									<textarea class="form-control text-black" rows="5" placeholder="Quy cách đóng gói" v-model="selectedProduct.detail_product_order_packing" :class="{'is-invalid':updateErrors.detail_product_order_packing}"></textarea>
                    <div class="invalid-feedback" v-if="updateErrors.detail_product_order_packing">{{updateErrors.detail_product_order_packing}} </div>
                </div>
								</td>
								</tr>
								<tr>
								<td colspan="2">
									<div class="form-group">
									<label class="fw-bold"> Yêu cầu chi tiết</label>
									<textarea class="form-control text-black" rows="5" placeholder="Yêu cầu chi tiết" v-model="selectedProduct.detail_product_order_detail" :class="{'is-invalid':updateErrors.detail_product_order_detail}"></textarea>
								<div class="invalid-feedback" v-if="updateErrors.detail_product_order_detail">{{updateErrors.detail_product_order_detail}} </div>	
                </div>
								</td>
								</tr>
							</tbody>
							</table>
						</div>
              <div v-if="showSuccessMessage" class="alert alert-success text-center">
            {{ successMessage }}
          </div>
          <div class="modal-footer">
            <div>
              <button  class="btn ripple btn-primary" @click="updateProduct">Lưu</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('updateProductModal','selectedProduct','updateErrors')" > Đóng </button>
            </div>
          </div>
					</div>
				</div>
			</div>
			<!--End Grid modal -->
</template>

<script>
import util from '@/components/uses/utils.js'
import api from '@/components/uses/api.js'
import {format} from'date-fns'

export default {
data() {
    return {
	  editData:[],
      proOderID:null,
      storeProOder:[],
      newProduct: [],
	    selectedProduct: [],
      products: [],
      addErrors: [],
	   updateErrors: [],
      updateOderErrors:[],
      productErrors:[],
      dontCloseModal: false,
      showSuccessMessage: false, 
      successMessage: '',
	  updateID:null
    };
  },
 mounted (){
	this.updateID = this.$route.params.id
	this.getProOderID();
 },
	methods: {
    getProOderID() {
        api.getData(`product-order/editProductOrder/${this.updateID}`)
        .then(Response => {
        this.editData = Response.data.data[0]
        this.products = this.editData.dataDetail
        this.storeProOder.production_order_create = format(new Date(this.editData.production_order_create), 'MM/dd/yyyy') 
        this.storeProOder.user_create_by = this.editData.detail_user[0].fullname;
        this.storeProOder.production_order_invoice = this.editData.production_order_invoice;
        this.storeProOder.production_order_note = this.editData.production_order_note;
        })
        .catch(error=> {
          console.log(error)
        })
      },

	   removeTableRow(id) {
        util.confirmDelete(() => {
          api.deleteData(`detail-product-order/deleteDetailProductOrder?id=${id}`)
          .then(response => {
            util.showSuccessAlert('Xóa thành công');
            this.getProOderID();
          })
          .catch(error => {
          console.error('Lỗi khi xóa khu vực:', error);
          });
          })
      },
	 addTableRow() {
      const newProductData = {
        product_order_code: this.updateID,
        detail_product_order_name:this.newProduct.detail_product_order_name,
        detail_product_order_quantity:this.newProduct.detail_product_order_quantity,
        detail_product_order_unit:this.newProduct.detail_product_order_unit,
        detail_product_order_finish:this.newProduct.detail_product_order_finish,
        detail_product_order_packing:this.newProduct.detail_product_order_packing,
        detail_product_order_detail:this.newProduct.detail_product_order_detail,
      }
      const newProductField = [
        { name: 'detail_product_order_name', label: 'Tên sản phẩm', maxLength: 100, nullable: false}, 
        { name: 'detail_product_order_quantity', label: 'Số lượng', maxLength: 100, nullable: false}, 
        { name: 'detail_product_order_unit', label: 'Đơn vị', maxLength: 100, nullable: false},
        { name: 'detail_product_order_finish', label: 'Ngày hoàn thành', maxLength: 100, nullable: false},
        { name: 'detail_product_order_packing', label: 'Quy cách đóng gói', maxLength: 100, nullable: false},
        { name: 'detail_product_order_detail', label: 'Yêu cầu chi tiết', maxLength: 900, nullable: true}
      ] 
       this.addErrors = util.validateData(newProductData, newProductField);
        if (Object.keys(this.addErrors).length === 0) {
          api.postData('detail-product-order/storeDetailProductOrder',newProductData) 
          .then(Response => {
            
            if(Response.data.status===200) {
               if (!this.dontCloseModal) {
                  this.getProOderID();
                  this.closeModal('productModal', 'newProduct', 'addErrors');
                  util.showSuccessAlert('Thêm thành công');
                } else {
                  this.getProOderID();
                  this.newProduct={};
                  this.showSuccessMessage = true;
                  this.successMessage = 'Thêm thành công'
                  setTimeout(() => {
                  this.showSuccessMessage = false; 
                  }, 1000); 
                }
            }
          })
          .catch(Errors => {
            console.log(Errors)
          })
        }
    },

    closeModal(modalRef, datakey, errorskey) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
        this[datakey] = {};
        this[errorskey] = {};
      },
	  
	editProduct(item) {
		this.selectedProduct._id=item._id
    this.selectedProduct.detail_product_order_name = item.detail_product_order_name;
    this.selectedProduct.detail_product_order_quantity = item.detail_product_order_quantity;
    this.selectedProduct.detail_product_order_unit = item.detail_product_order_unit;
		this.selectedProduct.detail_product_order_finish = item.detail_product_order_finish;
    this.selectedProduct.detail_product_order_packing = item.detail_product_order_packing;
		this.selectedProduct.detail_product_order_detail = item.detail_product_order_detail;
	},
	updateProduct() {
		const updateProductData = {
        detail_product_order_name:this.selectedProduct.detail_product_order_name,
        detail_product_order_quantity:this.selectedProduct.detail_product_order_quantity,
        detail_product_order_unit:this.selectedProduct.detail_product_order_unit,
        detail_product_order_finish:this.selectedProduct.detail_product_order_finish,
        detail_product_order_packing:this.selectedProduct.detail_product_order_packing,
        detail_product_order_detail:this.selectedProduct.detail_product_order_detail,
		}
		const updateProductField = [
			{ name: 'detail_product_order_name', label: 'Tên sản phẩm', maxLength: 100, nullable: false}, 
			{ name: 'detail_product_order_quantity', label: 'Số lượng', maxLength: 100, nullable: false}, 
			{ name: 'detail_product_order_unit', label: 'Đơn vị', maxLength: 100, nullable: false},
			{ name: 'detail_product_order_finish', label: 'Ngày hoàn thành', maxLength: 100, nullable: false},
			{ name: 'detail_product_order_packing', label: 'Quy cách đóng gói', maxLength: 100, nullable: false},
			{ name: 'detail_product_order_detail', label: 'Yêu cầu chi tiết', maxLength: 900, nullable: true}
		] 
		this.updateErrors = util.validateData(updateProductData, updateProductField);
			if (Object.keys(this.updateErrors).length === 0) { 
				api.putData(`detail-product-order/updateDetailProductOrder/${this.selectedProduct._id}` , updateProductData)
				.then(response => {
				if (response.data.error) {
				response.data.error.details.forEach((error) => {
					const errorMessage = error.message;
					const errorField = error.path[0];
					this.updateErrors[errorField] = [];
					this.updateErrors[errorField] = errorMessage;
				});
				} else {
					util.showSuccessAlert('Cập nhật thành công');
					this.closeModal('updateProductModal', 'selectedProduct', 'updateErrors');
					this.getProOderID();
				}
				})
				.catch(error => {
				console.error('Lỗi khi cập nhật khu vực:', error);
					});
			}
		},

		updateProOder() {
			const updateProdOderData = {
				production_order_invoice : this.storeProOder.production_order_invoice,
				production_order_note : this.storeProOder.production_order_note
			}
			const updateProOderField = [
				{name:'production_order_invoice' , label:'Số báo giá/Đơn hàng' , maxLength:100 , nullable:false},
				{name:'production_order_note' , label:'Ghi chú' , maxLength:900 , nullable:true},
			]
			this.updateOderErrors = util.validateData(updateProdOderData, updateProOderField);
			if(Object.keys(this.updateOderErrors).length ===0  ) {
				if(this.products.length ===0) {
					this.updateOderErrors.data ='Vui lòng nhập chi tiết yêu cầu'
					return;
				}
				else {
					api.putData(`product-order/updateProductOrder/${this.editData._id}` ,updateProdOderData)
					.then(Response => {
						if (Response.data.error) {
						util.showErrorAlert('Cập nhật phiếu không thành công')
					} else {
						this.storeProOder.production_order_invoice=''
						this.storeProOder.production_order_note=''
						util.showSuccessAlert('Cập nhật thành công');
					}
					}) 
					.catch(error => {
					console.log(error)
					})
				}
			}
		},



	}
  }
</script>
<style>
</style>