<script>
  import util from '@/components/uses/utils.js'
  import VueCookies from 'vue-cookies';

export default {
  data() {
    return {
      user:[]
    }
  },
  mounted() {
    this.user = JSON.parse(sessionStorage.getItem('userLogin'));
  },
  created() {
    const script1 = document.createElement('script');
    script1.src = '/frontend/plugins/sidemenu/sidemenu.js';
    script1.async = true;
    document.head.appendChild(script1);

    const script2 = document.createElement('script');
    script2.src = '/frontend/js/custom.js';
    script2.async = true;
    document.head.appendChild(script2);
  },
  methods: {
      isRouteActive(routes) {
        const currentPath = this.$route.path;
        const regex = new RegExp(`^(${routes.join('|')})($|\/)`);
        return regex.test(currentPath);
      },

      isActive(route) {
        return this.$route.path === route;
      },

    logout() {
        util.confirmDelete(() => {
        VueCookies.remove('accessToken');
        sessionStorage.removeItem('userLogin');
        this.$router.push({ name: 'signin' });
        },'Bạn chắc chắn muốn đăng xuất khỏi hệ thống')
    },

    displayModule() {
      return this.user.role == 1
    }
  },  
}
</script>
