<template>
  <!-- Main Content-->
  <div class="main-content side-content pt-0">
    <div class="main-container container-fluid pt-5">
      <div class="inner-body">
        <!-- Page Header -->
        <div class="page-header">
          <div>
            <h2 class="main-content-title tx-24 mg-b-5">Quản lý gói cước</h2>
            <ol class="breadcrumb">
              <li class="breadcrumb-item">
                <a href="#">Trang chủ</a>
              </li>
              <li class="breadcrumb-item active" aria-current="page"> gói cước </li>
            </ol>
          </div>
          <div class="d-flex">
            <div class="justify-content-center">
              <a data-bs-target="#add_Package" data-bs-toggle="modal" class="btn btn-primary btn-rounded my-2 text-white">
                <i class="fe fe-plus"></i> Thêm mới </a>
            </div>
          </div>
        </div>
        <!-- End Page Header -->
        <!-- Row -->
        <div class="row row-sm">
          <div class="col-lg-12">
            <div class="card custom-card">
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">Danh sách gói cước</h6>
                  <p class="text-muted card-sub-title"></p>
                </div>
                <div class="table-responsive">
                  <table :data="packages" class="table table-bordered" id="PackageTable">
                    <thead>
                      <tr>
                        <th class="wd-20p">STT</th>
                        <th class="wd-25p">Thời hạn (tháng)</th>
                        <th class="wd-25p">Mô tả</th>
                        <th class="wd-15p">Thao tác</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in packages" :key="item._id">
                        <td>{{ index + 1 }}</td>
                        <td>{{ item.sim_package_expiration}}</td>
                        <td>{{ item.sim_package_description}}</td>
                        <td>
                            <a @click="editPackage(item)" data-bs-target="#update_Package" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-info ms-1"><i class="fe fe-edit-2"></i></a>
                          <a @click="deletePackage(item._id)" class="btn btn-sm btn-outline-danger ms-1"> <i class="fe fe-trash"></i></a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- End Row -->
      </div>
    </div>
  </div>
  <!-- End Main Content-->
  <!-- Large Modal -->
  <div class="modal" id="add_Package" ref="addPackageModal">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Thêm mới gói cước</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="addPackage">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Thời hạn (tháng)</label>
                      <input v-model="storePackage.sim_package_expiration" class="form-control text-body" name="sim_package_expiration" :class="{'is-invalid':addErrors.sim_package_expiration}" type="number" placeholder="Nhập Thời hạn" />
                      <div class="invalid-feedback mt-2" v-if="addErrors.sim_package_expiration">{{addErrors.sim_package_expiration}}</div>
                    </div>
                  </td>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Mô tả</label>
                      <input v-model="storePackage.sim_package_description" class="form-control text-body" name="sim_package_description" :class="{'is-invalid':addErrors.sim_package_description}" type="text" placeholder="Nhập Mô tả" />
                      <div class="invalid-feedback" v-if="addErrors.sim_package_description">{{addErrors.sim_package_description}}</div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div v-if="showSuccessMessage" class="alert alert-success text-center">
            {{ successMessage }}
          </div>
          <div class="modal-footer d-flex justify-content-between">
            <div class="form-check">
              <input class="form-check-input" type="checkbox" id="check1" name="option1" value="something" v-model="dontCloseModal">
              <label class="form-check-label fw-bold">Không đóng màn hình</label>
            </div>
            <div>
              <button class="btn ripple btn-primary" type="submit">Lưu</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('addPackageModal','storePackage','addErrors')" type="button"> Đóng </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->
  <!-- Large Modal -->
  <div class="modal" id="update_Package" ref="updatePackageModal">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Cập nhật gói cước</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="updatePackage">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Thời hạn (tháng)</label>
                      <input v-model="selectedPackage.sim_package_expiration" :class="{'is-invalid': updateErrors.sim_package_expiration}" class="form-control text-body" type="number" placeholder="Nhập Thời hạn" />
                      <div class="invalid-feedback" v-if="updateErrors.sim_package_expiration">{{updateErrors.sim_package_expiration}} </div>
                    </div>
                  </td>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Mô tả</label>
                      <input v-model="selectedPackage.sim_package_description" class="form-control text-body" :class="{'is-invalid': updateErrors.sim_package_description}" type="text" placeholder="Nhập Mô tả" />
                      <div class="invalid-feedback" v-if="updateErrors.sim_package_description">{{updateErrors.sim_package_description}} </div>
                    </div>
                  </td>
                </tr>
               
              </tbody>
            </table>
          </div>
          <div class="modal-footer">
            <button class="btn ripple btn-primary" type="submit">Lưu</button>
            <button class="btn ripple btn-danger" @click="closeModal('updatePackageModal', 'selectedPackage','updateErrors' )" type="button"> Đóng </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->
</template>
<script>
  import util from '@/components/uses/utils.js'
  import api from '@/components/uses/api.js'
  export default {
    data() {
      return {
        packages: [],
        storePackage:{
          sim_package_expiration:'',
          sim_package_description:'',
        },
        selectedPackage: [],
        dontCloseModal: false,
        addErrors: [],
        updateErrors: [],
        showSuccessMessage: false, 
        successMessage: '',
      };
    },
    mounted() {
      this.getpackages();
    },
    methods: {
      loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#PackageTable')
        });
      },
      getpackages() {
        api.getData('sim-package/listSimPackage')
        .then(response=> {
          this.packages = response.data.data;
          this.loadtable();
        })
        .catch(error=> {
          util.errorRedirect()
        })
      },

      addPackage() {
        const PackageData = {
          sim_package_expiration: this.storePackage.sim_package_expiration,
          sim_package_description: this.storePackage.sim_package_description,
        };
        const PackageField = [
          { name: 'sim_package_expiration', label: 'Thời hạn', maxLength: 50, type:'number', nullable: false }, 
          { name: 'sim_package_description', label: 'Mô tả', maxLength: 50, nullable: false }, 
        ]
        this.addErrors = util.validateData(PackageData, PackageField);
        if (Object.keys(this.addErrors).length === 0) {
          api.postData('sim-package/storeSimPackage',PackageData)
          .then(response => {
             if (response.data.error) {
              response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.errors[errorField] = [];
                this.errors[errorField] = errorMessage;
              });
              } else {
                if (!this.dontCloseModal) {
					  this.getpackages();
                  this.closeModal('addPackageModal', 'storePackage', 'errors');
                  util.showSuccessAlert('Thêm thành công');
                } else {
                  this.getpackages();
                  this.storePackage={};
                  this.showSuccessMessage = true;
                  this.successMessage = 'Thêm thành công'
                  setTimeout(() => {
                  this.showSuccessMessage = false; 
                }, 1000); 
                }
              }
          })
            .catch(error => {
            console.error('Lỗi khi thêm gói cước:', error);
        });
        }
      },

      deletePackage(id) {
        util.confirmDelete(() => {
          api.deleteData(`sim-package/deleteSimPackage/?id=${id}`)
          .then(response => {
            util.showSuccessAlert('Xóa thành công');
            this.getpackages();
          })
          .catch(error => {
          console.error('Lỗi khi xóa gói cước:', error);
          });
          })
      },

      closeModal(modalRef, datakey, errorskey) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
        this[datakey] = {};
        this[errorskey] = {};
      },

      editPackage(item) {
        this.selectedPackage.id = item._id;
        this.selectedPackage.sim_package_expiration = item.sim_package_expiration;
        this.selectedPackage.sim_package_description = item.sim_package_description;
      },
      updatePackage() {
        const dataUpdate = {
          sim_package_expiration: this.selectedPackage.sim_package_expiration,
          sim_package_description: this.selectedPackage.sim_package_description,
        }
        const updatePackageField = [
          { name: 'sim_package_expiration', label: 'Thời hạn', maxLength: 50, type:'number', nullable: false}, 
          { name: 'sim_package_description', label: 'Mô tả', maxLength: 50, nullable: false}, 
          { name: 'Package_note', label: 'Ghi chú', maxLength: 50, nullable: true}
        ]
        this.updateErrors = util.validateData(dataUpdate, updatePackageField); 
        if (Object.keys(this.updateErrors).length === 0) {
            api.putData(`sim-package/updateSimPackage/${this.selectedPackage.id}` , dataUpdate)
            .then(response => {
              if (response.data.error) {
              response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.updateErrors[errorField] = [];
                this.updateErrors[errorField] = errorMessage;
              });
              } else {
                util.showSuccessAlert('Cập nhật thành công');
                this.closeModal('updatePackageModal', 'selectedPackage', 'updateErrors');
                this.getpackages();
              }
            })
            .catch(error => {
              console.error('Lỗi khi cập nhật gói cước:', error);
            });
        }
      },
    }
  }
</script>
