<template>
  <!-- Main Content-->
  <div class="main-content side-content pt-0">
    <div class="main-container container-fluid pt-5">
      <div class="inner-body">
        <!-- Page Header -->
        <div class="page-header">
          <div>
            <h2 class="main-content-title tx-24 mg-b-5">
              Quản lý thiết bị
            </h2>
            <ol class="breadcrumb">
              <li class="breadcrumb-item"><a href="#">Trang chủ</a></li>
              <li class="breadcrumb-item active" aria-current="page">
                Quản lý thiết bị
              </li>
            </ol>
          </div>
          <div class="d-flex">
            <div class="justify-content-center">
              <button data-bs-target="#add_detail_monitoring" data-bs-toggle="modal"
                class="btn btn-primary btn-rounded my-2 me-2 btn-icon-text">
                <i class="fe fe-plus"></i> Thêm mới
              </button>
              <RouterLink to="/monitoring_network" class="btn btn-white btn-rounded my-2 btn-icon-text">
									  <i class="fe fe-corner-down-left"></i> Trở lại
                                    </RouterLink>
            </div>
          </div>
        </div>
        <!-- End Page Header -->
         <!-- Row -->
        <div class="row row-sm">
          <div class="col-lg-12">
            <div class="card custom-card">
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">
                    Khu Vực
                  </h6>
                  <p class="text-muted card-sub-title"></p>
                </div>
                <div class="table-responsive">
                  <table  class="table table-bordered" id="DetailMonitor">
                    <thead>
                      <tr>
                        <th>STT</th>
                        <th>Tên trạm</th>
                        <th>Ngày lắp đặt</th>
                        <th>Địa chỉ</th>
