                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in monitors" :key="item._id">
                        <td>{{ index +1 }}</td>
                        <td>{{ item.name_station }}</td>
                        <td>{{ item.installtion_date }}</td>
                        <td>{{ item.address_of_station }}</td>
                        <td>{{ item.customer }}</td>
                        <td> 
                           <a @click="viewImages(item)"  data-bs-target="#monitoring_network_image" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-primary  ms-1"><i class="fe fe-eye"></i></a>
                          </td>
                           <td> 
                           <a @click="viewLocation(item)"  data-bs-target="#monitoring_network_location" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-primary  ms-1"><i class="fe fe-eye"></i></a>
                          </td>
                        <td>

                            <a @click="editDetailMonitor(item)" data-bs-target="#update_detail_monitoring" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-info  ms-1"><i class="fe fe-edit-2"></i></a>
                          <a @click="deleteDetailMonitor(item._id)" class="btn btn-sm btn-outline-danger  ms-1"> <i class="fe fe-trash"></i></a>
                        </td>
                       </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- End Row -->
      </div>
    </div>
  </div>
  <!-- End Main Content-->

        <!-- Extra Large Modal -->
      <div class="modal fade" id="monitoring_network_image" ref="MonitoringNetwork">
        <div class="modal-dialog modal-xl" role="document">
          <div class="modal-content modal-content-demo">
            <div class="modal-header">
              <h6 class="modal-title">Hình ảnh</h6>
              <button
                aria-label="Close"
                class="btn-close"
                data-bs-dismiss="modal"
                type="button"
              ></button>
            </div>
            <div class="modal-body">
                <div >
								<div class="card custom-card">
									<div class="card-body">
                    <div class="carousel-slider" >
											<div id="carousel" class="carousel slide" data-bs-ride="carousel">
												 <div class="carousel-inner">
                            <div v-for="(image, index) in images" :key="index" :class="{ 'carousel-item': true, 'active': index === 0 }">
                              <img :src="'http://192.168.48.31:8080/' + image" alt="img">
                            </div>
                          </div>
												<a class="carousel-control-prev" href="#carousel" role="button" data-bs-slide="prev">
													<i class="fa fa-angle-left fs-30" aria-hidden="true"></i>
												</a>
												<a class="carousel-control-next" href="#carousel" role="button" data-bs-slide="next">
													<i class="fa fa-angle-right fs-30" aria-hidden="true"></i>
												</a>
											</div>
										</div>
									</div>
								</div>
							</div>
            </div>
            <div class="modal-footer">
            <div>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('MonitoringNetwork','selectedSemiPro','addErrors')" type="button"> Đóng </button>
            </div>
          </div>
          </div>
        </div>
      </div>
      <!--End Extra Large Modal -->

        <!-- Extra Large Modal -->
      <div class="modal fade" id="monitoring_network_location" ref="MonitoringNetworkLocation">
        <div class="modal-dialog modal-xl" role="document">
          <div class="modal-content modal-content-demo">
            <div class="modal-header">
              <h6 class="modal-title">Hình ảnh</h6>
              <button
                aria-label="Close"
                class="btn-close"
                data-bs-dismiss="modal"
                type="button"
              ></button>
            </div>
            <div class="modal-body">
                <div >
								<div class="card custom-card">
									<div class="card-body">

                      <div style="height: 500px;">
                              <div>
                            <div>
                              <div class="d-flex">
                               <div class="form-group">
                                <input class="form-control text-body" v-model="infoLocation.lat" type="text" readonly/>
                              </div>
                               <div class="form-group ms-2">
                                <input class="form-control text-body" v-model="infoLocation.lng" type="text" readonly/>
                              </div>
                              </div>
                              <div class="form-group">
                                <input class="form-control text-body" v-model="infoLocation.address" type="text" readonly/>
                              </div>
                            </div>
                            <div>
                              <div ref="mapContainer1" style="width: 100%; height: 400px;"></div>
                            </div>
                          </div>
                         </div>
                    
									</div>
								</div>
							</div>
            </div>
            <div class="modal-footer">
            <div>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('MonitoringNetworkLocation','selectedSemiPro','addErrors')" type="button"> Đóng </button>
            </div>
          </div>
          </div>
        </div>
      </div>
      <!--End Extra Large Modal -->


      
        <!-- Extra Large Modal -->
      <div class="modal fade" id="add_detail_monitoring" ref="addDetailMonitorModal">
        <div class="modal-dialog modal-xl" role="document">
          <div class="modal-content modal-content-demo">
            <div class="modal-header">
              <h6 class="modal-title">Thêm mới</h6>
              <button
                aria-label="Close"
                class="btn-close"
                @click="closeModal('addDetailMonitorModal')"
                type="button"
              ></button>
            </div>
            <div class="modal-body">
              <table class="table ">
                <tbody>
                  <tr>
                    <td>
                      <div class="form-group">
                        <label class="fw-bold ">Tên trạm</label>
                        <input class="form-control text-body" v-model="storeMonitor.name_station" :class="{'is-invalid':addErrors.name_station}" type="text" placeholder="Tên trạm" />
                        <div class="invalid-feedback" v-if="addErrors.name_station">{{addErrors.name_station}}</div>
                      </div>
                    </td>
                    <td>
                      <div class="form-group">
                        <label class="fw-bold ">Ngày lắp đặt</label>
                        <input class="form-control text-body" v-model="storeMonitor.installtion_date" :class="{'is-invalid':addErrors.installtion_date}" type="date" />
                        <div class="invalid-feedback" v-if="addErrors.installtion_date">{{addErrors.installtion_date}}</div>
                      </div>
                    </td>
                    <td >
                      <div class="form-group">
                        <label class="fw-bold ">Khách hàng</label>
                         <input  class="form-control text-body" v-model="storeMonitor.customer" :class="{'is-invalid':addErrors.customer}" type="text" placeholder="Khách hàng" />
                        <div class="invalid-feedback" v-if="addErrors.customer">{{addErrors.customer}}</div>
                        </div>
                    </td>
                  </tr>
                  <tr>
                    <td class="align-top" colspan="3">
                      <div class="form-group">
                        <label class="fw-bold ">Địa chỉ lắp đặt</label>
                        <input  class="form-control text-body" v-model="storeMonitor.address_of_station" :class="{'is-invalid':addErrors.address_of_station}" type="text" placeholder="Địa chỉ lắp đặt" />
                        <div class="invalid-feedback" v-if="addErrors.address_of_station">{{addErrors.address_of_station}}</div>
                      </div>
                    </td>
                  </tr>
                  <tr>
                    <td class="align-top" colspan="3">
                      <div class="form-group">
                        <label class="fw-bold ">Chi tiết lắp đặt</label>
                        <textarea  class="form-control text-body" v-model="storeMonitor.detail_installtion" :class="{'is-invalid':addErrors.detail_installtion}" rows="5" placeholder="Chi tiết lắp đặt"></textarea>
                        <div class="invalid-feedback" v-if="addErrors.detail_installtion">{{addErrors.detail_installtion}}</div>
                      </div>
                    </td>
                  </tr>
                   <tr>
                    <td class="align-top" >
                      <div class="form-group">
                        <label class="fw-bold ">Hình ảnh</label>
                       	<div :class="{'is-invalid':addErrors.images}">
                           <input   type="file" class="dropify" name="images" accept="image/*" data-height="200" @change="handleImageChange" multiple />
                          
                           <ul>
                                   <li v-for="(file, index) in selectedFiles" :key="index">{{ file.name }}</li>
                           </ul>
                        </div>
                         <div class="invalid-feedback" v-if="addErrors.images">{{addErrors.images}}</div>
                      </div>
                    </td>
                    <td class="align-top" colspan="2">
                      <div class="form-group">
                        <label class="fw-bold ">Tọa độ</label>
                        <!-- <button @click="toggleMapLayer">Chuyển Map</button> -->
                          <div style="height: 500px;">
                              <div>
                            <div>
                              <div class="d-flex">
                               <div class="form-group">
                                <input class="form-control text-body" @input="onCoordinateInput"  :class="{'is-invalid':addErrors.location}" type="text" v-model="selectedCoordinates.lat" placeholder="Kinh độ"/>
                              </div>
                               <div class="form-group ms-2">
                                <input class="form-control text-body" @input="onCoordinateInput"  :class="{'is-invalid':addErrors.location}" type="text" v-model="selectedCoordinates.lng" placeholder="Vĩ độ"/>
                              </div>
                              </div>
                              <div class="form-group">
                                <input class="form-control text-body" :class="{'is-invalid':addErrors.location}" type="text" placeholder="Địa chỉ" v-model="selectedAddress" @input="onAddressInput"  />
                                 <div class="invalid-feedback" v-if="addErrors.location">{{addErrors.location}}</div>
                              </div>
                            </div>
                            <div>
                              <div ref="mapContainer" style="width: 100%; height: 330px;"></div>
                            </div>
                          </div>

                         </div>
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>

               <div v-if="showSuccessMessage" class="alert alert-success text-center">
            {{ successMessage }}
          </div>
          <div class="modal-footer d-flex justify-content-between">
            <div class="form-check">
              <input class="form-check-input" type="checkbox" id="check1" name="option1" value="something" v-model="dontCloseModal">
              <label class="form-check-label fw-bold">Không đóng màn hình</label>
            </div>
            <div>
              <button class="btn ripple btn-primary" @click="addDetailMonitor">Lưu</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('addDetailMonitorModal', 'storeMonitor', 'addErrors')" type="button"> Đóng </button>
            </div>
          </div>
          </div>
        </div>
      </div>
      <!--End Extra Large Modal -->

      <!-- Extra Large Modal -->
      <div class="modal fade" id="update_detail_monitoring" ref="updateDetailMonitorModal">
        <div class="modal-dialog modal-xl" role="document">
          <div class="modal-content modal-content-demo">
            <div class="modal-header">
              <h6 class="modal-title">Thêm mới</h6>
              <button
                aria-label="Close"
                class="btn-close"
                @click="closeModal('updateDetailMonitorModal')"
                type="button"
              ></button>
            </div>
            <div class="modal-body">
              <table class="table ">
                <tbody>
                  <tr>
                    <td>
                      <div class="form-group">
                        <label class="fw-bold ">Tên trạm</label>
                        <input class="form-control text-body" v-model="selectedMonitor.name_station" :class="{'is-invalid':updateErrors.name_station}" type="text" placeholder="Tên trạm" />
                        <div class="invalid-feedback" v-if="updateErrors.name_station">{{updateErrors.name_station}}</div>
                      </div>
                    </td>
                    <td>
                      <div class="form-group">
                        <label class="fw-bold ">Ngày lắp đặt</label>
                        <input class="form-control text-body" v-model="selectedMonitor.installtion_date" :class="{'is-invalid':updateErrors.installtion_date}" type="date" />
                        <div class="invalid-feedback" v-if="updateErrors.installtion_date">{{updateErrors.installtion_date}}</div>
                      </div>
                    </td>
                    <td >
                      <div class="form-group">
                        <label class="fw-bold ">Khách hàng</label>
                         <input  class="form-control text-body" v-model="selectedMonitor.customer" :class="{'is-invalid':updateErrors.customer}" type="text" placeholder="Khách hàng" />
                        <div class="invalid-feedback" v-if="updateErrors.customer">{{updateErrors.customer}}</div>
                        </div>
                    </td>
                  </tr>
                  <tr>
                    <td class="align-top" colspan="3">
                      <div class="form-group">
                        <label class="fw-bold ">Địa chỉ lắp đặt</label>
                        <input  class="form-control text-body" v-model="selectedMonitor.address_of_station" :class="{'is-invalid':updateErrors.address_of_station}" type="text" placeholder="Địa chỉ lắp đặt" />
                        <div class="invalid-feedback" v-if="updateErrors.address_of_station">{{updateErrors.address_of_station}}</div>
                      </div>
                    </td>
                  </tr>
                   <tr>
                    <td class="align-top" >
                      <div class="form-group">
                        <label class="fw-bold ">Hình ảnh</label>
                       	<div>
													<input id="demo" type="file" name="files" accept="image/jpeg, image/png, text/html, application/zip, text/css, text/js" multiple>
												</div>
                      </div>
                    </td>
                    <td class="align-top" colspan="2">
                      <div class="form-group">
                        <label class="fw-bold ">Tọa độ</label>
                       
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>

          <div class="modal-footer">
          
            <div>
              <button class="btn ripple btn-primary" @click="updateDetailMonitor">Lưu</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('updateDetailMonitorModal', 'selectedMonitor', 'updateErrors')" type="button"> Đóng </button>
            </div>
          </div>
          </div>
        </div>
      </div>
      <!--End Extra Large Modal -->


     
</template>

<script>
import api from '@/components/uses/api.js'
import util from '@/components/uses/utils.js'
import {format} from 'date-fns'
import L from 'leaflet';
export default {
  
   data() {
    return {
      storeMonitor:[],
      selectedMonitor:[],
      addErrors: [],
      updateErrors: [],
      dontCloseModal: false,
      showSuccessMessage: false,
      successMessage: '',
      monitors:[],
      selectedFiles: [], 
      images:[],
      province:[],

      map: null,
      mapContainer: null,
      selectedCoordinates: {},
      selectedAddress: null,
      marker:null,
      satelliteLayer: null,
      currentLayer: null,

      infoLocation:[],
      viewMap:null,
      viewMarker:null
    }
  },
  mounted() {
    const script1 = document.createElement('script');
    script1.src = '/frontend/plugins/fileuploads/js/fileupload.js';
    script1.async = true;
    document.head.appendChild(script1);

    const script2 = document.createElement('script');
    script2.src = '/frontend/plugins/fileuploads/js/file-upload.js';
    script2.async = true;
    document.head.appendChild(script2);
      this.getDetailMonitors()
      this.createMap()
  },
   
    methods: {

        toggleMapLayer() {
          const layerToAdd = this.currentLayer === 'map' ? this.satelliteLayer : this.mapLayer;
          const layerToRemove = this.currentLayer === 'map' ? this.mapLayer : this.satelliteLayer;

          layerToAdd.addTo(this.map);
          layerToRemove.removeFrom(this.map);

          this.currentLayer = (this.currentLayer === 'map') ? 'satellite' : 'map';
        },

      createMap() {
        this.map = L.map(this.$refs.mapContainer).setView([51.505, -0.09], 13);

        this.mapLayer = L.tileLayer('https://tile.openstreetmap.org/{z}/{x}/{y}.png', {
          maxZoom: 19,
        });

        this.satelliteLayer = L.tileLayer('https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}');

        this.mapLayer.addTo(this.map);
        this.currentLayer = 'map';

        this.map.on('click', this.onMapClick);
      },

      
   


     addSatelliteLayer() {
        this.satelliteLayer = L.tileLayer('https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}').addTo(this.map);
      },

    onMapClick(event) {
      if(this.marker) {
        this.marker.removeFrom(this.map)
      }
       this.marker = L.marker([event.latlng.lat, event.latlng.lng]).addTo(this.map);
        this.selectedCoordinates = {
          lat: event.latlng.lat.toFixed(6),
          lng: event.latlng.lng.toFixed(6),
        };
        this.getAddressFromCoordinates(event.latlng.lat, event.latlng.lng);
    },

   async getAddressFromCoordinates (lat, lng) {
        try {
            const response = await axios.get(`https://nominatim.openstreetmap.org/reverse?lat=${lat}&lon=${lng}&format=json`);
            this.selectedAddress = response.data.display_name;
            } catch (error) {
              console.error('Error fetching address:', error);
              this.selectedAddress = 'Lỗi khi tìm kiếm địa chỉ';
            }
    },

     onCoordinateInput() {
        this.selectedAddress = ''
        const lat = parseFloat(this.selectedCoordinates.lat);
        const lng = parseFloat(this.selectedCoordinates.lng);

        if (!isNaN(lat) && !isNaN(lng)) {
        this.setMarker(lat, lng);
        this.getAddressFromCoordinates(lat, lng);
        }
    },


    setMarker(lat, lng) {
        if(this.marker) {
            this.marker.removeFrom(this.map)
        }

        this.marker=L.marker([lat,lng]).addTo(this.map)
    },

      async getCoordinatesFromAddress(address) {
        try {
          const response = await axios.get(`https://nominatim.openstreetmap.org/search?format=json&q=${encodeURIComponent(address)}`);
          if (response.data && response.data.length > 0) {
            const { lat, lon } = response.data[0];
            this.setMarker(parseFloat(lat), parseFloat(lon));
            this.selectedCoordinates = {
              lat: lat.toFixed(6),
              lng: lon.toFixed(6),
            };
          } else {
            console.error('Không tìm thấy tọa độ cho địa chỉ.');
          }
        } catch (error) {
          console.error('Lỗi khi lấy tọa độ:', error);
        }
    },

    onAddressInput() {
      this.selectedCoordinates = {};
      if (this.selectedAddress.trim() !== '') {
        this.getCoordinatesFromAddress();
      }
    },

     async getCoordinatesFromAddress() {
      try {
        const response = await axios.get(`https://nominatim.openstreetmap.org/search?format=json&q=${encodeURIComponent(this.selectedAddress)}`);

        if (response.data && response.data.length > 0) {
          const result = response.data[0];
          this.selectedCoordinates = {
            lat: result.lat,
            lng: result.lon,
          };

          this.setMarker(result.lat, result.lon);
          if(this.map) {
            this.map.setView([result.lat, result.lon], 13);
          }
          
        }
      } catch (error) {
        console.error('Error fetching coordinates:', error);
      }
    },
  
        handleImageChange(event) {
            const selectedFiles = event.target.files;
            this.selectedFiles = Array.from(selectedFiles);
            this.storeMonitor.images = Array.from(selectedFiles);
        },

        loadtable() {
          this.$nextTick(() => {
            util.initializeDataTable('#DetailMonitor')
          });
        },

        getDetailMonitors() {
          api.getData(`detail_manager-ims/listDetailManagerIMS/${this.$route.params.id}`)
          .then(Response => {
            this.monitors=Response.data.data
            this.loadtable()
            this.province=Response.data.getProvinceId
            console.log(Response)
          })
          .catch(Error => {
            console.log(Error)
          })
        },

        addDetailMonitor() {
          const monitorData = {
            name_station: this.storeMonitor.name_station,
            installtion_date: this.storeMonitor.installtion_date,
            address_of_station: this.storeMonitor.address_of_station,
            customer: this.storeMonitor.customer,
            detail_installtion: this.storeMonitor.images,
          }
          const monitorField = [
             {name:'name_station', label:'Tên trạm', nullable:false},
             {name:'installtion_date', label:'Tên trạm', nullable:false},
             {name:'address_of_station', label:'Tên trạm', nullable:false},
             {name:'customer', label:'Tên trạm', nullable:false},
          ]

          const location = [
                this.selectedCoordinates.lat,
                this.selectedCoordinates.lng,
                this.selectedAddress
              ]
          this.storeMonitor.location = JSON.stringify(location)
         
          this.addErrors =util.validateData(monitorData,monitorField)
           if(!this.storeMonitor.images){
            this.addErrors.images='Hình ảnh không được bỏ trống'
          }
           if(!this.selectedCoordinates.lat && !this.selectedCoordinates.lng || !this.selectedAddress ){
              this.addErrors.location='Vui lòng chọn vị trí'
          }

          if(Object.keys(this.addErrors).length >0) {return} 
            const formData = new FormData();
            formData.append('name_station', this.storeMonitor.name_station)
            formData.append('installtion_date', this.storeMonitor.installtion_date)
            formData.append('address_of_station', this.storeMonitor.address_of_station)
            formData.append('area_id', this.province.province_id)
            formData.append('customer', this.storeMonitor.customer)
            formData.append('detail_installtion', this.storeMonitor.detail_installtion)
            formData.append('location', this.storeMonitor.location)
            this.storeMonitor.images.forEach((image) => {
              formData.append('images', image);
            });
          api.postData('detail_manager-ims/storeDetailManagerIMS',formData, {
             headers: {
              'Content-Type': 'multipart/form-data'
            }
          } )
          .then(Response => {
            if(Response.data.status==200) {
                if(!this.dontCloseModal) 
                {
                  this.getDetailMonitors()
                  util.showSuccessAlert('Thêm thành công')
                  this.closeModal('addDetailMonitorModal','storeMonitor', 'addErrors')
                }
                else {
                  this.getDetailMonitors()
                  this.storeMonitor={};
                  this.showSuccessMessage=true;
                  this.successMessage='Thêm thành công'
                  setTimeout(() => {
                    this.showSuccessMessage=false
                  }, 1000)
              }
                
              } 
          })
          .catch(Error => {
            console.log(Error)
          })
        },

        editDetailMonitor(item) {
          this.selectedMonitor = {
              id:item._id,
              name_station: item.name_station,
              installtion_date: format ( new Date (item.installtion_date), 'yyyy-MM-dd'),
              address_of_station: item.address_of_station,
              customer: item.customer,
          }
          console.log( this.selectedMonitor.installtion_date)
        },

        updateDetailMonitor() {
          const dataUpdate = {
            name_station: this.selectedMonitor.name_station,
            installtion_date: this.selectedMonitor.installtion_date,
            address_of_station: this.selectedMonitor.address_of_station,
            customer: this.selectedMonitor.customer,
          }
          const updateField = [
            {name:'name_station', label:'Tên trạm', nullable:false},
            {name:'installtion_date', label:'Tên trạm', nullable:false},
            {name:'address_of_station', label:'Tên trạm', nullable:false},
            {name:'customer', label:'Tên trạm', nullable:false},
          ]
          this.updateErrors = util.validateData(dataUpdate, updateField);

          if(Object.keys(this.updateErrors).length >0) {return}
          
          api.putData(`detail_manager-ims/updateDetailManagerIMS/${this.selectedMonitor.id}`, dataUpdate) 
          .then(Response => {
            console.log(Response)
            if(Response.data.status ==200) {
              util.showSuccessAlert('Cập nhật thành công')
              this.getDetailMonitors()
              this.closeModal('updateDetailMonitorModal', 'selectedMonitor', 'updateErrors')
            }
          })
          .catch(Error => {
            console.log(Error)
          })
        },

        deleteDetailMonitor(id) {
          util.confirmDelete(() => {
            api.deleteData(`detail_manager-ims/deleteDetailManagerIMS/${id}`)
            .then(Response => {
              util.showSuccessAlert('Xóa thành công');
              this.getDetailMonitors();
            })
            .catch(error => {
              console.error('Lỗi khi xóa khu vực:', error);
            });
            })
        },

        viewImages(item) {
          if(item.image) {
              const images = JSON.parse(item.image)
              this.images=images
          }
        },

        viewLocation(item) {
          const location = JSON.parse(item.location)
          this.infoLocation.lat = location[0]
          this.infoLocation.lng = location[1]
          this.infoLocation.address = location[2]

          if(!this.viewMap)  {
            this.viewMap = L.map(this.$refs.mapContainer1).setView([location[0], location[1]], 13);
            this.mapLayer = L.tileLayer('https://tile.openstreetmap.org/{z}/{x}/{y}.png', {
              maxZoom: 19,
            });
            this.satelliteLayer = L.tileLayer('https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}');
            this.mapLayer.addTo(this.viewMap);
          } else {
             this.viewMap.setView([location[0], location[1]], 13);
          }

           if (!this.viewMarker) {
              this.viewMarker = L.marker([location[0], location[1]]).addTo(this.viewMap);
            } else {
              this.viewMarker.setLatLng([location[0], location[1]]);
            }
       },

        closeModal(modalRef, datakey, errorskey) {
          const modal = this.$refs[modalRef];
          $(modal).modal('hide');
          this[datakey] = {};
          this[errorskey] = {};
        },
    }
}
 
</script>
