            this.reportData.proOderReport=Response.data.ProductOrder
            this.reportData.jobsheetReport=Response.data.JobSheet
            this.reportData.semiProReport=Response.data.SemiProduct
            this.reportData.productReport=Response.data.Product
        })
        .catch(Error => {
            console.log(Error)
        })
    }, 
},
