<template>
  <!-- Main Content-->
  <div class="main-content side-content pt-0">
    <div class="main-container container-fluid pt-5">
      <div class="inner-body">
        <!-- Page Header -->
        <div class="page-header">
          <div>
            <h2 class="main-content-title tx-24 mg-b-5">Yêu cầu sản xuất</h2>
            <ol class="breadcrumb">
              <li class="breadcrumb-item">
                <a href="index.html">Trang chủ</a>
              </li>
              <li class="breadcrumb-item active" aria-current="page">
                Yêu cầu sản xuất
              </li>
            </ol>
          </div>
          <div class="d-flex">
            <div class="justify-content-center">
             
              <router-link to="/production_required/add" class="btn btn-primary btn-rounded my-2 btn-icon-text"> <i class="fe fe-plus"></i>
                Thêm mới</router-link>
            </div>
          </div>
        </div>
        <!-- End Page Header -->

        <!-- Row -->
        <div class="row row-sm">
          <div class="col-lg-12">
            <div class="card custom-card">
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">Danh sách YCSX</h6>
                  <p class="text-muted card-sub-title"></p>
                </div>
                <div class="table-responsive">
                  <table :data="productionOders" class="table table-bordered" id="ProOderTable">
                    <thead>
                      <tr>
                        <th style="width: 4%;">STT</th>
                        <th style="width: 4%;">Phiếu YCSX</th>
                        <th style="width: 4%;">Số BG/Đơn hàng</th>
                        <th style="width: 4%;">Ngày tạo</th>
                        <th style="width: 4%;">Người tạo phiếu</th>
                        <th>Người nhận</th>
                        <th style="width: 4%;">Trạng thái</th>
                        <th>Ghi chú</th>
                        <th>Thao tác</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in productionOders" :key="item._id">
                        <td>{{ index +1 }}</td>
                        <td>{{ item.product_order_No }}</td>
                        <td>{{ item.production_order_invoice }}</td>
                        <td>{{  formatDate(item.production_order_create) }}</td>
                        <td>{{ item.detail_user[0].fullname }}</td>
                        <td>{{ item.production_order_receiver }}</td>
                        <td>
                          <span class="badge bg-pill" :class="getStatus(item.production_order_status)">{{ ProOderStatus(item.production_order_status) }}</span>
                        </td>
                         <td>
                          <textarea
                          class="form-control text-body"
                          readonly
                          v-if="item.production_order_note"
                          :value="item.production_order_note"
                          :style="{ height: (item.production_order_note.split('\n').length * 60) + 'px' }"
                        ></textarea>
                        </td>
                        <td>
                          <button class="btn btn-sm ripple btn-outline-primary" data-bs-toggle="dropdown">
                            <!-- Thao tác
                            <i class="icon ion-ios-arrow-down tx-11 mg-l-3"></i> -->
                            <i class="fe fe-more-horizontal"></i>
                          </button>
                          <div class="dropdown-menu">
                            <router-link class="dropdown-item" v-if="item.production_order_status == '0' && storedUserLogin.userID==item.user_create_by" :to="'/production_required/edit/' + item.product_order_No">Sửa</router-link>
                            <button class="dropdown-item" v-if="item.production_order_status=='0' && storedUserLogin.role == 1 " @click="approveProOrder(item._id)">Duyệt phiếu</button>
                            <button class="dropdown-item" v-if="item.production_order_status=='1' && storedUserLogin.role == 1 " @click="reapproveProOrder(item._id)">Hoàn duyệt</button>
                            <button class="dropdown-item" v-if="item.production_order_status=='0' && storedUserLogin.userID==item.user_create_by" @click="cancelProOrder(item._id)">Hủy phiếu</button>
                            <button class="dropdown-item" v-if="item.production_order_status=='2'">Phiếu đã hủy</button>
                            <router-link class="dropdown-item" :to="'/production_required/detail/' + item.product_order_No">Chi tiết</router-link>
                          </div>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- End Row -->
      </div>
    </div>
  </div>
  <!-- End Main Content-->
  <router-view></router-view>
</template>

<script>
import api from '@/components/uses/api.js'
import util from '@/components/uses/utils.js'
import {format} from 'date-fns'
export default {
  name: "ListProductionRequired",
    data() {
      return {
        productionOders: [],
        storedUserLogin:[]
      };
    },
    mounted() {
      this.getproductionOders();
      this.storedUserLogin = JSON.parse(sessionStorage.getItem('userLogin'));
      console.log(this.storedUserLogin)
    },
    methods: {
     formatDate(date) {
      if (!date) {
        return ''; 
      }
      const parsedDate = new Date(date);

        if (isNaN(parsedDate.getTime())) {
          return date; 
        }

      return format(parsedDate, 'MM/dd/yyyy');
      },

    loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#ProOderTable')
        });
      },
    getproductionOders() {
        api.getData('product-order/listProductOrder')
        .then(response=> {
          this.productionOders = response.data.data;
          this.loadtable();
          console.log(response)
        })
        .catch(error=> {
           console.log(error)
        })
      },
       ProOderStatus (statusCode) {
        switch(parseInt(statusCode)) {
          case 0: return "Mới tạo";
          case 1: return "Đã nhận";
          case 2: return "Đã hủy";
          default: return " Không rõ"
        }
      },
      getStatus(status) {
       if (status == '0') {
         return 'bg-primary-light';
        } else if (status == '1') {
          return 'bg-success-light';
        } else if (status == '2') {
          return 'bg-danger-light';
        } else {
          return 'bg-danger-light';
        }
      },
      cancelProOrder(id) {
        util.confirmDelete(() => {
          api.putData(`product-order/cancelProductOrder/${id}`)
          .then(response => {
            util.showSuccessAlert('Đã hủy');
            this.getproductionOders()
          })
          .catch(error => {
                console.log(error)
            });
          },'Bạn muốn hủy phiếu này')
      },
      approveProOrder(id) {
        util.confirmDelete(() => {
          const data = {
            production_order_receiver:'Thái Công Nhạ'
          }
          api.putData(`product-order/approveProductOrder/${id}`,data)
          .then(response => {
            util.showSuccessAlert('Đã duyệt');
            this.getproductionOders()
          })
          .catch(error => {
                console.log(error)
            });
          },'Bạn muốn duyệt phiếu này?')
      },
        reapproveProOrder(id) {
        util.confirmDelete(() => {
          api.putData(`product-order/reapproveProductOrder/${id}`)
          .then(response => {
            util.showSuccessAlert('Đã hoàn duyệt');
            this.getproductionOders()
          })
          .catch(error => {
                console.log(error)
            });
          },'Bạn muốn hoàn duyệt phiếu này?')
      },
    }
     
};
</script>

