<template>

						<!-- Row -->
						<div class="row row-sm">
							<div class="col-lg-12">
								<div class="card custom-card">
									<div class="card-body">
										<div>
											<h6 class="main-content-label mb-1">Chi tiết QA/QC Jobsheet: {{ jobsheetID }}</h6>
											<p class="text-muted card-sub-title">
											</p>
										</div>
										<div class="table-responsive">
											<table :data="QCDetails" class="table table-bordered display" id="QCTableDetail">
												<thead>
													<tr>
														<th class="wd-20p">STT</th>
														<th class="wd-25p">Số lot</th>
														<th class="wd-20p">Mã SP</th>
														<th class="wd-20p">Tên SP</th>
														<th class="wd-20p">Người kiểm tra</th>
														<th class="wd-20p">Ngày kiểm tra</th>
														<th class="wd-20p">Trạng thái</th>
														<th class="wd-20p">Kết quả</th>
														<th class="wd-20p">Ghi chú</th>
														<th class="wd-15p">Thao tác</th>
													</tr>
												</thead>
												<tbody>
													<tr v-for="(item, index) in QCDetails" :key="item._id">
														<td>{{ index +1 }}</td>
														<td>{{item.product_code || item.semi_product_lot  }}</td>
														<td>{{ item.getDetail[0].product_id }}</td>
														<td>{{ item.getDetail[0].product_name }}</td>
														<td>{{ item.semi_product_tester || item.product_tester }}</td>
														<td>{{ item.semi_product_test_date || item.product_test_date }}</td>
														<td>
															 <span class="badge bg-pill" :class="getStatus(item.semi_product_status|| item.product_status)">{{ semiProductStatus(item.semi_product_status|| item.product_status) }}</span>
														</td>
														<td>
															 <span v-if="item.semi_product_result||item.product_result" class="badge bg-pill" :class="getResult(item.semi_product_result||item.product_result)">{{ QCResultShow(item.semi_product_result||item.product_result) }}</span>
														</td>
														<td>{{ item.semi_product_note }}</td>
														<td>
															<a v-if="item.semi_product_status ==6|| item.product_status == 6" @click="editQCModal('update_quality_control',item)" class="btn btn-sm ripple btn-primary text-light">Kiểm tra</a>
														</td>
													</tr>
												</tbody>
											</table>
										</div>
									</div>
								</div>
							</div>
						</div>
						<!-- End Row -->
			
			 <!-- Large Modal -->
  <div class="modal" id="update_quality_control" ref="updateQC">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo" style="box-shadow: rgba(0, 0, 0, 0.5) 0px 4px 100px">
        <div class="modal-header">
          <h6 class="modal-title">Cập nhật kiểm tra</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="QCResult">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td class="align-top">
                    <div class="form-group">
                      <label class="fw-bold ">Kết quả kiểm tra</label>
					  <select class="form-control form-select text-body" v-model="updateQC.product_result"  :class="{'is-invalid': updateErrors.product_result}">
							<option value="1">PASS</option>
							<option value="0">FAIL</option>
					  </select>
					  <div class="invalid-feedback" v-if="updateErrors.product_result">{{updateErrors.product_result}} </div>
                    </div>
                  </td>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Ghi chú</label>
					  <textarea class="form-control text-body" v-model="updateQC.product_note"  rows="5" placeholder="Nhập ghi chú"></textarea>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="modal-footer">
            <div>
              <button class="btn ripple btn-primary" type="submit">Lưu</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('updateQC','updateQC','')" type="button"> Đóng </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->
</template>

<script>
import api from '@/components/uses/api.js'
import util from '@/components/uses/utils.js'
import { format }from "date-fns";
export default {
     props:[
        'jobsheetCode'
    ],
	data() {
    return {
      QCDetails: null,
	  updateQC:[],
	  updateErrors:[]
    }
  },
   watch: {
    jobsheetCode() {
        this.getQADetail()
    }
  },

  mounted(){
		this.getQADetail()
  },

  methods: {
	loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#QCTableDetail')
        });
      },

	getQADetail() {
        if(this.jobsheetCode != null) {
            api.getData(`quality-control/detailQualityControl/${this.jobsheetCode}`)
            .then(Response => {
                this.QCDetails = Response.data.data;
                this.loadtable();
            })
            .catch(Error => {
                console.log(Error)
            })
        }
	},

	editQCModal(modalId,item) {
         $(`#${modalId}`).modal('show');
		this.updateQC.product_lot =item.product_code ||  item.semi_product_lot 
		this.updateQC.semi_product_result = item.semi_product_result || item.product_result,
		this.updateQC.semi_product_note = item.semi_product_note || item.product_note
	},

	QCResult() {
		 const updateData ={
			jobsheetCode :this.jobsheetCode,
			productId : this.updateQC.product_lot,
			product_tester: JSON.parse(sessionStorage.getItem('userLogin')).fullname,
			product_test_date: format(new Date(), "dd/MM/yyyy"),
			product_result: this.updateQC.product_result,
			product_note: this.updateQC.product_note,
		 }
		const updateField = [
			{ name: 'product_result', label: 'Kết quả', nullable: false }, 
		]
		 this.updateErrors = util.validateData(updateData, updateField);
		 if (Object.keys(this.updateErrors).length === 0) { 
            api.putData(`quality-control/orderQualityControl`, updateData) 
			.then(Response => {
				if(Response.data.status == 200) {
					this.closeModal('updateQC')
					util.showSuccessAlert('Cập nhật thành công')
					this.getQADetail()
					this.updateQC={}
				} 
			})
			.catch(Error => {
				console.log(Error)
			})
		}
	  }, 

  closeModal(modalRef, datakey, errorskey) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
        this[datakey] = {};
        this[errorskey] = {};
    },

	getResult(result) {
         switch(result) {
          case '0': return 'bg-danger-light';
          case '1': return 'bg-success-light';
          default: return 'bg-danger-light';
        }
      },
    
      QCResultShow (result) {
         switch(parseInt(result)) {
          case 0: return "FAIL";
          case 1: return "PASS";
          default: return "Không rõ"
        }
      },
		 getStatus(status) {
         switch(status) {
          case '0': return 'bg-primary-light';
          case '1': return 'bg-success-light';
          case '2': return 'bg-success-light';
          case '3': return 'bg-success-light';
		  case '4': return 'bg-success-light';
		  case '5': return 'bg-success-light';
		  case '6': return 'bg-success-light';
		  case '7': return 'bg-success-light';
		  case '8': return 'bg-success-light';
		  case '9': return 'bg-success-light';
		  case '10': return 'bg-danger-light';
          default: return 'bg-danger-light';
        }
      },
    
      semiProductStatus (statusCode) {
         switch(parseInt(statusCode)) {
          case 0: return "Mới tạo";
          case 1: return "Đã yêu cầu xuất kho";
          case 2: return "Đã xuất kho";
          case 3: return "Đã yêu cầu sản xuất";
		  case 4: return "Đang sản xuất";
		  case 5: return "Hoàn thành";
		  case 6: return "Đã gửi yêu cầu kiểm tra";
		  case 7: return "Đã kiểm tra";
		  case 8: return "Đã gửi yêu cầu nhập kho";
		  case 9: return "Đã nhập kho";
		  case 10: return "Đã hủy";
          default: return "Không rõ"
        }
      },
  }
}
</script>

