<template>
    <div class="bg-img">
    <div class="content">
		<div class="content-header">
			<img class="logo-img" src="/../\public/frontend/img/brand/logo.png" alt="">
			<span class="slogan">Vui lòng nhập địa chỉ email để xác nhận tài khoản</span>
		</div>
        <form @submit.prevent="confirmInfo">
            <div class="field">
                <span class="fa fa-envelope"></span>
                <input type="text" :class="{'is-invalid':errors.to}"  placeholder="Nhập địa chỉ email" v-model="confirmData.email">
            </div>
                <div :class="{'is-invalid':errors.to}" class="errors" v-if="showErrorMessage">{{errors.to}}</div>
                <div class="alert alert-success text-center mt-3" v-if="showSuccessMessage">{{message.success}}</div>
                <div class="pass"></div>            
            <div class="field">
                <input type="submit" value="Xác minh tài khoản">
            </div>
            
            <div class="signup"><router-link to="/">Trở lại</router-link>
            </div>
			
        </form>
    </div>
</div>
</template>

<script>
import util from '@/components/uses/utils.js'
import api from '@/components/uses/api.js'
export default {
 data() {
    return {
        confirmData:[],
        errors:[],
        message:[],
        showSuccessMessage:false, 
        showErrorMessage:false
    }
 },

 methods: {
    confirmInfo() {
        const data = {
            to:this.confirmData.email,
            subject:'Xác minh tài khoản',
        }

        const dataField = [
              { name: 'to', label: 'Email', maxLength: 50, nullable: false, type:'email' }, 
        ]
        this.errors = util.validateData(data, dataField);
        if (Object.keys(this.errors).length === 0) {
            api.postData('account/sendMailUser', data) 
            .then(Response => {
                console.log(Response)
                if(Response.data.status == 200) {
                     this.showSuccessMessage = true;
                     this.confirmData.email =''
                    this.message.success='Gửi yêu cầu thành công! Vui lòng xác nhận email để tiếp tục'
                    setTimeout(() => {
                    this.showSuccessMessage = false;
                    }, 2000);
                }
                if(Response.data.status == 404) {
                      this.showErrorMessage = true;
                    this.errors.to='Địa chỉ email không tồn tại'
                      setTimeout(() => {
                    this.showErrorMessage = false;
                    }, 2000);
                }
            })
            .catch(Error => {
                console.log(Error)
            })
            
        }
    },
 }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css?family=Montserrat|Poppins&display=swap');
.bg-img {
    background: url('/public/frontend/img/brand/1.jpg');
    height: 100vh;
    background-size: cover;
    background-position: center;
}

.bg-img::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.3);
}

.content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    z-index: 999;
    width: 500px;
    text-align: center;
    padding: 60px 32px;
    /* background: rgba(255, 255, 255, 0.01); */
	background: rgba(0, 0, 0, 0.9);
    box-shadow: -1px 4px 28px 0px rgba(0, 0, 0, 0.75);
}
.content-header {
	display: flex; 
	flex-direction: column;
	margin-bottom: 40px;
	justify-content: center;
	align-items: center;
}
.slogan {
	color: rgba(255, 255, 255, 0.6);
	font-size: 15px;
}

.logo-img{
	width: 200px;
	margin-bottom: 10px;
}

.field {
    position: relative;
    height: 45px;
    width: 100%;
    display: flex;
    background: rgba(255, 255, 255, 0.94);
    border-radius: 4px;
}

.field span {
    color: #222;
    width: 40px;
    line-height: 45px;
    padding: 0 10px;
}

.field input {
    height: 100%;
    width: 100%;
    background: transparent;
    border: none;
    outline: none;
    color: #222;
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
}
.space {
    margin-top: 16px;
}

.pass {
    text-align: left;
	margin-top: 10px;
	margin-bottom: 20px;
}

.pass a {
    color: #fff;
    font-family: 'Poppins', sans-serif;
    text-decoration: none;
}

.pass:hover a {
	color: #428bca;
}

input[type="submit"] {
    background: linear-gradient(to right, #428bca 0%, #3071a9 100%);
    border: 1px solid linear-gradient(to right, #428bca 0%, #3071a9 100%);;
    color: #fff;
    font-size: 17px;
    letter-spacing: 1px;
    font-weight: 500;
    cursor: pointer;
    font-family: 'Poppins', sans-serif;
	text-transform: uppercase;
    border-radius: 4px;
}

input[type="submit"]:hover {
    background: linear-gradient(to left, #428bca 0%, #3071a9 100%);
}

.signup {
    color: #fff;
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
	margin-top:20px
}

.signup a {
	color: #428bca;
    text-decoration: none;
}

.signup a:hover {
    color: #3071a9;
}

.errors {
    display: flex;
    flex: flex-start;
    margin-top: 10px;
    font-size: 90%;
    color: #f16d75
}
</style>