<template>
  	<!-- Main Content-->
			<div class="main-content side-content pt-0">

				<div class="main-container container-fluid pt-5">
					<div class="inner-body">

						<!-- Page Header -->
						<div class="page-header">
							<div>
								<h2 class="main-content-title tx-24 mg-b-5">Hàn mạch</h2>
								<ol class="breadcrumb">
									<li class="breadcrumb-item"><a href="#">Trang chủ</a></li>
									<li class="breadcrumb-item active" aria-current="page">Chi tiết hàn mạch</li>
								</ol>
							</div>
							 <div class="d-flex">
								<div class="justify-content-center">
								<router-link to="/weldings" class="btn btn-white btn-rounded my-2"><i class="fe fe-corner-down-left"></i> Trở lại</router-link>
								</div>
							</div>
						</div>
						<!-- End Page Header -->

						<!-- Row -->
						<div class="row row-sm">
							<div class="col-lg-12">
								<div class="card custom-card">
									<div class="card-body">
										<div>
											<h6 class="main-content-label mb-1">Danh sách lắp ráp</h6>
											<p class="text-muted card-sub-title">
											</p>
										</div>
										<div class="table-responsive">
											 <table :data="semiproducts" class="table table-bordered display" id="weldingTable">
												<thead>
													<tr>
														<th>STT</th>
														<th>Số lot</th>
														<th>Mã BTP</th>
														<th>Tên BTP</th>
														<th>Số SIM</th>
														<th>NV thực hiện</th>
														<th>Ngày hoàn thành</th>
														<th>Trạng thái</th>
														<th>Ghi chú</th>
														<th>Thao tác</th>
													</tr>
												</thead>
												<tbody>
													<tr v-for="(item, index) in semiproducts" :key="item._id">
														<td>{{ index +1 }}</td>
														<td>{{ item.semi_product_lot}}</td>
														<td>{{ item.semi_product_code }}</td>
														<td>{{ item.semi_product_name }}</td>
														<td>{{ item.categoryInfo[0] ? item.categoryInfo[0].serial_sim : '' }}</td>
														<td>{{ item.semi_product_assembler }}</td>
														<td>{{ item.semi_product_assembly_date }}</td>
														<td>
															<span class="badge bg-pill" :class="getStatus(item.semi_product_status)">{{ semiProductStatus(item.semi_product_status) }}</span>
														</td>
														<td>{{ item.semi_product_note }}</td>
														<td>
															<a @click="editWelding(item)" v-if="item.semi_product_status == 4 || item.semi_product_status == 5" data-bs-target="#update_welding" data-bs-toggle="modal" class="btn btn-sm ripple btn-primary text-light">Cập nhật</a>
															<button  @click="confirmWelding(item.semi_product_lot)" class="btn btn-primary btn-sm" v-if="item.semi_product_status =='3'">Nhận</button>
														</td>
													</tr>
												</tbody>
											 </table>
										</div>
									</div>
								</div>
							</div>
						</div>
						<!-- End Row -->
					</div>
				</div>
			</div>
			<!-- End Main Content-->
			  <!-- Large Modal -->
			<div class="modal" id="update_welding" ref="SemiProductModal">
				<div class="modal-dialog modal-lg" role="document">
					<div class="modal-content modal-content-demo">
						<div class="modal-header">
							<h6 class="modal-title">Thông tin hàn mạch</h6>
							<button aria-label="Close" class="btn-close"  @click="closeModal('SemiProductModal', 'updateWelding','updateErrors' )" type="button"></button>
						</div>
						<div class="modal-body">
						<table class="table">
							<tbody>
								<tr>
									<td class="align-top">
										<div class="form-group">
										<p class="mg-b-10 fw-bold">Số lot BTP</p>
										<input type="text" class="form-control text-body" v-model="updateWelding.semi_product_lot" readonly />
										</div>
									</td>
									<td>
										<div class="form-group">
										<p class="mg-b-10 fw-bold">Tên BTP</p>
										<textarea class="form-control text-body" v-model="updateWelding.semi_product_name" rows="3" readonly></textarea>
										</div>
									</td>
								</tr>
								<tr>
									<td>
										<div class="form-group">
										<p class="mg-b-10 fw-bold">Số SIM/E-SIM</p>
										<select class="form-control form-select text-body" v-model="updateWelding.categories_sim_id" :class="{'is-invalid': updateErrors.categories_sim_id}">
											<option v-for="(sim, index) in Sims" :key="sim._id" :value="sim._id">{{ sim.serial_sim }}</option>
										</select>
										<div class="invalid-feedback" v-if="updateErrors.categories_sim_id">{{updateErrors.categories_sim_id}} </div>
										</div>
										  
									</td>
									<td>
										<div class="form-group">
										<p class="mg-b-10 fw-bold">Ngày kích hoạt</p>
										<input type="date" class="form-control text-body" v-model="updateWelding.activation_date" :class="{'is-invalid': updateErrors.activation_date}"/>
											<div class="invalid-feedback" v-if="updateErrors.activation_date">{{updateErrors.activation_date}} </div>
									</div>
										
									</td>
								</tr>
								<tr>
									<td class="align-top">
										<div class="form-group">
										<p class="mg-b-10 fw-bold">Gói cước sử dụng</p>
										<select class="form-control form-select text-body" v-model="updateWelding.sim_package_id" :class="{'is-invalid': updateErrors.sim_package_id}">
											<option v-for="(item, index) in Packages" :key="item._id" :value="item._id">{{ item.sim_package_description }}</option>
										</select>
										<div class="invalid-feedback" v-if="updateErrors.sim_package_id">{{updateErrors.sim_package_id}} </div>
										</div>
										
									</td>
									<td>
										
										<div class="form-group">
										<p class="mg-b-10 fw-bold">Mục đích</p>
										<textarea class="form-control text-body" v-model="updateWelding.purpose" rows="4" placeholder="Nhập mục đích" :class="{'is-invalid': updateErrors.purpose}"></textarea>
										<div class="invalid-feedback" v-if="updateErrors.purpose">{{updateErrors.purpose}} </div>
									</div>
										
									</td>
								</tr>
								<tr>
									<td class="align-top">
										<div class="form-group">
										<p class="mg-b-10 fw-bold">Trạng thái</p>
										<select class="form-control text-body" v-model="updateWelding.semi_product_status"  :class="{'is-invalid': updateErrors.semi_product_status}">
											<option value="5">Hoàn thành</option>
											<option value="10">Đã hủy</option>
										</select>
											<div class="invalid-feedback" v-if="updateErrors.semi_product_status">{{updateErrors.semi_product_status}} </div>
										</div>
										
									</td>
									<td>
										<div class="form-group">
										<p class="mg-b-10 fw-bold">Ngày hoàn thành</p>
										<input type="date" class="form-control text-body" v-model="updateWelding.semi_product_assembly_date"  :class="{'is-invalid': updateErrors.semi_product_assembly_date}" >
										<div class="invalid-feedback" v-if="updateErrors.semi_product_assembly_date">{{updateErrors.semi_product_assembly_date}} </div>	
									</div>
										
									</td>
								</tr>
								<tr>
									<td colspan="2">
										<div class="form-group">
										<p class="mg-b-10 fw-bold">Ghi chú</p>
										<textarea class="form-control text-body" v-model="updateWelding.semi_product_note" rows="4" placeholder="Nhập ghi chú"></textarea>
										</div>
									</td>
								</tr>
							</tbody>
							</table>
						</div>
						<div class="modal-footer">
							<button class="btn ripple btn-primary" type="button" @click="update">Lưu</button>
							<button class="btn ripple btn-danger" @click="closeModal('SemiProductModal', 'updateWelding','updateErrors' )" type="button">Đóng</button>
						</div>
					</div>
				</div>
			</div>
			<!--End Large Modal -->
</template>
<script>
import util from '@/components/uses/utils.js'
import api from '@/components/uses/api.js'
import { format} from "date-fns";
export default {
 data() {
    return {
      semiproducts: null,
	  jobsheetID:null,
	  Packages:[],
	  selectedSim:null,
	  Sims:[],
	  updateWelding:[],
	  updateErrors:[]
    }
  },
  props:[
    'jobsheetCode'
  ],
  mounted() {
	this.jobsheetID= this.$route.params.id,
	this.getSemiProducts()
	console.log(this.jobsheetCode)
  },
  methods: {
	loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#weldingTable')
        });
    },
	async getSemiProducts() {
		try {
			const Response = await api.getData(`welding/showDetailWelding/${this.jobsheetID}`);
			
			this.semiproducts = Response.data.data[0].getDetail
			console.log(Response)
			this.Packages = Response.data.getSimPackage
			this.Sims =  Response.data.getCategoriesSim
			this.loadtable()
		} catch (error) {
			console.log(Error)
		}
	},

		 getStatus(status) {
         switch(status) {
          case '0': return 'bg-primary-light';
          case '1': return 'bg-success-light';
          case '2': return 'bg-success-light';
          case '3': return 'bg-success-light';
		  case '4': return 'bg-success-light';
		  case '5': return 'bg-success-light';
		  case '6': return 'bg-success-light';
		  case '7': return 'bg-success-light';
		  case '8': return 'bg-success-light';
		  case '9': return 'bg-success-light';
		  case '10': return 'bg-danger-light';
          default: return 'bg-danger-light';
        }
      },
    
      semiProductStatus (statusCode) {
         switch(parseInt(statusCode)) {
          case 0: return "Mới tạo";
          case 1: return "Đã yêu cầu xuất kho";
          case 2: return "Đã xuất kho";
          case 3: return "Đã yêu cầu sản xuất";
		  case 4: return "Đang sản xuất";
		  case 5: return "Hoàn thành";
		  case 6: return "Đã gửi yêu cầu kiểm tra";
		  case 7: return "Đã kiểm tra";
		  case 8: return "Đã gửi yêu cầu nhập kho";
		  case 9: return "Đã nhập kho";
		  case 10: return "Đã hủy";
          default: return "Không rõ"
        }
      },

	  confirmWelding(id) {
		util.confirmDelete(() => {
        const User = JSON.parse(sessionStorage.getItem('userLogin'));
		const Data = {
			fullname:User.fullname,
			semiProductID: id
		}
		api.putData(`welding/approveOrderWelding/${id}`, Data)
		.then(Response => {
			console.log(Response.status)
			if(Response.status==200) {
				util.showSuccessAlert('Đã nhận yêu cầu hàn mạch')
				this.getSemiProducts()
			}
			else {
				util.showErrorAlert('Bị lỗi khi nhận yêu cầu hàn mạch')
			}
		})
		.catch(Error => {
			console.log(Error)
		})
          }, 'Bạn chắc chắn muốn nhận yêu cầu này?')
	  },

	  editWelding(item) {
		if(item.categoryInfo.length>0) {
			const oldSIM = {
				_id:item.categories_sim_id,
				serial_sim:item.categoryInfo[0].serial_sim 
			}
			this.Sims.push(oldSIM)
		}
		this.updateWelding.old_sim=item.categories_sim_id,
		this.updateWelding.id=item._id,
		this.updateWelding.semi_product_lot = item.semi_product_lot,
		this.updateWelding.semi_product_name = item.semi_product_name
		this.updateWelding.semi_product_assembly_date = item.semi_product_assembly_date,
		this.updateWelding.purpose = item.purpose
		this.updateWelding.categories_sim_id = item.categories_sim_id,
    	this.updateWelding.activation_date = item.activation_date ? format( new Date(item.activation_date), "yyyy-MM-dd") : 0,
		this.updateWelding.semi_product_note = item.semi_product_note, 
		this.updateWelding.semi_product_status = (item.semi_product_status == 5 || item.semi_product_status ==10) ? item.semi_product_status : '',
		this.updateWelding.sim_package_id = item.SimpackageInfo[0] ? item.SimpackageInfo[0]._id : ''
	},
	  update() {
		const user = JSON.parse(sessionStorage.getItem('userLogin'));
	
		const updateData = {
			old_sim:this.updateWelding.old_sim,
			semi_product_lot:this.updateWelding.semi_product_lot,
			categories_sim_id:this.updateWelding.categories_sim_id,
			activation_date: this.updateWelding.activation_date,
			sim_package_id: this.updateWelding.sim_package_id,
			semi_product_assembler:user.fullname,
			purpose : this.updateWelding.purpose,
			semi_product_note: this.updateWelding.semi_product_note,
			semi_product_status: this.updateWelding.semi_product_status,
			semi_product_assembly_date: this.updateWelding.semi_product_assembly_date,
		}
		const updateField = [
			{ name: 'categories_sim_id', label: 'Số SIM/E-SIM', nullable: false }, 
			{ name: 'activation_date', label: 'Ngày kích hoạt', nullable: false }, 
			{ name: 'sim_package_id', label: 'Gói cước', nullable: false }, 
			{ name: 'purpose', label: 'Mục đích', nullable: false }, 
			{ name: 'semi_product_status', label: 'Trạng thái', nullable: false }, 
			{ name: 'semi_product_assembly_date', label: 'Ngày hoàn thành', nullable: false }, 
		]
 		this.updateErrors = util.validateData(updateData, updateField);

		if (Object.keys(this.updateErrors).length === 0) {
			console.log(updateData)
			const filteredPackages = this.Packages.filter(pkg  => pkg._id === this.updateWelding.sim_package_id);
			const activationDate = this.updateWelding.activation_date; 
			const packageMonths = filteredPackages[0].sim_package_expiration;
			updateData.expiration_date = util.caculateExpirationDate(activationDate,packageMonths)
		
		api.putData(`welding/updateOrderWelding/${updateData.semi_product_lot}`, updateData)
		.then(Response => {
			console.log(Response)
			util.showSuccessAlert('Cập nhật thành công')
			this.closeModal('SemiProductModal')
			this.getSemiProducts()
		})
		.catch(Error => {
			console.log(Error)
		})
			}
	  },

	   closeModal(modalRef, datakey, errorskey) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
        this[datakey] = {};
        this[errorskey] = {};
      },
  }
  
}
</script>
