<template>
    <div class="bg-img">
    <div class="content">
		<div class="content-header">
			<img class="logo-img" src="/../\public/frontend/img/brand/logo.png" alt="">
			<span class="slogan">Authentication Anywhere Anytime</span>
		</div>
        <form @submit.prevent="login">
            <div class="field">
                <span class="fa fa-user"></span>
                <input type="text" v-model="addUsser.username"  placeholder="Tên đăng nhập">
            </div>
            <div :class="{'is-invalid':addErrors.username}" class="errors" v-if="addErrors.username">{{addErrors.username}}</div>
            <div class="field space">
                <span class="fa fa-lock"></span>
                <input type="password" v-model="addUsser.password" class="password"  placeholder="Mật khẩu">
            </div>
            <div :class="{'is-invalid':addErrors.password}" class="errors" v-if="addErrors.password">{{addErrors.password}}</div>
             <div :class="{'is-invalid':loginError}" class="errors" v-if="loginError">{{loginError}}</div>
            <div class="pass">
                <router-link to="/forgot-password">Quên mật khẩu?</router-link>
            </div>
            <div v-if="showRecaptcha" class="g-recaptcha" ref="captchaContainer"></div>
            <div class="field">
                <input type="submit" value="Đăng nhập">
            </div>
            <!-- <div class="signup">Quay <router-link to="/index">Trở lại</router-link> trang chủ
            </div> -->
        </form>
    </div>
</div>
</template>

<script>
import util from '@/components/uses/utils.js'
import api from '@/components/uses/api.js'
import VueCookies from 'vue-cookies';
import { format} from "date-fns";

export default {

  data() {
    return {
      addUsser:{},
	  userData: {
		username:'',
		password:''
	  },
      loginError:'',
      addErrors: [],
      loginAttempts: 0,
      showRecaptcha: false,
      userLoginInfor:[],
      recaptchaResponse:null,
      captchaRendered: false,
    };
  },
   mounted() {
    const script = document.createElement("script");
    script.src = "https://www.google.com/recaptcha/api.js";
    script.async = true;
    script.defer = true;
    document.head.appendChild(script);
  },

    methods: {
    login() {
        	const userData = {
                username: this.addUsser.username,
                password: this.addUsser.password,
            }; 
            const loginDataField = [
                { name: 'username', label: 'Tên đăng nhập', maxLength: 50, nullable: false }, 
                { name: 'password', label: 'Mật khẩu', maxLength: 50, nullable: false }, 
                ]
            this.addErrors = util.validateData(userData, loginDataField);
              if (Object.keys(this.addErrors).length === 0) {

                  api.postData('account/loginUser',userData ).then(Response => {
                        
                            if (this.showRecaptcha) {
                                this.recaptchaResponse = grecaptcha.getResponse();
                            }

                            if (this.showRecaptcha && !this.recaptchaResponse) {
                                this.loginError = 'Vui lòng xác nhận captcha';
                                return;
                            }

                        	this.loginStatus = Response.data.status
                            if(this.loginStatus === 200) {
                                const accessToken = Response.data.AccessToken
                                VueCookies .set('accessToken', accessToken)
                                this.$router.push({ name: 'index' });
                                this.userLoginInfor = Response.data.getInfo[0]
                                const storeUserLogin = {
                                    userID: this.userLoginInfor._id,
                                    avatar:this.userLoginInfor.avatar,
                                    birthday:format(new Date(this.userLoginInfor.birthday), 'dd/MM/yyyy'),
                                    fullname: this.userLoginInfor.fullname,
                                    email: this.userLoginInfor.email,
                                    gender: this.userLoginInfor.gender,
                                    role: this.userLoginInfor.role_id,
                                    department: this.userLoginInfor.getDepartment[0].department_name,
                                    region: this.userLoginInfor.getRegion[0].region_name,
                                    position: this.userLoginInfor.getPostion[0].position_name,
                                }
                                sessionStorage.setItem('userLogin', JSON.stringify(storeUserLogin));
                                this.showRecaptcha = false;
                                this.loginAttempts = 0;
                                util.showSuccessAlert('Đăng nhập thành công')
                            } else {
                                this.loginAttempts++;
                                this.loginError = 'Tên đăng nhập hoặc mật khẩu không chính xác';
                                if (this.loginAttempts >= 3) {
                                this.showRecaptcha = true;
                                this.$nextTick(() => {
                                    const container = this.$refs.captchaContainer;
                                    if (!this.captchaRendered) {
                                    grecaptcha.render(container, {
                                        sitekey: '6LcEyMIoAAAAAL8JXTpF3h5NRJBQAHyEiJUPL69D',
                                    });
                                    this.captchaRendered = true;
                                    }
                                        grecaptcha.reset();
                                    });
                                }
                            }
                    }) 
                    .catch(error => {
                        alert('Lỗi kết nối serve')
                    })
              }
    },
	}
}
</script>

<style>
@import url('https://fonts.googleapis.com/css?family=Montserrat|Poppins&display=swap');
.bg-img {
    background: url('/public/frontend/img/brand/1.jpg');
    height: 100vh;
    background-size: cover;
    background-position: center;
}

.bg-img::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.3);
}

.content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    z-index: 999;
    width: 500px;
    text-align: center;
    padding: 60px 32px;
    /* background: rgba(255, 255, 255, 0.01); */
	background: rgba(0, 0, 0, 0.9);
    box-shadow: -1px 4px 28px 0px rgba(0, 0, 0, 0.75);
}
.content-header {
	display: flex; 
	flex-direction: column;
	margin-bottom: 40px;
	justify-content: center;
	align-items: center;
}
.slogan {
	color: rgba(255, 255, 255, 0.6);
	font-size: 15px;
}

.logo-img{
	width: 200px;
	margin-bottom: 10px;
}

.field {
    position: relative;
    height: 45px;
    width: 100%;
    display: flex;
    background: rgba(255, 255, 255, 0.94);
    border-radius: 4px;
}

.field span {
    color: #222;
    width: 40px;
    line-height: 45px;
    padding: 0 10px;
}

.field input {
    height: 100%;
    width: 100%;
    background: transparent;
    border: none;
    outline: none;
    color: #222;
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
}
.space {
    margin-top: 16px;
}

.pass {
    text-align: left;
	margin-top: 10px;
	margin-bottom: 20px;
}

.pass a {
    color: #fff;
    font-family: 'Poppins', sans-serif;
    text-decoration: none;
}

.pass:hover a {
	color: #428bca;
}

input[type="submit"] {
    background: linear-gradient(to right, #428bca 0%, #3071a9 100%);
    border: 1px solid linear-gradient(to right, #428bca 0%, #3071a9 100%);;
    color: #fff;
    font-size: 18px;
    letter-spacing: 1px;
    font-weight: 500;
    cursor: pointer;
    font-family: 'Poppins', sans-serif;
	text-transform: uppercase;
    border-radius: 4px;
}

input[type="submit"]:hover {
    background: linear-gradient(to left, #428bca 0%, #3071a9 100%);
}

.signup {
    color: #fff;
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
	margin-top:20px
}

.signup a {
	color: #428bca;
    text-decoration: none;
}

.signup a:hover {
    color: #3071a9;
}

.errors {
    display: flex;
    flex: flex-start;
    margin-top: 10px;
    font-size: 90%;
    color: #f16d75
}
</style>

