                <div class="table-responsive">
                  <table :data="semi_products" class="table table-bordered" id="SemiProTable">
                    <thead>
                      <tr>
                        <th>STT</th>
                        <th>Số lot board</th>
                        <th>Mã BTP</th>
                        <th>Tên BTP</th>
                        <th>ĐVT</th>
                        <th>Số sim</th>
                        <th>Trạng thái</th>
                        <th>Ghi chú</th>
                        <th>Thao tác</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in semi_products" :key="item._id">
                        <td>{{index + 1}}</td>
                        <td>{{item.semi_product_lot}}</td>
                        <td>{{item.semi_product_code}}</td>
                        <td>{{item.semi_product_name}}</td>
                        <td>{{item.semi_product_unit}}</td>
                        <td>
