                            </div>
                        </td>
                        <td>
														<span class="badge bg-pill" :class="getStatus(item.semi_product_status)">{{ semiProductStatus(item.semi_product_status) }}</span>
												</td>
                        <td></td>
                        <td>

                             <a @click="editSemiPro(item)" data-bs-target="#update_semi_product" data-bs-toggle="modal"
