                          <a  @click="deleteSemiPro(item._id)" class="btn btn-sm btn-outline-danger ms-1"> <i class="fe fe-trash"></i></a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- End Row -->
      </div>
    </div>
  </div>
  <!-- End Main Content-->

   <!-- Extra Large Modal -->
