              </tr>
              <tr>
                <td colspan="3">
                  <div class="form-group">
                    <label class="fw-bold">Ghi chú</label>
                    <textarea v-model="storeSemiPro.semi_product_note" class="form-control text-body" :class="{'is-invalid':addErrors.semi_product_note}" rows="3" placeholder="Nhập ghi chú"></textarea>
                    <div class="invalid-feedback mt-2" v-if="addErrors.semi_product_note">{{addErrors.semi_product_note}}</div>
                  </div>
                </td>
              </tr>
            </tbody>
                </table>
            </div>
            <div v-if="showSuccessMessage" class="alert alert-success text-center">
            {{ successMessage }}
          </div>
            <div class="modal-footer d-flex justify-content-between">
            <div class="form-check">
              <input class="form-check-input" type="checkbox" id="check1" name="option1" value="something" v-model="dontCloseModal">
