                      </div>
                    </td>
                    <td colspan="2">
                      <div class="form-group">
                        <p class="mg-b-10 fw-bold">Tên BTP</p>
                         <span>{{ detailInfo.semi_product_name }}</span>
                      </div>
                    </td>
                    <td>
