                    </td>
                  </tr>

                    <tr>
                    <td>
                      <div class="form-group">
                       <p class="mg-b-10 fw-bold">Số SIM</p>
                        <span>{{ detailInfo.category &&detailInfo.category[0].serial_sim || ''}}</span>
                      </div>
                    </td>
                     <td>
                      <div class="form-group">
                        <p class="mg-b-10 fw-bold">Phân loại</p>
                            <span>{{ detailInfo.category &&detailInfo.category[0].sim_type || ''}}</span>
                      </div>
                    </td>
                    <td>
                      <div class="form-group">
                        <p class="mg-b-10 fw-bold">Gói cước</p>
                         <span>{{ detailInfo.category &&detailInfo.category[0].SimpackageInfo[0].sim_package_description || ''}}</span>
                      </div>
                    </td>
