</template>

<script>
  import util from '@/components/uses/utils.js'
  import api from '@/components/uses/api.js'
import { format } from "date-fns";

export default {
 
  data() {
    return {
        semi_products: [],
        sims:[],
        packages:[],
        storeSemiPro:{
          semi_product_lot:'',
          semi_product_code:'',
          semi_product_name:'',
          semi_product_unit:'',
          categories_sim_id:'',
          activation_date:'',
          sim_package_id:'',
          purpose:'',
          semi_product_note:'',
        },
        selectedSemiPro: [],
        dontCloseModal: false,
        addErrors: [],
        updateErrors: [],
        showSuccessMessage: false, 
        successMessage: '',
        detailInfo:[],
        
    }
  },
   mounted() {
      this.getsemiPros();
    },

  methods: {
    loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#SemiProTable')
        });
      },
    getsemiPros() {
      api.getData('semi-product/listSemiProduct')
      .then(response=> {
        this.semi_products = response.data.data;
        this.sims = response.data.getCategoriesSim;
        this.packages = response.data.getSimPackage;
        this.loadtable();
        console.log(this.semi_products)
      })
      .catch(error=> {
        console.log(error)
          if(Error.response.status == 401) {
                   util.LoginSession();
                    }
      })
    },

     getStatus(status) {
         switch(status) {
          case '0': return 'bg-primary-light';
          case '1': return 'bg-success-light';
          case '2': return 'bg-success-light';
          case '3': return 'bg-success-light';
          case '4': return 'bg-success-light';
          case '5': return 'bg-success-light';
          case '9': return 'bg-success-light';
          case '10': return 'bg-danger-light';
          default: return 'bg-danger-light';
        }
      },
    
      semiProductStatus (statusCode) {
         switch(parseInt(statusCode)) {
          case 0: return "Mới tạo";
          case 1: return "Đã yêu cầu xuất kho";
          case 2: return "Đã xuất kho";
          case 3: return "Đã yêu cầu sản xuất";
          case 4: return "Đang sản xuất";
          case 5: return "Hoàn thành";
          case 9: return "Đã nhập kho";
          case 10: return "Đã hủy";
          default: return "Không rõ"
        }
      },

     addSemiPro() {
        const semiProData = {
          semi_product_lot: this.storeSemiPro.semi_product_lot,
          semi_product_code: this.storeSemiPro.semi_product_code,
          semi_product_name: this.storeSemiPro.semi_product_name,
          semi_product_unit: this.storeSemiPro.semi_product_unit,
          categories_sim_id: this.storeSemiPro.categories_sim_id, 
          activation_date: this.storeSemiPro.activation_date,
          sim_package_id: this.storeSemiPro.sim_package_id,
          purpose: this.storeSemiPro.purpose,
          semi_product_status:'9',
          semi_product_note: this.storeSemiPro.semi_product_note ? this.storeSemiPro.semi_product_note : ''
        };
        const semiProField = [
          { name: 'semi_product_lot', label: 'Số lot board', maxLength: 50, nullable: false }, 
          { name: 'semi_product_code', label: 'Mã BTP', maxLength: 50, nullable: false }, 
          { name: 'semi_product_name', label: 'Tên BTP', maxLength: 50, nullable: false },
          { name: 'semi_product_unit', label: 'Tên BTP', maxLength: 50, nullable: false },
          { name: 'categories_sim_id', label: 'Số sim',  nullable: false }, 
          { name: 'activation_date', label: 'Ngày kích hoạt', nullable: false }, 
          { name: 'sim_package_id', label: 'Gói cước', nullable: false }, 
          { name: 'purpose', label: 'Mục đích', maxLength: 50, nullable: false },
          { name: 'semi_product_note', label: 'ghi chú', maxLength: 50, nullable: true }, 
        ]
        this.addErrors = util.validateData(semiProData, semiProField);

        if (Object.keys(this.addErrors).length === 0) {
          const filteredPackages = this.packages.filter(pkg  => pkg ._id === this.storeSemiPro.sim_package_id)||'';
          const activationDate = this.storeSemiPro.activation_date || ''; 
          const packageMonths = filteredPackages[0].sim_package_expiration|| '';
          semiProData.expiration_date = util.caculateExpirationDate(activationDate,packageMonths)

          api.postData('semi-product/storeSemiProduct',semiProData)
          .then(response => {
             if (response.data.error) {
              response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.errors[errorField] = [];
                this.errors[errorField] = errorMessage;
              });
              } else {
                if (!this.dontCloseModal) {
                  this.getsemiPros();
                  this.closeModal('addSemiProModal', 'storeSemiPro', 'errors');
                  util.showSuccessAlert('Thêm thành công');
                  console.log(response)
                } else {
                  this.getsemiPros();
                  this.storeSemiPro={};
                  this.showSuccessMessage = true;
                  this.successMessage = 'Thêm thành công'
                  setTimeout(() => {
                  this.showSuccessMessage = false; 
                }, 1000); 
                }
              }
          })
            .catch(error => {
            console.error('Lỗi khi thêm:', error);
        });
        }
      },

      deleteSemiPro(id) {
        util.confirmDelete(() => {
          api.deleteData(`semi-product/deleteSemiProduct/?id=${id}`)
          .then(response => {
            util.showSuccessAlert('Xóa thành công');
            this.loadtable()
            this.getsemiPros();
          })
          .catch(error => {
            console.error('Lỗi khi xóa khu vực:', error);
            });
          })
      },

      closeModal(modalRef, datakey, errorskey) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
        this[datakey] = {};
        this[errorskey] = {};
      },
      editSemiPro(item) {
        const oldSIM = {
          _id:item.categories_sim_id,
          serial_sim:item.category[0].serial_sim
        }
        this.sims.push(oldSIM)
        this.selectedSemiPro.semi_product_id = item._id
        this.selectedSemiPro.semi_product_lot =  item.semi_product_lot,
        this.selectedSemiPro.semi_product_code =  item.semi_product_code,
        this.selectedSemiPro.semi_product_name =  item.semi_product_name,
        this.selectedSemiPro.semi_product_unit =  item.semi_product_unit,
        this.selectedSemiPro.categories_sim_id =  item.categories_sim_id,
        this.selectedSemiPro.activation_date = format( new Date(item.activation_date), "yyyy-MM-dd"),
        this.selectedSemiPro.sim_package_id =  item.sim_package_id,
        this.selectedSemiPro.purpose =  item.purpose,
        this.selectedSemiPro.semi_product_note = item.semi_product_note 
      },

       updateSemiPro() {
        const dataUpdate = {
          old_semi_product_id:this.selectedSemiPro.semi_product_id,
          old_sim_id:this.storeSemiPro.categories_sim_id,
          semi_product_lot: this.selectedSemiPro.semi_product_lot,
          semi_product_code: this.selectedSemiPro.semi_product_code,
          semi_product_name: this.selectedSemiPro.semi_product_name,
          semi_product_unit: this.selectedSemiPro.semi_product_unit,
          categories_sim_id: this.selectedSemiPro.categories_sim_id,
          activation_date: this.selectedSemiPro.activation_date,
          sim_package_id: this.selectedSemiPro.sim_package_id,
          purpose: this.selectedSemiPro.purpose,
          semi_product_note: this.selectedSemiPro.semi_product_note,
        }
        const updatesemiProField = [
          { name: 'semi_product_lot', label: 'Số lot board', maxLength: 50, nullable: false }, 
          { name: 'semi_product_code', label: 'Mã BTP', maxLength: 50, nullable: false }, 
          { name: 'semi_product_name', label: 'Tên BTP', maxLength: 50, nullable: false },
          { name: 'semi_product_unit', label: 'Tên BTP', maxLength: 50, nullable: false },
          { name: 'categories_sim_id', label: 'Số sim',  nullable: false }, 
          { name: 'activation_date', label: 'Ngày kích hoạt', nullable: false }, 
          { name: 'sim_package_id', label: 'Gói cước', nullable: false }, 
          { name: 'purpose', label: 'Mục đích', maxLength: 50, nullable: false },
          { name: 'semi_product_note', label: 'ghi chú', maxLength: 50, nullable: true }, 
        ]
        this.updateErrors = util.validateData(dataUpdate, updatesemiProField); 
        if (Object.keys(this.updateErrors).length === 0) {
        const filteredPackages = this.packages.filter(pkg  => pkg ._id === this.selectedSemiPro.sim_package_id);
        const activationDate = this.selectedSemiPro.activation_date; 
        const packageMonths = filteredPackages[0].sim_package_expiration;
        dataUpdate.expiration_date = util.caculateExpirationDate(activationDate,packageMonths)

            api.putData(`semi-product/updateSemiProduct/${this.selectedSemiPro.semi_product_id}` , dataUpdate)
            .then(response => {
              if (response.data.error) {
              response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.updateErrors[errorField] = [];
                this.updateErrors[errorField] = errorMessage;
              });
              } else {
                util.showSuccessAlert('Cập nhật thành công');
                this.closeModal('updateSemiProModal', 'selectedSemiPro', 'updateErrors');
                this.getsemiPros();
              }
            })
            .catch(error => {
              console.error('Lỗi khi cập nhật nhóm sản phẩm:', error);
            });
        }
      },

      viewDetail(item) {
        this.detailInfo = item 
        console.log(this.detailInfo.category[0].serial_sim)
      }
  },
}
</script>

