<template>
    <div class="d-flex justify-content-between">
        <div class="p-2">
            <h6 class="main-content-label mb-1">Thống kê</h6>
            <p class="text-muted card-sub-title">
                Số lượng thiết bị lắp đặt theo vị trí
            </p>
        </div>
    </div>
    <div class="chartjs-wrapper-demo">
        <canvas ref="barChart"></canvas>
    </div>
</template>

<script>
import Chart from 'chart.js/auto';
import zoomPlugin from 'chartjs-plugin-zoom';
import datalabelsPlugin from 'chartjs-plugin-datalabels';
import api from '@/components/uses/api.js'

Chart.register(zoomPlugin);
Chart.register(datalabelsPlugin)

export default {
  data() {
    return {
      chart: null,
      labels:[], 
      data:{
        installed:[],
        next_phase:[]
      }
    }
  },
  mounted() {
    this.ChartByArea();
  },
  methods: {
    createChart(canvas, type, data) {
      const ctx = canvas.getContext('2d');
      return new Chart(ctx, {
        type: type,
        data: data,
        options: {
          plugins: {
            zoom: {
              zoom: {
                wheel: {
                  enabled:true,
                },
                pinch: {
                  enabled:true,
                },
                mode:'y',
                onZoom: ({ chart }) => this.handleZoom(chart),
              },
              pan: {
                  enabled: true,
                  mode: 'y',
              }, 
            },
            datalabels: {
              color: '#fff',
              display:true
            }
          },
        
        },
      });
    },
    handleZoom(chart) {
      const yAxis = chart.scales.y;
      const minDataY = yAxis._range.min;
      if (minDataY < 0 ) {
        chart.resetZoom();
      }
    },

      ChartByArea() {
        api.getData('dashboard-ims/LocationReportIMS')
        .then(Response => {
          const data = Response.data.data.getData
           const total = Response.data.data.getTotal
           
           const labels = data.map(item=> item.getProvinceData[0].province_name)
           const installed=data.map(item=>item.installed)
           const next_phase=data.map(item=>item.next_phase)
           
           labels.push('Tổng')
           installed.push(total.totalInstalled)
            next_phase.push(total.totalNextPhase)
            this.chart = this.createChart(this.$refs.barChart, 'bar', {
              labels:labels,
              datasets: [
                {
                  label: 'Đã lắp đặt',
                  data: installed,
                  backgroundColor: '#428bca',
                  borderWidth: 2,
                  stack: 'Stack 0',
                },
                {
                  label: 'Dự kiến lắp đặt',
                  data:  next_phase,
                  backgroundColor: '#f0ad4e',
                  borderWidth: 2,
                  stack: 'Stack 0',
                }
              ],
            }, {
              scales: {
                x: {
                  stacked: true,
                },
                y: {
                  stacked: true,
                  beginAtZero: true,
                    ticks: {
                      stepSize: 70, 
                    },
                },
              },
            });

           
        })
        .catch(Error => {
           console.log(Error)
        })
      },



  }
}
</script>
