<template>
    <div class="d-flex justify-content-between">
                                    <div class="p-2">
                                        <h6 class="main-content-label mb-1">Thống kê</h6>
                                        <p class="text-muted card-sub-title">
                                            Số lượng thiết bị lắp đặt theo năm
                                        </p>
                                    </div>
                                </div>
                                  <div class="chartjs-wrapper-demo">
                                    <canvas ref="barChart2"></canvas>
                                </div>
</template>

<script>
import Chart from 'chart.js/auto';
import zoomPlugin from 'chartjs-plugin-zoom';
import datalabelsPlugin from 'chartjs-plugin-datalabels';
import api from '@/components/uses/api.js'

Chart.register(zoomPlugin);
Chart.register(datalabelsPlugin)

export default {
  data() {
    return {
      chart: null,
    }
  },
  mounted() {
    this.ChartByYear();
  },
  methods: {
    createChart(canvas, type, data) {
      const ctx = canvas.getContext('2d');
      return new Chart(ctx, {
        type: type,
        data: data,
        options: {
          plugins: {
            zoom: {
              zoom: {
                wheel: {
                  enabled:true,
                },
                pinch: {
                  enabled:true,
                },
                mode:'y',
                onZoom: ({ chart }) => this.handleZoom(chart),
              },
              pan: {
                  enabled: true,
                  mode: 'y',
              }, 
            },
            datalabels: {
              color: '#fff',
              display:true
            }
          },
        
        },
      });
    },
    handleZoom(chart) {
      const yAxis = chart.scales.y;
      const minDataY = yAxis._range.min;
      if (minDataY < 0 ) {
        chart.resetZoom();
      }
    },


  
    ChartByYear() {

      api.getData('dashboard-ims/YearReportIMS')

     .then(Response => {
      console.log(Response)
      const getData = Response.data.data.getTotal.totalInstalledByYear
      const totalInstalled = Response.data.data.getTotal.SumInstalled
      const totalNextPhase = Response.data.data.getTotal.totalNextPhase
      const labels = getData.map(item => item._id)
      const data = getData.map(item => item.qty)
      labels.push('Tổng', 'Dự kiến')
      data.push(totalInstalled,totalNextPhase )
      
      this.chart = this.createChart(this.$refs.barChart2, 'bar', {
        labels: labels,
        datasets: [
          {
            label: 'Số lượng lắp đặt theo năm',
            data: data,
            backgroundColor: data.map((value, index, array) => {
              if (index === array.length - 1) {
                return 'green'; 
              } else if (index === array.length - 2) {
                return '#f0ad4e'; 
              } else {
                return '#428bca'; 
              }
            }),
          },
        ],
      });

     })
     .catch(Error => {
        console.log(Error)
     })


    },
 
    

  }
}
</script>
