<template>
  <!-- Main Content-->
			<div class="main-content side-content pt-0">

				<div class="main-container container-fluid pt-5">
					<div class="inner-body">

						<!-- Page Header -->
						<div class="page-header">
							<div>
								<h2 class="main-content-title tx-24 mg-b-5">Lắp ráp</h2>
								<ol class="breadcrumb">
									<li class="breadcrumb-item"><a href="#">Trang chủ</a></li>
									<li class="breadcrumb-item active" aria-current="page">Chi tiết lắp ráp</li>
								</ol>
							</div>
							 <div class="d-flex">
								<div class="justify-content-center">
								  <router-link to="/assemblys" class="btn btn-white btn-rounded my-2"><i class="fe fe-corner-down-left"></i> Trở lại</router-link>
								</div>
							</div>
						</div>
						<!-- End Page Header -->

						<!-- Row -->
						<div class="row row-sm">
							<div class="col-lg-12">
								<div class="card custom-card">
									<div class="card-body">
										<div>
											<h6 class="main-content-label mb-1">Danh sách lắp ráp</h6>
											<p class="text-muted card-sub-title"></p>
										</div>
										<div class="table-responsive">
											 <table :data="products" :columns="columns" class="table table-bordered display" id="productTable">
												<thead>
													<tr>
														<th>STT</th>
														<th>Số lot</th>
														<th>Số Seriral</th>
														<th>Mã SP</th>
														<th>Tên SP</th>
														<th>Số Lot Board</th>
														<th>NV thực hiện</th>
														<th>Ngày hoàn thành</th>
														<th>Trạng thái</th>
														<th>Ghi chú</th>
														<th>Thao tác</th>
													</tr>
												</thead>
												<tbody>
													<tr v-for="(item, index) in products" :key="item._id">
														<td>{{ index +1 }}</td>
														<td>{{ item.product_code }}</td>
														<td>{{ item.product_serial }}</td>
														<td>{{ item.product_id }}</td>
														<td>{{ item.product_name }}</td>
														<td> {{ item.semiProductData[0] ? item.semiProductData[0].semi_product_lot : '' }}</td>
														<td>{{ item.product_assembler }}</td>
														<td>{{item.product_assembly_date}}</td>
														<td>
															<span class="badge bg-pill" :class="getStatus(item.product_status)">{{ productStatus(item.product_status) }}</span>
														</td>
														<td></td>
														<td>
															<a @click="editProduct(item)" v-if="item.product_status == 4 || item.product_status == 5" data-bs-target="#update_assembly" data-bs-toggle="modal" class="btn btn-sm ripple btn-primary text-light">Cập nhật</a>
															<button @click="confirmAssembly(item.product_code)"  class="btn btn-primary btn-sm" v-if="item.product_status =='3'">Nhận</button>
														</td>
													</tr>
												</tbody>
											</table>
										</div>
									</div>
								</div>
							</div>
						</div>
						<!-- End Row -->
					</div>
				</div>
			</div>
			<!-- End Main Content-->
			
          <!-- Extra Large Modal -->
			<div class="modal fade" id="update_assembly" ref="productModal">
				<div class="modal-dialog modal-xl" role="document">
					<div class="modal-content modal-content-demo">
						<div class="modal-header">
							<h6 class="modal-title">Thông tin lắp ráp</h6>
							<button aria-label="Close" class="btn-close" @click="closeModal('productModal','updateProduct','updateErrors')" type="button"></button>
						</div>
						<div class="modal-body">
                        <table class="table">
                            <tbody>
                                <tr>
                                    <td>
                                        <div class="form-group">
											<p class="mg-b-10 fw-bold">Số lot TP</p>
											<input type="text" class="form-control text-body" v-model="updateProduct.product_lot" readonly>
										</div>
                                    </td>
                                    <td>
                                         <div class="form-group">
											<p class="mg-b-10 fw-bold">Mã TP</p>
											<input type="text" class="form-control text-body"  v-model="updateProduct.product_code" readonly>
										</div>
                                    </td>
                                    <td>
										<div class="form-group">
											<p class="mg-b-10 fw-bold">Tên TP</p>
											<input type="text" class="form-control text-body" v-model="updateProduct.product_name"  readonly>
										</div>      
                                    </td>
                                </tr>
                                 <tr>
                                    <td>
                                        <div class="form-group">
											<p class="mg-b-10 fw-bold">Số lot board</p>
												<select class="form-control form-select text-body" id="semiProductId" v-model="selectedSemi" :class="{'is-invalid': updateErrors.semi_product_lot}">
												<option v-for="product in semiProducts" :value="product.semi_product_lot">{{ product.semi_product_lot }}</option>
												</select>
												<div class="invalid-feedback" v-if="updateErrors.semi_product_lot">{{updateErrors.semi_product_lot}} </div>
										</div>
                                    </td>
                                    <td>
										<div class="form-group">
											<p class="mg-b-10 fw-bold">Mã BTP board</p>
											<input type="text" class="form-control text-body"  v-model='updateProduct.semi_product_code'  readonly>
												
										</div>
                                    </td>
                                    <td>
                                        <div class="form-group">
											<p class="mg-b-10 fw-bold">Tên board</p>
											<input type="text" class="form-control text-body" v-model="updateProduct.semi_product_name" readonly>
										</div>
                                    </td>
                                </tr>
                                 <tr>
                                    <td>
                                         <div class="form-group">
											<p class="mg-b-10 fw-bold">Số SIM/E-SIM</p>
											<input type="text" class="form-control text-body" v-model="updateProduct.serial_sim" readonly>
										</div>
                                    </td>
                                    <td>
                                         <div class="form-group">
											<p class="mg-b-10 fw-bold">Gói cước sử dụng</p>
											<input type="text" class="form-control text-body" v-model="updateProduct.package_sim" readonly>
										</div>
                                    </td>
                                    <td>
                                        <div class="form-group">
											<p class="mg-b-10 fw-bold">Ngày kích hoạt</p>
											<input type="text" class="form-control text-body" v-model="updateProduct.activation_date" readonly>
										</div>
                                    </td>
                                </tr>
                                <tr>
                                    <td class="align-top">
                                         <div class="form-group">
											<p class="mg-b-10 fw-bold">Trạng thái</p>
											<select class="form-control form-select text-body" v-model="updateProduct.product_status" :class="{'is-invalid': updateErrors.product_status}">
												<option value="5">Hoàn thành</option>
												<option value="10">Đã hủy</option>
											</select>
												<div class="invalid-feedback" v-if="updateErrors.product_status">{{updateErrors.product_status}} </div>
										</div>
                                    </td>
									<td class="align-top">
										 <div class="form-group">
											<p class="mg-b-10 fw-bold">Ngày hoàn thành</p>
											<input type="date" class="form-control text-body" v-model="updateProduct.product_assembly_date"  :class="{'is-invalid': updateErrors.product_assembly_date}">
												<div class="invalid-feedback" v-if="updateErrors.product_assembly_date">{{updateErrors.product_assembly_date}} </div>
										</div>
									</td>
                                    <td>
                                        <div class="form-group">
											<p class="mg-b-10 fw-bold">Ghi chú</p>
											<textarea class="form-control text-body" v-model="updateProduct.product_note" rows="4" placeholder="Ghi chú"  ></textarea>
												
										</div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
						<div class="modal-footer">
							<button class="btn ripple btn-primary" type="button" @click="updateAssemble">Lưu</button>
							<button class="btn ripple btn-danger" @click="closeModal('productModal','updateProduct','updateErrors')" type="button">Đóng</button>
						</div>
					</div>
				</div>
			</div>
			</div>
			<!--End Extra Large Modal -->

</template>

<script>
import util from '@/components/uses/utils.js'
import api from '@/components/uses/api.js'
import { format} from "date-fns";

export default {

  data() {
    return {
		products: null,
		jobsheetID:null,
		updateProduct:[],
		selectedSemi: null,
		selectedProductName: '',
		semiProducts:[],
		updateErrors:[],
    }
  },
  watch: {
    selectedSemi(newVal) {
		const selectedSemi = this.semiProducts.find(product => product.semi_product_lot === newVal);
		if(selectedSemi) {
		this.updateProduct.semi_product_code = selectedSemi ? selectedSemi.semi_product_code : '';
		this.updateProduct.semi_product_name = selectedSemi ? selectedSemi.semi_product_name : '';
		this.updateProduct.serial_sim = selectedSemi && selectedSemi.CategoriesInfo ? selectedSemi.CategoriesInfo[0].serial_sim : '';
		this.updateProduct.activation_date = selectedSemi  && selectedSemi.CategoriesInfo ? selectedSemi.CategoriesInfo[0].activation_date : '';
		this.updateProduct.package_sim = selectedSemi  && selectedSemi.SimpackageInfo ? selectedSemi.SimpackageInfo[0].sim_package_description : '';
		}
    },
  },
  mounted() {
	this.jobsheetID= this.$route.params.id,
	this.getProducts()
  },

  methods: {

	formatDate(date) {
      return format(new Date(date), 'dd/MM/yyyy');
    },

		loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#productTable')
        });
    },
	getProducts() {
		api.getData(`assemble/showDetailAssemble/${this.jobsheetID}`)
		.then(Response => {
			this.products = Response.data.data[0].getDetail
			this.semiProducts = Response.data.getProductLot
			console.log(Response)
			this.loadtable()
		})
		.catch(Error => {
			console.log(Error)
		})
	},

		 getStatus(status) {
         switch(status) {
          case '0': return 'bg-primary-light';
          case '1': return 'bg-success-light';
          case '2': return 'bg-success-light';
          case '3': return 'bg-success-light';
		  case '4': return 'bg-success-light';
		  case '5': return 'bg-success-light';
		  case '6': return 'bg-success-light';
		  case '7': return 'bg-success-light';
		  case '8': return 'bg-success-light';
		  case '9': return 'bg-success-light';
		  case '10': return 'bg-danger-light';
          default: return 'bg-danger-light';
        }
      },
    
      productStatus (statusCode) {
         switch(parseInt(statusCode)) {
          case 0: return "Mới tạo";
          case 1: return "Đã yêu cầu xuất kho";
          case 2: return "Đã xuất kho";
          case 3: return "Đã yêu cầu sản xuất";
		  case 4: return "Đang sản xuất";
		  case 5: return "Hoàn thành";
		  case 6: return "Đã gửi yêu cầu kiểm tra";
		  case 7: return "Đã kiểm tra";
		  case 8: return "Đã gửi yêu cầu nhập kho";
		  case 9: return "Đã nhập kho";
		  case 10: return "Đã hủy";
          default: return "Không rõ"
        }
      },

	   confirmAssembly(id) {
		util.confirmDelete(() => {
        const User = JSON.parse(sessionStorage.getItem('userLogin'));
		const Data = {
			fullname:User.fullname,
			ProductID: id
		}
		api.putData(`assemble/approveOrderAssemble/${id}`, Data)
		.then(Response => {
			console.log(Response.status)
			if(Response.status==200) {
				util.showSuccessAlert('Đã nhận yêu cầu lắp ráp')
				this.getProducts()
			}
			else {
				util.showErrorAlert('Bị lỗi khi nhận yêu cầu lắp ráp')
			}
		})
		.catch(Error => {
			console.log(Error)
		})
          }, 'Bạn chắc chắn muốn nhận yêu cầu này?')
	  },

	  
	   

	editProduct(item) {
		if(item.semiProductData.length>0 )  {
			const oldSemiProduct = {
				semi_product_lot : item.semiProductData ? item.semiProductData[0].semi_product_lot :'',
				semi_product_code: item.semiProductData ? item.semiProductData[0].semi_product_code : '',
				semi_product_name: item.semiProductData ? item.semiProductData[0].semi_product_name : '',
			}
				 oldSemiProduct.CategoriesInfo = [{
					serial_sim: item.semiProductData && item.semiProductData[0].categoriesSimData ? item.semiProductData[0].categoriesSimData[0].serial_sim : '',
					activation_date: item.semiProductData ? item.semiProductData[0].categoriesSimData[0].activation_date: ''
				}];

				 oldSemiProduct.SimpackageInfo = [{
					sim_package_description: item.semiProductData ? item.semiProductData[0].SimpackageInfo[0].sim_package_description : '',
				}];
			this.semiProducts.push(oldSemiProduct)
		}
				if (this.selectedSemi !== item.semi_product_lot) {
					this.selectedSemi = item.semi_product_lot;
				}

		this.updateProduct.old_semi_product_lot=item.semi_product_lot,
		this.updateProduct.product_lot = item.product_code,
		this.updateProduct.product_code = item.product_id,
		this.updateProduct.product_name = item.product_name
		this.updateProduct.selectedSemi = item.semi_product_id
		this.updateProduct.product_assembly_date = item.product_assembly_date ? format(new Date(item.product_assembly_date), "yyyy-MM-dd") : ''
		this.updateProduct.product_note = item.product_note
		this.updateProduct.product_status = (item.product_status == 5 || item.product_status ==10) ? item.product_status : ''
	  },

	  updateAssemble () {
		const updateData = {
			old_semi_product_lot:this.updateProduct.old_semi_product_lot,
			semi_product_lot:this.selectedSemi, 
			product_assembly_date:this.updateProduct.product_assembly_date,
			product_status:this.updateProduct.product_status,
		}
		console.log(updateData)
		const updateField = [
			{ name: 'semi_product_lot', label: 'Số Lot Board', nullable: false }, 
			{ name: 'product_status', label: 'Trạng thái', nullable: false }, 
			{ name: 'product_assembly_date', label: 'Ngày hoàn thành', nullable: false }, 
		]
		this.updateErrors = util.validateData(updateData, updateField);
		if (Object.keys(this.updateErrors).length === 0) {
		api.putData(`assemble/updateOrderAssemble/${this.updateProduct.product_lot}`, updateData)
		.then(Response => {
			console.log(Response)
			util.showSuccessAlert('Cập nhật thành công')
			this.closeModal('productModal')
			this.getProducts()
		})
		.catch(Error => {
			console.log(Error)
		})
		}
	  },
	closeModal(modalRef, datakey, errorskey) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
        this[datakey] = {};
        this[errorskey] = {};
      },

  }

}


</script>

<style>

</style>