<template>
    <div class="bg-img">
    <div class="content">
		<div class="content-header">
			<img class="logo-img" src="/../\public/frontend/img/brand/logo.png" alt="">
			<span class="slogan">Reset your password</span>
		</div>
        <form @submit.prevent="resetPass">
            <div class="field">
                <span class="fa fa-lock"></span>
                <input type="password" class="password"  v-model="userData.password"  placeholder="Mật khẩu mới">
            </div>
             <div :class="{'is-invalid':addErrors.password}" class="errors" v-if="addErrors.password">{{addErrors.password}}</div>
            <div class="field space">
                <span class="fa fa-lock"></span>
                <input type="password"  class="password" v-model="userData.repeat_password" placeholder="Nhập lại mật khẩu">
            </div>
            <div :class="{'is-invalid':addErrors.repeat_password}" class="errors" v-if="addErrors.repeat_password">{{addErrors.repeat_password}}</div>
            <div class="pass">
            </div>
           
            <div class="field">
                <input type="submit" value="Xác nhận">
            </div>
        
        </form>
    </div>
</div>
</template>

<script>
import util from '@/components/uses/utils.js'
import api from '@/components/uses/api.js'
export default {
  data() {
    return {
	  userData:[],
      addErrors: [],
    };
  },
  methods:{
    resetPass() {
        const dataReset = {
            new_password:this.userData.password,
            repeat_password:this.userData.repeat_password
        }

        const resetDataField = [
        { name: 'new_password', label: 'Mật khẩu', maxLength: 50, nullable: false, type:'password' }, 
        { name: 'repeat_password', label: 'Mật khẩu nhập lại', maxLength: 50, nullable: false }, 
        ]
         this.addErrors = util.validateData(dataReset, resetDataField);
           if (Object.keys(this.addErrors).length === 0) { 
             if (this.userData.password != this.userData.repeat_password) {
                    this.addErrors.repeat_password ='Mật khẩu nhập lại không trùng khớp'
                    return;
                }
            const token = this.$route.params.id;
            api.postData(`account/resetPasswordUser/${token}`,dataReset)
            .then(Response => {
                console.log(Response)
                if(Response.data.status ==401) {
                    util.showInfoAlert('Token không hợp lệ')
                } 
                if(Response.data.status ==200) {
                    util.showSuccessAlert('Mật khẩu đã được cập nhật')
                    this.$router.push({ name: 'signin' });
                } 
            })
            .catch(Error => {
                console.log(Error)
            })
           }
       
    }
  }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css?family=Montserrat|Poppins&display=swap');
.bg-img {
    background: url('/public/frontend/img/brand/1.jpg');
    height: 100vh;
    background-size: cover;
    background-position: center;
}

.bg-img::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.3);
}

.content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    z-index: 999;
    width: 500px;
    text-align: center;
    padding: 60px 32px;
    /* background: rgba(255, 255, 255, 0.01); */
	background: rgba(0, 0, 0, 0.9);
    box-shadow: -1px 4px 28px 0px rgba(0, 0, 0, 0.75);
}
.content-header {
	display: flex; 
	flex-direction: column;
	margin-bottom: 40px;
	justify-content: center;
	align-items: center;
}
.slogan {
	color: rgba(255, 255, 255, 0.6);
	font-size: 15px;
}

.logo-img{
	width: 200px;
	margin-bottom: 10px;
}

.field {
    position: relative;
    height: 45px;
    width: 100%;
    display: flex;
    background: rgba(255, 255, 255, 0.94);
    border-radius: 4px;
}

.field span {
    color: #222;
    width: 40px;
    line-height: 45px;
    padding: 0 10px;
}

.field input {
    height: 100%;
    width: 100%;
    background: transparent;
    border: none;
    outline: none;
    color: #222;
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
}
.space {
    margin-top: 16px;
}

.pass {
    text-align: left;
	margin-top: 10px;
	margin-bottom: 20px;
}

.pass a {
    color: #fff;
    font-family: 'Poppins', sans-serif;
    text-decoration: none;
}

.pass:hover a {
	color: #428bca;
}

input[type="submit"] {
    background: linear-gradient(to right, #428bca 0%, #3071a9 100%);
    border: 1px solid linear-gradient(to right, #428bca 0%, #3071a9 100%);;
    color: #fff;
    font-size: 18px;
    letter-spacing: 1px;
    font-weight: 500;
    cursor: pointer;
    font-family: 'Poppins', sans-serif;
	text-transform: uppercase;
    border-radius: 4px;
}

input[type="submit"]:hover {
    background: linear-gradient(to left, #428bca 0%, #3071a9 100%);
}

.signup {
    color: #fff;
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
	margin-top:20px
}

.signup a {
	color: #428bca;
    text-decoration: none;
}

.signup a:hover {
    color: #3071a9;
}

.errors {
    display: flex;
    flex: flex-start;
    margin-top: 10px;
    font-size: 90%;
    color: #f16d75
}
</style>

