<template>
	<!-- Main Content-->
	<div class="main-content side-content pt-0">

		<div class="main-container container-fluid pt-5">
			<div class="inner-body">

				<!-- Page Header -->
				<div class="page-header">
					<div>
						<h2 class="main-content-title tx-24 mg-b-5">Quản lý bộ phận</h2>
						<ol class="breadcrumb">
							<li class="breadcrumb-item"><a href="#">Trang chủ</a></li>
							<li class="breadcrumb-item active" aria-current="page">Bộ phận</li>
						</ol>
					</div>
					<div class="d-flex">
						<div class="justify-content-center">
							<a data-bs-target="#add_department" data-bs-toggle="modal"
								class="btn btn-primary btn-rounded my-2 text-white">
								<i class="fe fe-plus"></i> Thêm mới
							</a>
						</div>
					</div>
				</div>
				<!-- End Page Header -->
			   <!-- Row -->
        <div class="row row-sm">
          <div class="col-lg-12">
            <div class="card custom-card">
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">Danh sách bộ phận</h6>
                  <p class="text-muted card-sub-title"></p>
                </div>
                <div class="table-responsive">
                  <table :data="departments" class="table table-bordered" id="departmentTable">
                    <thead>
                      <tr>
                        <th class="wd-20p">STT</th>
                        <th class="wd-25p">Mã bộ phận</th>
                        <th class="wd-25p">Tên bộ phận</th>
                        <th class="wd-25p">Ghi chú</th>
                        <th class="wd-15p">Thao tác</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in departments" :key="item._id">
                        <td>{{ index + 1 }}</td>
                        <td>{{ item.department_code }}</td>
                        <td>{{ item.department_name }}</td>
                        <td>{{ item.department_note }}</td>
                        <td>
                          <a @click="editDepartment(item)" data-bs-target="#update_department" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-info ms-1"><i class="fe fe-edit-2"></i></a>
                          <a @click="deleteDepartment(item._id)" class="btn btn-sm btn-outline-danger ms-1"> <i class="fe fe-trash"></i></a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- End Row -->
			</div>
		</div>
	</div>
	<!-- End Main Content-->

	<!-- Large Modal -->
  <div class="modal" id="add_department" ref="addDepartmentModal">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Thêm mới bộ phận</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="addDepartment">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Mã bộ phận</label>
                      <input v-model="storeDepartment.department_code" class="form-control text-body" name="department_code" :class="{'is-invalid':addErrors.department_code}" type="text" placeholder="Nhập mã bộ phận" />
                      <div class="invalid-feedback mt-2" v-if="addErrors.department_code">{{addErrors.department_code}}</div>
                    </div>
                  </td>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Tên bộ phận</label>
                      <input v-model="storeDepartment.department_name" class="form-control text-body" name="department_name" :class="{'is-invalid':addErrors.department_name}" type="text" placeholder="Nhập tên bộ phận" />
                      <div class="invalid-feedback" v-if="addErrors.department_name">{{addErrors.department_name}}</div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td colspan="2">
                    <div class="form-group">
                      <label class="fw-bold ">Ghi chú</label>
                      <textarea v-model="storeDepartment.department_note" class="form-control text-body" :class="{'is-invalid':addErrors.department_note}" rows="5" placeholder="Nhập ghi chú"></textarea>
                      <div class="invalid-feedback" v-if="addErrors.department_note">{{addErrors.department_note}}</div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div v-if="showSuccessMessage" class="alert alert-success text-center">
            {{ successMessage }}
          </div>
          <div class="modal-footer d-flex justify-content-between">
            <div class="form-check">
              <input class="form-check-input" type="checkbox" id="check1" name="option1" value="something" v-model="dontCloseModal">
              <label class="form-check-label fw-bold">Không đóng màn hình</label>
            </div>
            <div>
              <button class="btn ripple btn-primary" type="submit">Lưu</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('addDepartmentModal','storeDepartment','addErrors')" type="button"> Đóng </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->

  <!-- Large Modal -->
  <div class="modal" id="update_department" ref="updateDepartmentModal">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Cập nhật bộ phận</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="updateDepartment">
          <div class="modal-body">
            <table class="table ">
              <tbody>
                <tr>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Mã bộ phận</label>
                      <input v-model="selectedDepartment.department_code" :class="{'is-invalid': updateErrors.department_code}" class="form-control text-body" type="text" placeholder="Nhập mã bộ phận" />
                      <div class="invalid-feedback" v-if="updateErrors.department_code">{{updateErrors.department_code}} </div>
                    </div>
                  </td>
                  <td>
                    <div class="form-group">
                      <label class="fw-bold ">Tên bộ phận</label>
                      <input v-model="selectedDepartment.department_name" class="form-control text-body" :class="{'is-invalid': updateErrors.department_name}" type="text" placeholder="Nhập tên bộ phận" />
                      <div class="invalid-feedback" v-if="updateErrors.department_name">{{updateErrors.department_name}} </div>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td colspan="2">
                    <div class="form-group">
                      <label class="fw-bold ">Ghi chú</label>
                      <textarea v-model="selectedDepartment.department_note" :class="{'is-invalid':updateErrors.department_note}" class="form-control text-body" name="" rows="5" placeholder="Nhập ghi chú"></textarea>
                      <div class="invalid-feedback" v-if="updateErrors.department_note">{{updateErrors.department_note}} </div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="modal-footer">
            <button class="btn ripple btn-primary" type="submit">Lưu</button>
            <button class="btn ripple btn-danger" @click="closeModal('updateDepartmentModal', 'selectedDepartment','updateErrors' )" type="button"> Đóng </button>
          </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->
	
</template>

<script>
  import util from '@/components/uses/utils.js'
  import api from '@/components/uses/api.js'
  export default {
    data() {
      return {
        departments: {
          department_code:'',
          department_name:'',
          department_note:''
        },
		    storeDepartment: {
          department_code:'',
          department_name:'',
          department_note:''
        },
        selectedDepartment: [],
        dontCloseModal: false,
        addErrors: [],
        updateErrors: [],
        showSuccessMessage:false, 
      };
    },
    mounted() {
      this.getDepartments();
    },
    methods: {
      loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#departmentTable')
        });
      },
      async getDepartments() {
        try {
          const response = await api.getData('deparment/listDepartment');
          this.departments = response.data.data;
          this.loadtable();
        } catch (error) {
          if (error.response && error.response.status === 403) {
            util.LoginSession();
          } else {
            util.errorRedirect();
          }
        }
      },
      async addDepartment() {
      const departmentData = {
        department_code: this.storeDepartment.department_code,
        department_name: this.storeDepartment.department_name,
        department_note: this.storeDepartment.department_note || ''
      };

      const departmentField = [
        { name: 'department_code', label: 'Mã bộ phận', maxLength: 50, nullable: false },
        { name: 'department_name', label: 'Tên bộ phận', maxLength: 50, nullable: false },
        { name: 'department_note', label: 'Ghi chú', maxLength: 50, nullable: true }
      ];

      this.addErrors = util.validateData(departmentData, departmentField);

      if (Object.keys(this.addErrors).length > 0) {
        return; 
      }

      try {
        const response = await api.postData('deparment/storeDepartment', departmentData);

        if (response.data.error) {
          response.data.error.details.forEach((error) => {
            const errorMessage = error.message;
            const errorField = error.path[0];
            this.errors[errorField] = errorMessage;
          });
        } else {
          if (!this.dontCloseModal) {
            this.getDepartments();
            this.closeModal('addDepartmentModal', 'storeDepartment', 'errors');
            util.showSuccessAlert('Thêm thành công');
          } else {
            this.getDepartments();
            this.storeDepartment = {};
            this.showSuccessMessage = true;
            this.successMessage = 'Thêm thành công';
            setTimeout(() => {
              this.showSuccessMessage = false;
            }, 1000);
          }
        }
      } catch (error) {
        console.error('Lỗi khi thêm:', error);
      }
    },
	  closeModal(modalRef, datakey, errorskey) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
        this[datakey] = {};
        this[errorskey] = {};
      },

	  editDepartment(item) {
      this.selectedDepartment = {
        id: item._id,
        department_code: item.department_code,
        department_name: item.department_name,
        department_note: item.department_note,
      };
    },

      async updateDepartment() {
        const dataUpdate = {
          department_code: this.selectedDepartment.department_code,
          department_name: this.selectedDepartment.department_name,
          department_note: this.selectedDepartment.department_note
        };

        const updateDepartmentField = [
          { name: 'department_code', label: 'Mã bộ phận', maxLength: 50, nullable: false }, 
          { name: 'department_name', label: 'Tên bộ phận', maxLength: 50, nullable: false }, 
          { name: 'department_note', label: 'Ghi chú', maxLength: 50, nullable: true }
        ];

        this.updateErrors = util.validateData(dataUpdate, updateDepartmentField); 

        if (Object.keys(this.updateErrors).length > 0) {
          return;
        }
        try {
          const response = await api.putData(`deparment/updateDepartment/${this.selectedDepartment.id}`, dataUpdate);

          if (response.data.error) {
            response.data.error.details.forEach((error) => {
              const errorMessage = error.message;
              const errorField = error.path[0];
              this.updateErrors[errorField] = errorMessage;
            });
          } else {
            util.showSuccessAlert('Cập nhật thành công');
            this.closeModal('updateDepartmentModal', 'selectedDepartment', 'updateErrors');
            await this.getDepartments();
          }
        } catch (error) {
          console.error('Lỗi khi cập nhật bộ phận:', error);
          console.log(error);
        }
    },
      
	    deleteDepartment(id) {
        util.confirmDelete(() => {
          api.deleteData(`deparment/deleteDepartment/${id}`)
          .then(response => {
            util.showSuccessAlert('Xóa thành công');
            this.getDepartments();
          })
          .catch(error => {
          console.error('Lỗi khi xóa khu vực:', error);
          });
          })
      },
    
    }
  }
</script>
