<template>
     <div class="d-flex justify-content-between">
                                    <div class="p-2">
                                        <h6 class="main-content-label mb-1">Thống kê</h6>
                                        <p class="text-muted card-sub-title">
                                            Tỷ lệ sản phẩm đạt, không đạt, đã hủy
                                        </p>
                                    </div>
                                    <div>
                                            <el-date-picker
                                                v-model="selectedFilterMonth"
                                                @change="handleMonthChange"
                                                type="month"
                                                placeholder="Chọn tháng"
                                            />
                                    </div>
                                </div>
                                <div class="chartjs-wrapper-demo" style="display: flex; align-items: center; justify-content: center;">
                                    <canvas ref="pieChart"></canvas>
                                </div>
</template>

<script>
import Chart from 'chart.js/auto'
import api from '@/components/uses/api.js'
import { format, parse  } from 'date-fns';
export default {
    data() {
        return {
            selectedFilterMonth:format( new Date(), "yyyy-MM"),
            labels:[],
            data:[],
            month:null,
            pieCreated: null,
            pieChartData:{
                product:null,
                semiProduct:null
            }
        }
    },
    mounted() {
        this.pieChart()
    },
    methods: {
        handleMonthChange(month) {
            this.selectedFilterMonth = month;
            this.pieChart()
        },
        createChart(canvas, type, data) {
            const ctx = canvas.getContext('2d');
               return new Chart(ctx, {
                    type: type,
                    data: data,
                      options: {
                        plugins: {
                            datalabels: {
                                color:'white'
                            }
                        }
                    },
                })
            ;
        },
  
        pieChart() {
             if(this.pieCreated) {
                this.pieCreated.destroy();
            }
             api.getData('dashboard/pie-chart-report')
             .then(Response => {
                    this.month = format(new Date(this.selectedFilterMonth), "yyyy-MM") 
                    this.pieChartData.product = Response.data.infoProduct
                    this.pieChartData.semiProduct = Response.data.infoSemiProduct

                    const filteredProduct = this.pieChartData.product.filter(item => {
                         const date=format(parse(item.created, "yyyy-MM-dd'T'HH:mm:ss.SSSX", new Date()),'yyyy-MM');
                         return date == this.month;
                    }) 
                    
                   const filteredSemiProduct = this.pieChartData.semiProduct.filter(item => {
                         const date=format(parse(item.created, "yyyy-MM-dd'T'HH:mm:ss.SSSX", new Date()),'yyyy-MM');
                         return date == this.month;
                    }) 

                    this.pieChartData.pass = 0;
                    this.pieChartData.fail = 0;
                    this.pieChartData.cancel = 0;

                    for (let i=0; i<filteredProduct.length; i++) {
                        if(filteredProduct[i].product_status==10) {
                            this.pieChartData.cancel++
                        }
                        if(filteredProduct[i].product_result==0) {
                            this.pieChartData.fail++
                        }
                        if(filteredProduct[i].product_result==1) {
                            this.pieChartData.pass++
                        }
                    }
                    for (let j=0; j<filteredSemiProduct.length; j++) {
                        if(filteredSemiProduct[j].semi_product_status==10) {
                            this.pieChartData.cancel++
                        }
                        if(filteredSemiProduct[j].semi_product_result==0) {
                            this.pieChartData.fail++
                        }
                        if(filteredSemiProduct[j].semi_product_result==1) {
                            this.pieChartData.pass++
                        }
                    }

                    if(this.pieChartData.cancel!=0 || this.pieChartData.fail!=0 || this.pieChartData.pass!=0) {
                    this.pieCreated = this.createChart(this.$refs.pieChart, 'pie', {
                    labels: ['Đã hủy', 'Đạt', 'Không đạt'],
                    datasets: [{
                        label: 'Số lượng',
                        data: [this.pieChartData.cancel, this.pieChartData.pass, this.pieChartData.fail],
                        backgroundColor: ['red', 'green', 'grey'],
                        hoverOffset: 4,
                        }],
                    });
                } else {
                    this.pieCreated = this.createChart(this.$refs.pieChart, 'pie', {
                        labels: ['Không có dữ liệu'],
                        datasets: [{
                            label: 'Số lượng',
                            data: [this.pieChartData.cancel, this.pieChartData.pass, this.pieChartData.fail],
                            backgroundColor: ['red', 'green', 'grey'],
                            hoverOffset: 4,
                            }],
                    });
                }
             })
             .catch(Error => {
                console.log(Error)
               
             })
        }
    },
};
</script>


<style scoped>
.demo-date-picker {
  display: flex;
  width: 100%;
  padding: 0;
  flex-wrap: wrap;
}

.demo-date-picker .block {
  padding: 30px 0;
  text-align: center;
  border-right: solid 1px var(--el-border-color);
  flex: 1;
}

.demo-date-picker .block:last-child {
  border-right: none;
}

.demo-date-picker .demonstration {
  display: block;
  color: var(--el-text-color-secondary);
  font-size: 14px;
  margin-bottom: 20px;
}


.demo-date-picker .container {
  flex: 1;
  border-right: solid 1px var(--el-border-color);
}
.demo-date-picker .container .block {
  border-right: none;
}
.demo-date-picker .container .block:last-child {
  border-top: solid 1px var(--el-border-color);
}
.demo-date-picker .container:last-child {
  border-right: none;
}
</style>