<template>
  <!-- Main Content-->
  <div class="main-content side-content pt-0">
    <div class="main-container container-fluid pt-5">
      <div class="inner-body">
        <!-- Page Header -->
        <div class="page-header">
          <div>
            <h2 class="main-content-title tx-24 mg-b-5">
              Quản lý SIM/E-SIM
            </h2>
            <ol class="breadcrumb">
              <li class="breadcrumb-item"><a href="#">Trang chủ</a></li>
              <li class="breadcrumb-item active" aria-current="page">
                SIM/E-SIM
              </li>
            </ol>
          </div>
          <div class="d-flex">
            <div class="justify-content-center">
              <a data-bs-target="#add_sim" data-bs-toggle="modal" class="btn btn-primary btn-rounded my-2 text-white">
                <i class="fe fe-plus"></i> Thêm mới
              </a>
            </div>
          </div>
        </div>
        <!-- End Page Header -->

        <!-- Row -->
        <div class="row row-sm">
          <div class="col-lg-12">
            <div class="card custom-card">
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">
                    Danh sách SIM/E-SIM
                  </h6>
                  <p class="text-muted card-sub-title"></p>
                </div>
                <div class="table-responsive">
                  <table :data="sims" class="table table-bordered" id="simTable">
                    <thead>
                      <tr>
                        <th>STT</th>
                        <th>Serial</th>
                        <th>Số lot board</th>
                        <th>Tên board</th>
                        <th>Gói cước</th>
                        <th>Ngày kích hoạt</th>
                        <th>Ngày hết hạn</th>
                        <th>Mục đích</th>
                        <th>Phân loại</th>
                        <th>Trạng thái</th>
                        <th>cảnh báo</th>
                        <th>Thao tác</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in sims" :key="item._id">
                        <td>{{ index + 1 }}</td>
                        <td>{{ item.serial_sim }}</td>
                        <td>
                              <span v-if="item.semi_products && item.semi_products[0] && item.semi_products[0].semi_product_lot">
                                {{ item.semi_products[0].semi_product_lot }}
                              </span>
                              <span v-else>
                               
                              </span>
                            </td>
                         <td>
                              <span v-if="item.semi_products && item.semi_products[0] && item.semi_products[0].semi_product_name">
                                {{ item.semi_products[0].semi_product_name }}
                              </span>
                              <span v-else>
                               
                              </span>
                            </td>
                           <td>
                              <span v-if="item.sim_package && item.sim_package[0] && item.sim_package[0].sim_package_expiration">
                                {{ item.sim_package[0].sim_package_expiration }} Tháng
                              </span>
                              <span v-else>
                               
                              </span>
                            </td>
                        <td>
                        
                            {{item.activation_date}} 
                        </td>
                        <td>
                            {{ item.expiration_date}}
                        </td>
                         <td>
                            {{ item.purpose }}
                        </td>
                           <td>

                         <span :class="item.sim_type === 'E-SIM' ? 'tag tag-success tag-pill' : 'tag tag-warning tag-pill'">
                            {{ item.sim_type == 'E-SIM' ? 'E-SIM' : item.sim_type == 'SIM' ? 'SIM' : '' }}
                          </span>
                        </td>
                         <td>
                          <span class="badge bg-pill" :class="getStatus(item.sim_status)">{{ item.sim_status}}</span>

                          
                           
                        </td>
                        <td>
                         
                            <span class="label text-danger d-flex align-items-center"><span v-if="item.sim_warning" class="dot-label bg-danger me-1">
                            </span>{{item.sim_warning}}</span>
                        </td>
                        <td>
                     
                          <a @click="editSim(item)" data-bs-target="#update_sim" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-info ms-1"><i class="fe fe-edit-2"></i></a>
                          <a @click="deleteSim(item._id)" class="btn btn-sm btn-outline-danger ms-1"> <i class="fe fe-trash"></i></a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- End Row -->
      </div>
    </div>
  </div>
  <!-- End Main Content-->
  <!-- Large Modal -->
  <div class="modal" id="add_sim" ref="addSimModal">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Thêm mới SIM/E-SIM</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="addSim">
        <div class="modal-body">
           <table class="table ">
            <tbody>
            <tr>
              <td>
                <div class="form-group">
                  <label class="fw-bold">Số SIM/E-SIM</label>
                  <input v-model="storeSim.serial_sim" :class="{'is-invalid':addErrors.serial_sim}" type="text" class="form-control text-body" placeholder="Nhập số SIM/E-SIM" />
                  <div class="invalid-feedback mt-2" v-if="addErrors.serial_sim">{{addErrors.serial_sim}}</div>
                </div>
              </td>
              <td>
                <div class="form-group">
                    <label class="fw-bold">Phân loại</label>
                    <select class="form-control form-select text-body" v-model="storeSim.sim_type" :class="{'is-invalid':addErrors.sim_type}">
                      <option value="" disabled selected>Chọn loại</option>
                      <option selected>SIM</option>
                      <option>E-SIM</option>
                    </select>
                    <div class="invalid-feedback mt-2" v-if="addErrors.sim_type">{{addErrors.sim_type}}</div>
                  </div>
              </td>
            </tr>
              <tr>
              <td colspan="2">
                  <div class="form-group">
                    <label class="fw-bold">Ghi chú</label>
                    <textarea class="form-control text-body" name="" v-model="storeSim.manage_sim_note" rows="5" placeholder="Nhập ghi chú"></textarea>
                  </div>
              </td>
              </tr>
        
            </tbody>
          </table>
        </div>
        <div v-if="showSuccessMessage" class="alert alert-success text-center">
            {{ successMessage }}
          </div>
       <div class="modal-footer d-flex justify-content-between">
            <div class="form-check">
              <input class="form-check-input" type="checkbox" id="check1" name="option1" value="something" v-model="dontCloseModal">
              <label class="form-check-label fw-bold">Không đóng màn hình</label>
            </div>
            <div>
              <button class="btn ripple btn-primary" type="submit">Lưu</button>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('addSimModal','storeSim','addErrors')" type="button"> Đóng </button>
            </div>
          </div>
          </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->

  <!-- Large Modal -->
  <div class="modal" id="update_sim" ref="updateSimModal">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content modal-content-demo">
        <div class="modal-header">
          <h6 class="modal-title">Cập nhật SIM/E-SIM</h6>
          <button aria-label="Close" class="btn-close" data-bs-dismiss="modal" type="button"></button>
        </div>
        <form @submit.prevent="updateSim">
        <div class="modal-body">
          <!-- Row -->
          <div class="row row-sm">
            <div class="col-lg-12 col-md-12">
              <div class="card custom-card">
                <div class="card-body">
                 <table class="table ">
            <tbody>
            <tr>
              <td>
                <div class="form-group">
                  <label class="fw-bold">Số SIM/E-SIM</label>
                  <input v-model="selectedSim.serial_sim" :class="{'is-invalid':updateErrors.serial_sim}" type="text" class="form-control text-body" placeholder="Nhập số SIM/E-SIM" />
                  <div class="invalid-feedback mt-2" v-if="updateErrors.serial_sim">{{updateErrors.serial_sim}}</div>
                </div>
              </td>
              <td>
                <div class="form-group">
                    <label class="fw-bold">Phân loại</label>
                    <select class="form-control form-select text-body"  v-model="selectedSim.sim_type" :class="{'is-invalid':updateErrors.sim_type}">
                      <option value="" disabled selected>Chọn loại</option> 
                      <option>SIM</option>
                      <option>E-SIM</option>
                    </select>
                    <div class="invalid-feedback mt-2" v-if="updateErrors.sim_type">{{updateErrors.sim_type}}</div>
                  </div>
              </td>
            </tr>
              <tr>
              <td colspan="2">
                  <div class="form-group">
                    <label class="fw-bold">Ghi chú</label>
                    <textarea class="form-control text-body" name="" v-model="selectedSim.manage_sim_note" rows="5" placeholder="Nhập ghi chú"></textarea>
                  </div>
              </td>
              </tr>
        
            </tbody>
          </table>
                </div>
              </div>
            </div>
          </div>
          <!-- End Row -->
        </div>
        <div class="modal-footer">
          <button class="btn ripple btn-primary" type="submit">Lưu</button>
          <button class="btn ripple btn-danger" @click="closeModal('updateSimModal', 'selectedSim','updateErrors' )" type="button">
            Đóng
          </button>
        </div>
        </form>
      </div>
    </div>
  </div>
  <!--End Large Modal -->
</template>

<script>
import { format} from "date-fns";
import util from '@/components/uses/utils.js'
import api from '@/components/uses/api.js'
export default {
  data() {
    return {
      sims: [],
       storeSim:{
          serial_sim:'',
          sim_type:'',
          manage_sim_note:''
        },
        selectedSim: [],
        dontCloseModal: false,
        addErrors: [],
        updateErrors: [],
        showSuccessMessage: false, 
        successMessage: '',
    }
  }, 
  mounted() {
      this.getSims();
    },
    methods: {
     

       loadtable() {
          this.$nextTick(() => {
            util.initializeDataTable('#simTable')
          });
        },

      getSims() {
        api.getData('categories-sim/listCategoriesSim')
        .then(response=> {
          this.sims = response.data.data;
          this.loadtable();
          console.log(response)
        })
        .catch(error=> {
          console.log(error)
        })
      },
       addSim() {
        const simData = {
          serial_sim: this.storeSim.serial_sim,
          sim_type: this.storeSim.sim_type,
          sim_status:'Chưa kích hoạt',
          manage_sim_note: this.storeSim.manage_sim_note ? this.storeSim.manage_sim_note : ''
        };
        const simField = [
          { name: 'serial_sim', label: 'Số SIM/E-SIM', maxLength: 50, nullable: false }, 
          { name: 'sim_type', label: 'Phân loại', maxLength: 50, nullable: false }, 
          { name: 'manage_sim_note', label: 'Ghi chú', maxLength: 50, nullable: true }
        ]
        this.addErrors = util.validateData(simData, simField);
        if (Object.keys(this.addErrors).length === 0) {
          api.postData('categories-sim/storeCategoriesSim',simData)
          .then(response => {
             if (response.data.error) {
                response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.errors[errorField] = [];
                this.errors[errorField] = errorMessage;
              });
              } else {
                if (!this.dontCloseModal) {
                  this.getSims();
                  this.closeModal('addSimModal', 'storeSim', 'errors');
                  util.showSuccessAlert('Thêm thành công');
                } else {
                  this.getSims();
                  this.storeSim={};
                  this.showSuccessMessage = true;
                  this.successMessage = 'Thêm thành công'
                  setTimeout(() => {
                  this.showSuccessMessage = false; 
                }, 1000); 
                }
              }
          })
            .catch(error => {
            console.error('Lỗi khi thêm :', error);
        });
        }
      },

       closeModal(modalRef, datakey, errorskey) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
        this[datakey] = {};
        this[errorskey] = {};
      },

      deleteSim(id) {
        util.confirmDelete(() => {
          api.deleteData(`categories-sim/deleteCategoriesSim/?id=${id}`)
          .then(response => {
            util.showSuccessAlert('Xóa thành công');
            this.getSims();
          })
          .catch(error => {
              if(error.response.status===400) {
                util.showErrorAlert ('Sim/E-Sim đang sử dụng')
              } else {
                console.log(error)
              }
            });
          })
      },
       editSim(item) {
        this.selectedSim.id = item._id;
        this.selectedSim.serial_sim = item.serial_sim;
        this.selectedSim.sim_type = item.sim_type;
        this.selectedSim.manage_sim_note = item.manage_sim_note;
      },
      updateSim() {
        const dataUpdate = {
          serial_sim: this.selectedSim.serial_sim,
          sim_type: this.selectedSim.sim_type,
          manage_sim_note: this.selectedSim.manage_sim_note
        }
        const updateSimField = [
          { name: 'serial_sim', label: 'Số SIM/E-SIM', maxLength: 50, nullable: false}, 
          { name: 'sim_type', label: 'Phân Loại', maxLength: 50, nullable: false}, 
          { name: 'manage_sim_note', label: 'Ghi chú', maxLength: 50, nullable: true}
        ]
        this.updateErrors = util.validateData(dataUpdate, updateSimField); 
        if (Object.keys(this.updateErrors).length === 0) {
            api.putData(`categories-sim/updateCategoriesSim/${this.selectedSim.id}` , dataUpdate)
            .then(response => {
              if (response.data.error) {
              response.data.error.details.forEach((error) => {
                const errorMessage = error.message;
                const errorField = error.path[0];
                this.updateErrors[errorField] = [];
                this.updateErrors[errorField] = errorMessage;
              });
              } else {
                util.showSuccessAlert('Cập nhật thành công');
                this.closeModal('updateSimModal', 'selectedSim', 'updateErrors');
                this.getSims();
              }
            })
            .catch(error => {
              console.error('Lỗi khi cập nhật khu vực:', error);
            });
        }
      },

         getStatus(status) {
         switch(status) {
          case 'Chưa kích hoạt': return 'bg-danger-light';
          case 'Đang sử dụng': return 'bg-success-light';
          default: return 'bg-danger-light';
        }
      },
    }
}
</script>

<style>
@import "vue-select/dist/vue-select.css";
</style>
