<template>
  <!-- Main Content-->
  <div class="main-content side-content pt-0">
    <div class="main-container container-fluid pt-5">
      <div class="inner-body">
        <!-- Page Header -->
        <div class="page-header">
          <div>
            <h2 class="main-content-title tx-24 mg-b-5">QA/QC</h2>
            <ol class="breadcrumb">
              <li class="breadcrumb-item"><a href="#">Trang chủ</a></li>
              <li class="breadcrumb-item active" aria-current="page">QA/QC</li>
            </ol>
          </div>
        </div>
        <!-- End Page Header -->

        <!-- Row -->
        <div class="row row-sm">
          <div class="col-lg-12">
            <div class="card custom-card">
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">Danh sách kiểm tra</h6>
                  <p class="text-muted card-sub-title"></p>
                </div>
                <div class="table-responsive">
                  <table :data="qualitycontrols" class="table table-bordered" id="QCTable">
                    <thead>
                      <tr>
                        <th>STT</th>
                        <th class="wd-25p">Phiếu YCKT</th>
                        <th class="wd-20p">Ngày tạo</th>
                        <th class="wd-20p">Người tạo</th>
                        <th class="wd-20p">Mã sản phẩm</th>
                        <th class="wd-20p">Tên sản phẩm</th>
                        <th class="wd-20p">Ghi chú</th>
                        <th class="wd-15p">Thao tác</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in qualitycontrols" :key="item._id">
                        <td>{{ index +1 }}</td>
                        <td>{{ item.quality_control_code }}</td>
                        <td>{{ formatDate(item.quality_control_create_date) }}</td>
                        <td>{{ item.user_id }}</td>
                        <td>{{ item.getDetail[0].product_id }}</td>
                        <td>{{ item.getDetail[0].product_name }}</td>
                        <td></td>
                        <td>
                            <a @click="chooseJobsheet(item)" data-bs-target="#qualityControl" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-primary  ms-1"><i class="fe fe-eye"></i></a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- End Row -->
      </div>
    </div>
  </div>
  <!-- End Main Content-->
    <!--Extra Large Modal -->
  <div class="modal fade" id="qualityControl" ref="qualityControl">
  <div class="modal-dialog modal-fullscreen" role="document">
    <div class="modal-content modal-content-demo">
        <div class="modal-body">
            <ListQualityControl :jobsheetCode="jobsheetCode"/>
        </div>
		   <div class="modal-footer">
            <div>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('qualityControl')" type="button"> Đóng </button>
            </div>
          </div>
    </div>
  </div>
</div>
<!--End Extra Large Modal -->
</template>

<script>
import api from '@/components/uses/api.js'
import util from '@/components/uses/utils.js'
import {format} from 'date-fns'
import ListQualityControl from '@/views/production-management/quality_control/ViewDetail.vue'
export default {
  components: {
    ListQualityControl
  },
  data() {
    return {
      qualitycontrols: null,
      jobsheetCode:null
    }
  }, 

  mounted() {
    this.getQualityControls()
  },
  methods: {
    formatDate(date) {
      if (!date) {
        return ''; 
      }
      const parsedDate = new Date(date);

      if (isNaN(parsedDate.getTime())) {
        return date; 
      }

    return format(parsedDate, 'MM/dd/yyyy');
    },
      loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#QCTable')
        });
      },

      getQualityControls() {
        api.getData('quality-control/listQualityControl')
        .then(Response => {
          this.qualitycontrols = Response.data.data
          this.loadtable()
        })
        .catch(Error => {
          console.log(Error)
        })
      },

      chooseJobsheet(item){
        this.jobsheetCode=item.jobsheet_code
      },

       closeModal(modalRef) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
      },
  }
};
</script>

<style></style>
