<template>
  <div class="d-flex justify-content-between">
                                    <div class="p-2">
                                        <h6 class="main-content-label mb-1">Thống kê</h6>
                                        <p class="text-muted card-sub-title">
                                            Số lịch sản xuất trong ngày
                                        </p>
                                    </div>
                                         <div>
                                            <el-date-picker
                                                v-model="selectedFilterDate"
                                                type="daterange"
                                                @change="handleDateChange"
                                                unlink-panels
                                                range-separator="-"
                                                start-placeholder="Từ ngày"
                                                end-placeholder="Đến ngày"
                                                :shortcuts="shortcuts"
                                                :size="size"
                                                style="width:60%; right:0"
                                            />
                                    </div>
                                </div>
                                <div class="chartjs-wrapper-demo">
                                    <canvas ref="barChart"></canvas>
                                    
                                </div>
</template>


<script>
import Chart from 'chart.js/auto'
import api from '@/components/uses/api.js'
import { format, parse  } from 'date-fns';
export default {
    data() {
        return {
            selectedFilterDate: [new Date(new Date().getFullYear(), new Date().getMonth(), 1), new Date()],
            jobsheetData:[],
            startDate:null,
            endDate:null,
            barCreated: null,
            chart:null
        }
    },
    mounted() {
        this.barChar()
    },
    methods: {
        handleDateChange(date) {
            this.selectedFilterDate = date;
            this.barChar()
        },
       
        barChar() {
            const ctx = this.$refs.barChart.getContext('2d');
              if(this.chart) {
                this.chart.destroy();
            }
            api.getData('dashboard/bar-chart-report')
            .then(Response => {

                this.jobsheetData=Response.data.infoJobSheet
                this.startDate = format(this.selectedFilterDate[0],'yyyy-MM-dd') ;
                this.endDate =  format(this.selectedFilterDate[1],'yyyy-MM-dd');
                const filteredData = this.jobsheetData.filter(item => {
                    const date=format(parse(item.created, "yyyy-MM-dd'T'HH:mm:ss.SSSX", new Date()),'yyyy-MM-dd');
                    return date>=this.startDate && date<=this.endDate;
                })

                const groupData= this.groupDataByDate(filteredData)
                const colors = ['#428bca', '#008000', '#f0ad4e']; 
                const chartData = {
                    labels: Object.keys(groupData),
                    datasets: Object.values(groupData[Object.keys(groupData)[0]]).map((_, index) => ({
                    label: `Lịch số ${index + 1}`,
                    data: Object.values(groupData).map((group) => group[index]?.product_quantity ),
                    backgroundColor: colors[index % colors.length],
                    stack: 'Stack 0',
                    })),
                };
                    this.chart = new Chart(ctx, {
                    type: 'bar',
                    data: chartData,
                    options: {
                        scales: {
                            x: { stacked: true },
                            y: { stacked: true },
                        },
                        plugins: {
                            datalabels: {
                                color:'white'
                            }
                        }
                    },
                });
            })
            .catch(Error => {
                console.log(Error)
            })
        },

        groupDataByDate(data) {
            return data.reduce((result, item) => {
                if (!result[item.jobsheet_create_date]) {
                result[item.jobsheet_create_date] = [];
                }
                result[item.jobsheet_create_date].push(item);
                return result;
            }, {});
        },

       
    },
};
</script>


<style scoped>
.demo-date-picker {
  display: flex;
  width: 100%;
  padding: 0;
  flex-wrap: wrap;
}

.demo-date-picker .block {
  padding: 30px 0;
  text-align: center;
  border-right: solid 1px var(--el-border-color);
  flex: 1;
}

.demo-date-picker .block:last-child {
  border-right: none;
}

.demo-date-picker .demonstration {
  display: block;
  color: var(--el-text-color-secondary);
  font-size: 14px;
  margin-bottom: 20px;
}


.demo-date-picker .container {
  flex: 1;
  border-right: solid 1px var(--el-border-color);
}
.demo-date-picker .container .block {
  border-right: none;
}
.demo-date-picker .container .block:last-child {
  border-top: solid 1px var(--el-border-color);
}
.demo-date-picker .container:last-child {
  border-right: none;
}
</style>