const blankLayoutRoutes = [
     {
        path: "/",
        name: "signin",
        component: Signin,
    },
    {
        path: "/forgot-password",
        name: "ForgotPass",
        component: ForgotPass,
    },
    {
        path: "/reset-password/:id",
        name: "ResetPass",
        component: ResetPass,
    },
 
    {
        path: '/:pathMatch(.*)*', 
        beforeEnter: (to, from, next) => {
            to.meta.title='Lỗi đường dẫn'
            to.meta.content='Có thể bạn đã gõ sai đường dẫn hoặc trang không tồn tại'
            next();
        },
        component: NotFoundPage,
    },
      {
        path: '/error-no-connection', 
        name:'ConnectionError',
        beforeEnter: (to, from, next) => {
            to.meta.title='Lỗi kết nối'
            to.meta.content='Xảy ra lỗi trong quá trình kết nối đến máy chủ.'
            next();
        },
        component: NotFoundPage,
    }
];

const routes = [
   {
    path: '/index',
    component: DefaultLayout,
    children: defaultLayoutRoutes, 
    beforeEnter:authMiddleware,
  },
  {
    path: '/',
    component: BlankLayout,
    children: blankLayoutRoutes, 
  },
];

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes,
});

export default router;
