<template>
  <!-- Main Content-->
  <div class="main-content side-content pt-0">
    <div class="main-container container-fluid pt-5">
      <div class="inner-body">
        <!-- Page Header -->
        <div class="page-header">
          <div>
            <h2 class="main-content-title tx-24 mg-b-5">Hàn mạch</h2>
            <ol class="breadcrumb">
              <li class="breadcrumb-item"><a href="#">Trang chủ</a></li>
              <li class="breadcrumb-item active" aria-current="page">
                Hàn mạch
              </li>
            </ol>
          </div>
        </div>
        <!-- End Page Header -->

        <!-- Row -->
        <div class="row row-sm">
          <div class="col-lg-12">
            <div class="card custom-card">
              <div class="card-body">
                <div>
                  <h6 class="main-content-label mb-1">Danh sách hàn mạch</h6>
                  <p class="text-muted card-sub-title"></p>
                </div>
                <div class="table-responsive">
                  <table :data="weldings" class="table table-bordered" id="weldingTable">
                    <thead>
                      <tr>
                        <th>STT</th>
                        <th class="wd-25p">Mã lịch SX</th>
                        <th class="wd-20p">Mã sản phẩm</th>
                        <th class="wd-20p">Têm sản phẩm</th>
                        <th class="wd-20p">Ghi chú</th>
                        <th class="wd-15p">Thao tác</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in weldings" :key="item._id">
                        <td>{{ index +1 }}</td>
                        <td>{{ item.jobsheet_code }}</td>
                        <td>{{ item.getDetail[0].product_id }}</td>
                        <td>{{ item.getDetail[0].product_name }}</td>
                        <td></td>
                        <td>
                          <a @click="chooseJobsheet(item)" data-bs-target="#SemiProduct" data-bs-toggle="modal"
                             class="btn btn-sm btn-outline-primary  ms-1"><i class="fe fe-eye"></i></a>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- End Row -->
      </div>
    </div>
  </div>
  <!-- End Main Content-->

  <!--Extra Large Modal -->
  <div class="modal fade" id="SemiProduct" ref="SemiProduct">
  <div class="modal-dialog modal-fullscreen" role="document">
    <div class="modal-content modal-content-demo">
        <div class="modal-body">
            <ListSemiProduct :jobsheetCode="jobsheetCode"/>
        </div>
		   <div class="modal-footer">
            <div>
              <button class="btn ripple btn-danger ms-2" @click="closeModal('SemiProduct')" type="button"> Đóng </button>
            </div>
          </div>
    </div>
  </div>
</div>
<!--End Extra Large Modal -->
</template>

<script>
import util from '@/components/uses/utils.js'
import api from '@/components/uses/api.js'
import ListSemiProduct from '@/views/production-management/welding/ViewDetail.vue'
export default {
  components: {
    ListSemiProduct
  },
  data() {
    return {
      weldings: null,
      jobsheetCode:null
    }
  }, 
  mounted() {
    this.getWeldings()
  },
  methods: {
    chooseJobsheet(item) {
     this.jobsheetCode=item.jobsheet_code
    },
    loadtable() {
        this.$nextTick(() => {
          util.initializeDataTable('#weldingTable')
        });
    },
    getWeldings() {
      api.getData('welding/listWelding')
      .then(Response => {
        this.weldings =Response.data.data
        this.loadtable()
      })
      .catch(Error => {
        console.log(Error)
      })
    },

    closeModal(modalRef) {
        const modal = this.$refs[modalRef];
        $(modal).modal('hide');
      },
  }
};
</script>

<style></style>
